import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { ThemeProvider } from '@material-ui/core';
import StringFormat from 'string-format';
import theme from './theme/material';
import './languages';
import './theme/styles';
import { PathConstant } from 'const';
import { StoreProvider } from 'store';
import { ProtectedRoute } from 'components';

import {
  Home,
  Achievements,
  ServiceDetails,
  Order,
  Partner,
  Overview,
  FrequentlyQuestion,
  Feedback,
  Search,
  Cooperate,
  OpenLetter,
  Profile,
  History,
  OutStanding,
  Service,
  AdminCooperate,
  Note,
  News,
  AdminSearch,
  Nofifycation,
  NewsDetails,
  ManagePages,
  ManageNews,
  ManageQuestion,
  Login,
} from 'pages';

const App = () => {
  return (
    <StoreProvider>
      <ThemeProvider theme={theme}>
        <BrowserRouter>
          <Switch>
            <Route exact path={PathConstant.ROOT} component={Home} />
            <Route
              exact
              path={PathConstant.ACHIEVEMENTS}
              component={Achievements}
            />
            <Route exact path={PathConstant.PARTNER} component={Partner} />
            <Route exact path={PathConstant.OVERVIEW} component={Overview} />
            <Route
              exact
              path={PathConstant.OPENLETTER}
              component={OpenLetter}
            />
            <Route exact path={PathConstant.PROFILE} component={Profile} />
            <Route exact path={PathConstant.HISTORY} component={History} />
            <Route
              exact
              path={PathConstant.NEWS}
              // component={News}
              render={(props) => (
                <News {...props} key={props.location.search} />
              )}
            />
            <Route
              exact
              path={StringFormat(PathConstant.NEWSDETAILS, ':slug', ':id')}
              render={(props) => <NewsDetails {...props} />}
            />
            <Route exact path={PathConstant.NEWSCATEGORY} component={News} />
            <Route
              exact
              path={PathConstant.OUTSTANDING}
              component={OutStanding}
            />
            {/* Admin Route */}
            <ProtectedRoute exact path={PathConstant.ORDER} component={Order} />
            <Route exact path={PathConstant.ADMIN_LOGIN} component={Login} />
            <ProtectedRoute
              exact
              path={PathConstant.ADMIN_COOPERATE}
              component={AdminCooperate}
            />
            <ProtectedRoute exact path={PathConstant.NOTE} component={Note} />
            <ProtectedRoute
              exact
              path={PathConstant.MANAGEUSER}
              component={Nofifycation}
            />
            <ProtectedRoute
              exact
              path={PathConstant.MANAGEPAGE}
              component={ManagePages}
            />
            <ProtectedRoute
              exact
              path={PathConstant.ADMIN_SEARCH}
              component={AdminSearch}
            />

            <ProtectedRoute
              exact
              path={PathConstant.MANAGE_QUESTION}
              component={ManageQuestion}
            />
            <ProtectedRoute
              exact
              path={PathConstant.ADMIN_NEWS}
              component={ManageNews}
            />

            {/* Support Customer */}
            <Route
              exact
              path={PathConstant.FREQUENTLY_QUESTION}
              component={FrequentlyQuestion}
            />
            <Route exact path={PathConstant.FEEDBACK} component={Feedback} />
            <Route exact path={PathConstant.SEARCH} component={Search} />
            <Route exact path={PathConstant.COOPERATE} component={Cooperate} />
            <Route
              exact
              path={StringFormat(PathConstant.SERVICE_ID, ':id')}
              // component={ServiceDetails}
              render={(props) => (
                <ServiceDetails {...props} key={props.match.params.id} />
              )}
            />
            <Route exact path={PathConstant.SERVICE} component={Service} />
          </Switch>
        </BrowserRouter>
      </ThemeProvider>
    </StoreProvider>
  );
};
export default App;
