import React, { useState, useEffect } from 'react';
import { Typography } from '@material-ui/core';
import StringFormat from 'string-format';
import clsx from 'clsx';
import { HomeLayout } from 'layouts';
import { useStyles } from './styles';
import { PathConstant } from 'const';
import { ImageContainer } from 'components';
import { Link } from 'react-router-dom';
import { LangConstant } from 'const';
import { useTranslation } from 'react-i18next';
import style from './ServiceAnimation.module.css';

const Service = () => {
  const classes = useStyles();
  const { t: getLabel } = useTranslation();
  const [arrayServices, setArrayServices] = useState();
  const origin = window.location.origin;
  useEffect(() => {
    if (document.documentElement.clientWidth < 850) {
      setArrayServices(ARRAY_SERVICES_MOBILE);
    } else {
      setArrayServices(ARRAY_SERVICES);
    }
  }, []);
  return (
    <HomeLayout typeFooter="copyright">
      <ImageContainer
        className={classes.root}
        src={origin + '/intro/overview_back.jpg'}
      >
        <div className={classes.container}>
          {arrayServices?.map((item, index) => (
            <Link
              key={index}
              to={StringFormat(PathConstant.SERVICE_ID, index)}
              className={clsx(classes.boxService, style.boxService)}
            >
              <ImageContainer
                src={origin + '/' + item.src}
                className={classes.imageService}
                cover={true}
              ></ImageContainer>
              <div className={classes.boxContent}>
                <div className={clsx('root-center', classes.noteContainer)}>
                  <Typography className={classes.note}></Typography>
                </div>

                <div className={classes.content}>
                  <Typography
                    className={classes.textContent}
                    dangerouslySetInnerHTML={{ __html: getLabel(item.name) }}
                  ></Typography>
                </div>
              </div>
            </Link>
          ))}
        </div>
      </ImageContainer>
    </HomeLayout>
  );
};

export default Service;

