import React, { useState, useEffect } from 'react';
import { Typography, OutlinedInput, Button } from '@material-ui/core';
import { useTranslation } from 'react-i18next';
import clsx from 'clsx';
import { HomeLayout } from 'layouts';
import { Success, Failed, TextEditor, Spinner } from 'components';
import { LangConstant } from 'const';
import { useStyles } from './styles';
import { useGlobalContext } from 'store';

const Feedback = () => {
  const { t: getLabel, i18n } = useTranslation();
  const [contentQs, setContentQs] = useState();
  const [loading, setLoading] = useState(false);
  const { addComments, getDataConfig, PAGE_KEY } = useGlobalContext();
  const [feedbackForm, setFeedbackForm] = useState({
    fullname: '',
    email: '',
    phone: '',
    feedback: '',
  });
  useEffect(() => {
    const run = async () => {
      setLoading(true);
      const res = await getDataConfig(PAGE_KEY.comments, i18n.language);
      setContentQs(res.data[0].value);
      setLoading(false);
    };
    run();
  }, [i18n.language]);
  const classes = useStyles({ backGroundUrl: contentQs?.imgBackGround });
  const [isSuccess, setIsSuccess] = useState(false);
  const [isFailed, setIsFailed] = useState(false);
  const [actionBtn, setActionBtn] = useState(false);

  const onChange = (event) =>
    setFeedbackForm({
      ...feedbackForm,
      [event.target.name]: event.target.value,
    });
  const isNumeric = (value) => {
    return /^-?\d+$/.test(value);
  };
  const isEmail = (value) => {
    return /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(value);
  };
  const onSubmit = async () => {
    setActionBtn(true);
    if (!isNumeric(feedbackForm.phone) || !isEmail(feedbackForm.email)) {
      alert('Vui lng nhp ng s in thoi hoc email');
      setIsFailed(true);
    } else {
      const res = await addComments(feedbackForm);
      if (res.success) {
        setIsSuccess(true);
      } else {
        setIsFailed(true);
      }
    }
  };
  const onChangeTextEditor = (value) => {
    setFeedbackForm({ ...feedbackForm, feedback: value });
  };

  return loading ? (
    <Spinner />
  ) : (
    <HomeLayout typeFooter="copyright">
      {loading && <Spinner />}
      <div className={classes.root}>
        <div className={classes.container}>
          <div className={classes.contentContainer}>
            <Typography className={classes.title}>
              {contentQs?.title}
              {/* {getLabel(LangConstant.TXT_SUPPORT_TITLE)} */}
            </Typography>
            <div>
              <Typography className={classes.content}>
                {contentQs?.text}
                {/* {getLabel(LangConstant.TXT_SUPPORT_CONTENT_1)} */}
              </Typography>
              {/* <Typography className={classes.content}>
                {getLabel(LangConstant.TXT_SUPPORT_CONTENT_2)}
              </Typography> */}
            </div>
          </div>

          {!isSuccess && !isFailed && (
            <div className={clsx('center-root', classes.containerForm)}>
              <OutlinedInput
                name="fullname"
                onChange={onChange}
                placeholder={getLabel(LangConstant.TXT_FULLNAME)}
                className={classes.input}
                style={{ marginTop: '10px' }}
                autoComplete="off"
              />
              <OutlinedInput
                name="email"
                onChange={onChange}
                placeholder={`${getLabel(LangConstant.TXT_EMAIL)}*`}
                className={classes.input}
                autoComplete="off"
              />
              <OutlinedInput
                name="phone"
                onChange={onChange}
                placeholder={getLabel(LangConstant.TXT_PHONE)}
                className={classes.input}
                autoComplete="off"
              />
              <div className={classes.textEditor}>
                <TextEditor
                  setFeedbackForm={setFeedbackForm}
                  feedbackForm={feedbackForm}
                  onChange={onChangeTextEditor}
                />
              </div>
              <Button
                onClick={onSubmit}
                className={classes.button}
                variant="contained"
                color="primary"
                size="small"
                disabled={actionBtn}
              >
                {getLabel(LangConstant.TXT_BUTTON_FEEDBACK)}
              </Button>
            </div>
          )}
          {isSuccess && <Success />}
          {isFailed && <Failed />}
        </div>
      </div>
    </HomeLayout>
  );
};

export default Feedback;
