import { makeStyles } from '@material-ui/core';
import { HEADER_HEIGHT, FOOTER_HEIGHT } from 'layouts/home-layout/components';

export const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
  },
  boxQuestion: {
    height: 'calc(100% - 32px)',
  },
  paginationBox: {
    display: 'flex',
    height: 32,
    '&>span': {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      padding: '0 18px',
      backgroundColor: 'white',
      borderRadius: '8px',
      fontSize: '1rem',
      fontWeight: 'bold',
    },
  },
  contentQuestionContainer: {
    width: '100%',
    height: 'calc(80% - 40px)',
    display: 'flex',
    padding: '5% 5%',
  },
  contentQuestionImage: { width: '35%', height: '100%' },
  contentQuestionText: {
    width: '65%',
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
  },
  popup_question: {
    top: '50%',
    left: '50%',
    width: '60vw',
    border: '1px solid #eb0032',

    padding: '16px 32px',
    overflow: 'auto',
    position: 'fixed',
    transform: 'translateX(-50%)',
    borderRadius: '6px',
    backgroundColor: '#fff',
    '& > p': {
      fontSize: '1rem',
      lineHeight: '1.4rem',
      color: '#333',
    },
    '& > h2': {
      fontSize: '1.2rem',
      marginBottom: '16px',
    },
  },
  popup_question_note: {
    fontStyle: 'italic',
  },
  close_popup: {
    position: 'absolute',
    top: '16px',
    right: '16px',
    transform: 'rotate(90deg)',
    color: '#ec1140',
    cursor: 'pointer',
  },
  popup_question_content: { paddingLeft: '32px' },
  container: {
    width: '70%',
    maxWidth: '70%',
    minHeight: `calc(100vh - ${HEADER_HEIGHT + FOOTER_HEIGHT}px)`,
    padding: '32px 0px',
    margin: '0 auto',
  },
  header: {
    width: '100%',

    // margin: "10px",
    fontSize: 28,
    fontWeight: 'bolder',
    textAlign: 'center',
    color: '#e9182d',
  },
  headerContainer: {
    width: '100%',
    position: 'relative',
    height: '20%',
    display: 'flex',
    alignItems: 'flex-end',
    paddingBottom: '15px',
  },

  outlinedSearch: {
    width: '100%',
    height: 40,
    backgroundColor: theme.palette.common.white,
    border: '1px solid #e6e6e6',
    borderRadius: '5px',
    '& ::placeholder': {
      fontStyle: 'italic',
    },
  },
  input: {
    padding: '8px 24px',
  },
  buttonSearch: {
    minWidth: '120px',
    height: 40,
    marginLeft: '10px',
    padding: 3,
    backgroundColor: theme.palette.common.white,
    borderRadius: 6,
    border: '1px solid ' + theme.palette.grey[500],
  },
  linkQuestion: {
    width: '100%',
    fontSize: '1rem',
    height: 'calc(100% / 8)',
    '&:hover': {
      textDecoration: 'underline',
      cursor: 'pointer',
    },
  },
  '@media screen and (min-width:1200px) and (max-height:600px)': {
    linkQuestion: {
      fontSize: '0.8rem',
    },
  },
  '@media screen and (max-width: 1349px)': {
    popup_question: {
      top: 'calc(50px + (100vh - 50px - 30px)*0.2)',
      minHeight: 'calc((100vh - 50px - 30px)*0.62)',
      maxHeight: 'calc((100vh - 50px - 30px)*0.75)',
    },
  },
  '@media screen and (min-width: 1350px) and (max-width: 1589px)': {
    popup_question: {
      top: 'calc(60px + (100vh - 60px - 30px)*0.2)',
      minHeight: 'calc((100vh - 60px - 30px)*0.62)',
      maxHeight: 'calc((100vh - 60px - 30px)*0.75)',
    },
  },
  '@media screen and (min-width: 1590px) and (max-width: 1909px)': {
    popup_question: {
      top: 'calc(70px + (100vh - 70px - 30px)*0.2)',
      minHeight: 'calc((100vh - 70px - 30px)*0.62)',
      maxHeight: 'calc((100vh - 70px - 30px)*0.75)',
    },
  },
  '@media screen and (min-width: 1910px)': {
    popup_question: {
      top: 'calc(80px + (100vh - 80px - 30px)*0.2)',
      minHeight: 'calc((100vh - 80px - 30px)*0.62)',
      maxHeight: 'calc((100vh - 80px - 30px)*0.75)',
    },
  },
  '@media screen and (max-width: 850px)': {
    contentQuestionImage: { display: 'none' },
    contentQuestionText: {
      width: '100%',
    },
    linkQuestion: {
      marginBottom: '16px',
    },
    paginationBox: {
      height: 'auto',
    },
    popup_question: {
      width: '75vw',
      padding: '8px',
      minHeight: '30vh',
      maxHeight: '50vh',
      '&>h2': {
        paddingRight: '25px',
      },
    },
    header: {
      margin: '30px 0 10px',
      fontSize: 24,
    },
  },
}));
