import React, { useState, useEffect } from 'react';
import clsx from 'clsx';
import { useStyles } from './styles';
import { IntroLayout } from 'layouts';
import { HtmlRender, Spinner } from 'components';
import Stepper from '../../components/stepper/Stepper';
import { useTranslation } from 'react-i18next';
// import { getAchievementContent } from 'api/intro';
import { LangConstant } from 'const';
import { useGlobalContext } from 'store';

const Achievements = () => {
  const classes = useStyles();
  const [isChange, setChange] = useState(0);
  const { t: getLabel, i18n } = useTranslation();
  const [loading, setLoading] = useState(false);
  const { getDataConfig, PAGE_KEY } = useGlobalContext();
  const [data, setData] = useState();
  useEffect(() => {
    const getData = async () => {
      setLoading(true);
      const res = await getDataConfig(PAGE_KEY.achievements, i18n.language);
      // const dataRes = await getAchievementContent(i18n.language);
      setData(res.data[0].value);
      setLoading(false);
    };
    getData();
  }, [i18n.language]);
  const header = (
    <>
      {data && (
        <div className="full-height flex-content">
          <div className={classes.header}>
            <div className={classes.title}>
              <div className={classes.icon}></div>
              <div>{data.title}</div>
            </div>
            <HtmlRender pathname="achievements" html={data.text}></HtmlRender>
          </div>
          <div className={clsx(classes.stepper, 'flex-center')}>
            <StepperCustom
              isChange={isChange}
              setChange={setChange}
              maxChange={data.arrYears.length}
              steps={data.arrYears}
            />
          </div>
        </div>
      )}
    </>
  );

  const content = (
    <>
      {data && (
        <div className={classes.content}>
          <div className={classes.info}>
            <div className={classes.imagePrize}>
              <img
                src={data.arrYears[isChange].img}
                className={classes.image}
                alt="..."
              ></img>
            </div>

            <div className={classes.details}>
              <div>
                {data.arrYears[isChange].achievements.map((item, index) => (
                  <div key={index} className={classes.awards}>
                    <div className={classes.circle} />
                    {item}
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
  return (
    <>
      {data && (
        <>
          {loading ? (
            <Spinner />
          ) : (
            <IntroLayout
              background_url={data.imgBackGround}
              header={header}
              content={content}
            ></IntroLayout>
          )}
        </>
      )}
    </>
  );
};

export default Achievements;

const StepperCustom = (props) => {
  const { isChange, setChange, maxChange, steps } = props;

  const classes = useStyles();
  // const [activeStep, setActiveStep] = useState(0);

  const changeStep = (value) => {
    setChange(value);
  };

  const nextStep = (value) => {
    if (value + 1 < maxChange) {
      changeStep(value + 1);
    } else {
      changeStep(0);
    }
  };

  const prevStep = (value) => {
    if (value > 0) {
      changeStep(value - 1);
    } else {
      changeStep(maxChange - 1);
    }
  };

  return (
    <div className={classes.rootStepper}>
      <button
        className={classes.buttonPrevNext}
        onClick={() => prevStep(isChange)}
      >
        <img src="./prev-stepper.png" alt="arrow"></img>
      </button>
      <Stepper steps={steps} setChange={setChange} isChange={isChange} />
      <button
        className={classes.buttonPrevNext}
        onClick={() => {
          nextStep(isChange);
        }}
      >
        <img src="./next-stepper.png" alt="arrow"></img>
      </button>
    </div>
  );
};
