import { authReducer } from './reducer';
import axios from 'axios';
import { SET_AUTH } from './constains';
import { GlobalContext } from './context';
import React, { useEffect, useReducer, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { apiUrl } from './constains';

const StoreProvider = ({ children }) => {
  const [lang, setLang] = useState();
  const [authState, dispatch] = useReducer(authReducer, {
    isAuthenticated: false,
    authLoading: true,
  });
  const { isAuthenticated } = authState;
  const { i18n } = useTranslation();
  const tokenAccess = JSON.parse(localStorage.getItem('accessToken')) || 'a';
  const config = {
    headers: {
      'x-access-token': tokenAccess,
    },
  };

  const formDataconfig = {
    headers: {
      'x-access-token': tokenAccess,
      'Content-Type': 'multipart/form-data',
    },
  };

  // login user
  const login = async ({ email, password }) => {
    try {
      const res = await axios.post(`${apiUrl}/api/admin/login`, {
        username: email,
        password: password,
      });
      if (res.data.success) {
        localStorage.setItem(
          'accessToken',
          JSON.stringify(res.data.accessToken)
        );
        dispatch({ type: SET_AUTH, payload: { isAuthenticated: true } });
      }
      return res.data;
    } catch (error) {
      if (error.response.data) {
        return error.response.data;
      }
    }
  };

  // load user
  const checkUser = async () => {
    if (localStorage.getItem('accessToken')) {
      try {
        const res = await axios.get(`${apiUrl}/api/admin/comments`, config);
        if (res.data.success) {
          dispatch({
            type: SET_AUTH,
            payload: { isAuthenticated: true, authLoading: false },
          });
        }
      } catch (error) {
        dispatch({
          type: SET_AUTH,
          payload: { isAuthenticated: false, authLoading: false },
        });
      }
    } else {
      dispatch({
        type: SET_AUTH,
        payload: { isAuthenticated: false, authLoading: false },
      });
    }
  };

  //logout user
  const logout = () => {
    localStorage.removeItem('accessToken');
    dispatch({ type: SET_AUTH, payload: { isAuthenticated: false } });
  };

  //get Feedback
  const getFeedbacks = async (page = 1) => {
    try {
      const res = await axios.get(
        `${apiUrl}/api/admin/comments?page=${page}&limit=6`,
        config
      );
      return res.data;
    } catch (error) {
      return { success: false };
    }
  };

  //add Comment
  const addComments = async (feedbackForm) => {
    try {
      const res = await axios.post(`${apiUrl}/api/comments`, {
        name: feedbackForm.fullname,
        email: feedbackForm.email,
        phone: feedbackForm.phone,
        content: feedbackForm.feedback,
      });
      return res.data;
    } catch (error) {
      if (error.response.message) {
        return error.response.message;
      }
    }
  };

  // get all question
  const getQuestion = async (page = 1, lang = 'vi', limit = 6) => {
    try {
      const res = await axios.get(
        `${apiUrl}/api/questions?page=${page}&limit=${limit}&lang=${lang}`
      );

      return res.data;
    } catch (error) {
      return { success: false };
    }
  };
  // add a Question
  const addQuestion = async (form) => {
    try {
      const res = await axios({
        ...config,
        method: 'post',
        url: `${apiUrl}/api/admin/questions`,
        data: form,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };

  // delete a question
  const deleteQuestion = async (id, form) => {
    try {
      const res = await axios.delete(
        `${apiUrl}/api/admin/questions/${id}`,
        config
      );
      return res.data;
    } catch (error) {
      return { success: false };
    }
  };

  // update a question
  const editQuestionSv = async (id, form) => {
    try {
      const res = await axios({
        ...config,
        method: 'put',
        url: `${apiUrl}/api/admin/questions/${id}`,
        data: form,
      });
      return res.data;
    } catch (error) {
      return { success: false };
    }
  };

  // add orders
  const addOrder = async (form) => {
    const { fullname, phone, email, service, start, end, weight, note } = form;
    try {
      const res = await axios.post(`${apiUrl}/api/orders`, {
        name: fullname,
        email,
        phone,
        service,
        from: start,
        to: end,
        weight,
        other: note,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };

  //get orders
  const getOrders = async (page = 1) => {
    try {
      const res = await axios({
        ...config,
        method: 'get',
        url: `${apiUrl}/api/admin/orders?page=${page}&limit=6`,
      });

      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };

  // add Add Warehouse Cooperate
  const addWarehouseCooperate = async (form) => {
    try {
      const res = await axios.post(
        `${apiUrl}/api/cooperations/warehouse`,
        form
      );
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };

  // Add Other Cooperate
  const addOtherCooperate = async (form) => {
    try {
      const res = await axios.post(`${apiUrl}/api/cooperations/other`, form);
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };

  // add Add Transport Cooperate
  const addTransportCooperate = async (form) => {
    try {
      const res = await axios.post(
        `${apiUrl}/api/cooperations/transport`,
        form
      );
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };

  // Get Transport Other
  const getTransportCooperate = async (page = 1) => {
    try {
      const res = await axios({
        ...config,
        method: 'get',
        url: `${apiUrl}/api/admin/cooperations/transport?page=${page}&limit=4`,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  //Get Warehouse Other
  const getWarehouseCooperate = async (page = 1) => {
    try {
      const res = await axios({
        ...config,
        method: 'get',
        url: `${apiUrl}/api/admin/cooperations/warehouse?page=${page}&limit=4`,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };

  //Get Cooperation Other
  const getCooperateOther = async (page = 1) => {
    try {
      const res = await axios({
        ...config,
        method: 'get',
        url: `${apiUrl}/api/admin/cooperations/other?page=${page}&limit=4`,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };

  //get Search Unit
  const getSearchUnit = async (type, lang = 'vi') => {
    try {
      const res = await axios({
        ...config,
        method: 'get',
        url: `${apiUrl}/api/units?type=${type}&lang=${lang}`,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };

  //get Search Unit
  const uploadSearchUnit = async (data) => {
    try {
      const res = await axios({
        ...formDataconfig,
        method: 'post',
        url: `${apiUrl}/api/admin/units/import`,
        data: data,
      });
      if (res.data.success) {
        return true;
      }
    } catch (error) {
      return { success: false };
    }
  };

  //add news
  const addNews = async (form) => {
    try {
      const res = await axios({
        ...config,
        url: `${apiUrl}/api/admin/news`,
        method: 'post',
        data: form,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };

  //edit news
  const editNewsFn = async (form, id) => {
    try {
      const res = await axios({
        ...config,
        url: `${apiUrl}/api/admin/news/${id}`,
        method: 'put',
        data: form,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  //edit news
  const deleteNews = async (id) => {
    try {
      const res = await axios({
        ...config,
        url: `${apiUrl}/api/admin/news/${id}`,
        method: 'delete',
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  //get news
  const getNews = async (category, page = 1, limit = 2, lang = 'vi') => {
    try {
      const res = await axios({
        url: `${apiUrl}/api/news?lang=${lang}&category_id=${category}&limit=${limit}&page=${page}`,
        method: 'get',
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  //upload Img
  const uploadImg = async (form) => {
    try {
      const res = await axios({
        ...formDataconfig,
        url: `${apiUrl}/api/admin/upload`,
        method: 'post',
        data: form,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  //get type posts
  const getTypeNews = async (page, lang = 'vi') => {
    try {
      const res = await axios({
        url: `${apiUrl}/api/news?lang=${lang}&page=${page}&limit=2`,
        method: 'get',
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  // add units search
  const addUnitsSearch = async (form) => {
    try {
      const res = await axios({
        ...config,
        url: `${apiUrl}/api/admin/units`,
        method: 'post',
        data: form,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  // edit units search
  const editUnitsSearch = async (form, id) => {
    try {
      const res = await axios({
        ...config,
        url: `${apiUrl}/api/admin/units/${id}`,
        method: 'put',
        data: form,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  // delete units search
  const deleteUnitsSearch = async (id) => {
    try {
      const res = await axios({
        ...config,
        url: `${apiUrl}/api/admin/units/${id}`,
        method: 'delete',
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  //get news detail
  const getNewsDetail = async (id) => {
    try {
      const res = await axios({
        url: `${apiUrl}/api/news/${id}`,
        method: 'get',
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  // get all news
  const getAllPosts = async (lang = 'vi') => {
    try {
      const res = await axios({
        url: `${apiUrl}/api/news_all?lang=${lang}`,
        method: 'get',
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  //get category
  const getCategorys = async (language = 'vi') => {
    try {
      const res = await axios.get(`${apiUrl}/api/categories?lang=${language}`);
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  //edit category
  const editCategory = async (id, form) => {
    try {
      const res = await axios({
        ...config,
        url: `${apiUrl}/api/admin/categories/${id}`,
        method: 'put',
        data: form,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  //search question
  const searchQuestions = async (key, lang) => {
    try {
      const res = await axios({
        url: `${apiUrl}/api/questions/search?q=${key}&lang=${lang}`,
        method: 'get',
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  //get data config
  const getDataConfig = async (pageKey, lang) => {
    try {
      const res = await axios({
        url: `${apiUrl}/api/admin/config?pageKey=${pageKey}&lang=${lang}`,
        method: 'get',
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  //update data config
  const updataDataConfig = async (idConfig, data) => {
    try {
      const res = await axios({
        ...config,
        url: `${apiUrl}/api/admin/config/${idConfig}`,
        method: 'put',
        data,
      });

      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  //get images
  const getImages = async (page, type) => {
    try {
      const res = await axios({
        method: 'get',
        url: `${apiUrl}/api/admin/images?page=${page}&limit=6&type=${type}`,
        ...config,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  // delete image
  const deleteImage = async (id) => {
    try {
      const res = await axios({
        method: 'delete',
        url: `${apiUrl}/api/admin/images/${id}`,
        ...config,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  // upload pdf
  const uploadPdf = async (form) => {
    try {
      const res = await axios({
        method: 'post',
        url: `${apiUrl}/api/admin/upload_pdf`,
        ...formDataconfig,
        data: form,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  const exportOrders = async (form) => {
    try {
      const res = await axios({
        method: 'post',
        url: `${apiUrl}/api/admin/orders/export`,
        ...config,
        responseType: 'blob',
        data: form,
      });
      return res;
    } catch (error) {
      return { success: false };
    }
  };
  const exportFeed = async (form) => {
    try {
      const res = await axios({
        method: 'post',
        url: `${apiUrl}/api/admin/comments/export`,
        ...config,
        responseType: 'blob',
        data: form,
      });
      return res;
    } catch (error) {
      return { success: false };
    }
  };
  const exportTransport = async (form) => {
    try {
      const res = await axios({
        method: 'post',
        url: `${apiUrl}/api/admin/cooperations/transport/export`,
        ...config,
        responseType: 'blob',
        data: form,
      });
      return res;
    } catch (error) {
      return { success: false };
    }
  };
  const exportWarehouse = async (form) => {
    try {
      const res = await axios({
        method: 'post',
        url: `${apiUrl}/api/admin/cooperations/warehouse/export`,
        ...config,
        responseType: 'blob',
        data: form,
      });
      return res;
    } catch (error) {
      return { success: false };
    }
  };
  const exportOther = async (form) => {
    try {
      const res = await axios({
        method: 'post',
        url: `${apiUrl}/api/admin/cooperations/other/export`,
        ...config,
        responseType: 'blob',
        data: form,
      });
      return res;
    } catch (error) {
      return { success: false };
    }
  };
  const exportSearch = async (form) => {
    try {
      const res = await axios({
        method: 'post',
        url: `${apiUrl}/api/admin/units/export`,
        ...config,
        responseType: 'blob',
        data: form,
      });
      return res;
    } catch (error) {
      return { success: false };
    }
  };
  const exportInfoUser = async (form) => {
    try {
      const res = await axios({
        method: 'post',
        url: `${apiUrl}/api/admin/user_notification/export`,
        ...config,
        responseType: 'blob',
        data: form,
      });
      return res;
    } catch (error) {
      return { success: false };
    }
  };
  const sendInfo = async (form) => {
    try {
      const res = await axios({
        method: 'post',
        url: `${apiUrl}/api/user_notification`,
        data: form,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: false };
    }
  };
  const getUserInfomation = async (page = 1, limit = 6) => {
    try {
      const res = await axios({
        url: `${apiUrl}/api/admin/user_notification?page=${page}&limit=${limit}`,
        method: 'get',
        ...config,
      });
      if (res.data.success) {
        return res.data;
      }
    } catch (error) {
      return { success: 'false' };
    }
  };
  //check use
  useEffect(() => {
    checkUser();
  }, []);

  //set language
  useEffect(() => {
    const currentLang = JSON.parse(localStorage.getItem('lang')) || 0;
    if (currentLang == 1) {
      setLang(0);
      i18n.changeLanguage('en');
    } else {
      setLang(1);
      i18n.changeLanguage('vi');
    }
  }, []);

  //data context
  const globalContextData = {
    isAuthenticated,
    login,
    lang,
    exportSearch,
    getNews,
    editNewsFn,
    exportInfoUser,
    setLang,
    deleteNews,
    exportTransport,
    addComments,
    exportOther,
    addWarehouseCooperate,
    addOtherCooperate,
    addTransportCooperate,
    getFeedbacks,
    getUserInfomation,
    exportWarehouse,
    getTransportCooperate,
    exportOrders,
    exportFeed,
    addNews,
    getTypeNews,
    getCooperateOther,
    getWarehouseCooperate,
    getAllPosts,
    getQuestion,
    addQuestion,
    uploadPdf,
    addOrder,
    editQuestionSv,
    sendInfo,
    deleteQuestion,
    getOrders,
    getSearchUnit,
    uploadSearchUnit,
    uploadImg,
    addUnitsSearch,
    editUnitsSearch,
    deleteUnitsSearch,
    getNewsDetail,
    logout,
    getCategorys,
    editCategory,
    searchQuestions,
    updataDataConfig,
    getImages,
    deleteImage,
    getDataConfig,
    PAGE_KEY,
  };
  return (
    <GlobalContext.Provider value={globalContextData}>
      {children}
    </GlobalContext.Provider>
  );
};

export { StoreProvider };
const PAGE_KEY = {
  letter: 'letter',
  history: 'history',
  overview: 'overview',
  outstanding: 'outstanding',
  partner: 'partner',
  achievements: 'achievements',
  profile: 'profile',
  home: 'home',
  services1: 'services1',
  services2: 'services2',
  services3: 'services3',
  services4: 'services4',
  services5: 'services5',
  services6: 'services6',
  services7: 'services7',
  services8: 'services8',
  question: 'question',
  comments: 'comments',
  search: 'search',
  cooperate: 'cooperate',
  news: 'news',
  newsdetail: 'newsdetail',
};
