import React from 'react';
import './stepper.css';
const Stepper = ({ steps, isChange, setChange }) => {
  React.useEffect(() => {
    const listStepE = document.querySelectorAll('.step-item-cricle');
    const stepActive = document.querySelector('.step-item-cricle.active');
    if (stepActive) {
      stepActive.classList.remove('active');
    }
    listStepE[isChange].classList.add('active');
  }, [isChange]);
  return (
    <div className="section-step">
      {steps.map((y, i) => (
        <div key={i} className={`step-item ${i <= isChange ? 'active' : ''}`}>
          <div onClick={() => setChange(i)} className="step-item-cricle">
            <div className="step-item-year">
              NM <span>{y.year}</span>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default Stepper;
