import React, { useState, useRef, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { HomeLayout } from 'layouts';
import { ImageContainer } from 'components';
import { SliderHome } from '../../components/slider/Slider';
import { getHomeContent } from 'api/home';
import { Link } from 'react-router-dom';
import { CircleProgress } from 'components';
import { useGlobalContext } from 'store';

const Home = () => {
  const dataImg = useRef([]);
  const { t: getLabel, i18n } = useTranslation();
  const { getDataConfig, PAGE_KEY } = useGlobalContext();
  const [loading, setLoading] = useState(false);
  const [reload, setReload] = useState(null);
  const [dataCurrent, setDataCurrent] = useState();
  const [showBanner, setShowBanner] = useState(null);
  useEffect(() => {
    const windowHeight = () => {
      const doc = document.documentElement;
      doc.style.setProperty('--window-height', `${window.innerHeight}px`);
    };
    window.addEventListener('resize', windowHeight);
    windowHeight();
  }, []);
  useEffect(() => {
    const run = async () => {
      setLoading(true);
      const res = await getDataConfig(PAGE_KEY.home, i18n.language);
      setDataCurrent(res.data[0].value);
      setShowBanner(res.data[0].value.banner.is_show);
      setLoading(false);
    };
    run();
  }, [i18n.language]);

  return (
    <HomeLayout typeFooter="home">
      {loading && <CircleProgress />}
      {dataCurrent && !loading && <SliderHome data={dataCurrent.arrData} />}
      {showBanner && dataCurrent && (
        <Link
          to={dataCurrent.banner.link}
          className="banner fullwidth absolute"
        >
          <ImageContainer
            src={dataCurrent.banner.value}
            close={true}
          ></ImageContainer>
        </Link>
      )}
    </HomeLayout>
  );
};

export default Home;

const banner = './banner.jpg';
