import React from 'react';
import { makeStyles, Button } from '@material-ui/core';
import clsx from 'clsx';

const TabButton = ({ selectedIndex, arrayName, onChangeTab, cooperate }) => {
  const classes = useStyles({ cooperate });
  const handleClickButtonSeatch = (index, name) => {
    onChangeTab(index);
    if (name === 'ORDERS' || name === 'N HNG') {
      window.open('https://viettelpost.com.vn/');
    }
  };
  return (
    <div className={classes.root}>
      {arrayName.length > 0 &&
        arrayName.map((name, index) => (
          <Button
            key={index}
            onClick={() => handleClickButtonSeatch(index, name)}
            className={clsx(
              classes.button,
              index !== selectedIndex && classes.notChoose
            )}
            variant="contained"
            color="primary"
            size="small"
          >
            <div className={classes.icon}></div>
            <span> {name}</span>
          </Button>
        ))}
    </div>
  );
};

export default TabButton;

const useStyles = makeStyles((theme) => ({
  root: {
    width: '65%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: ({ cooperate }) =>
      cooperate ? 'flex-end' : 'space-between',
  },
  button: {
    fontSize: '0.9rem',
    fontWeight: '800',
    borderRadius: '8px',
    minWidth: '100px',
    marginLeft: ({ cooperate }) => (cooperate ? '32px' : 'unset'),
  },
  notChoose: {
    color: theme.palette.grey[700],
    backgroundColor: theme.palette.grey[300],
    '&:hover': {
      color: theme.palette.common.white,
    },
  },
  icon: {
    marginRight: 4,
  },
  '@media screen and (max-width: 850px)': {
    root: {
      display: 'block',
      width: '100%',
      textAlign: 'center',
    },
    button: {
      width: '80%',
      marginBottom: '16px',
      marginLeft: ({ cooperate }) => (cooperate ? '0' : 'unset'),
    },
  },
}));
