import React from 'react';
import PropTypes from 'prop-types';
import { HomeLayout } from 'layouts';
import { useStyles } from './styles';
import { Image } from 'components';

import clsx from 'clsx';
const BaseLayout = ({
  children,
  background_url,
  coverImageHistory,
  profile,
}) => {
  const classes = useStyles();

  return (
    <HomeLayout typeFooter="copyright">
      <div
        className={clsx(classes.contentContainer, {
          [classes.contentProfileContainer]: profile,
        })}
      >
        <Image
          src={background_url}
          cover={true}
          className="absolute"
          coverImageHistory
        />
        <div className={clsx(classes.content, 'root-body')}>{children}</div>
      </div>
    </HomeLayout>
  );
};

BaseLayout.propTypes = {
  children: PropTypes.node,
  background_url: PropTypes.string,
};

export default BaseLayout;
