import React, { useState, useEffect, useRef } from 'react';
import { Button } from '@material-ui/core';
import { useTranslation } from 'react-i18next';
import clsx from 'clsx';
import { ServiceLayout } from 'layouts';
import { LangConstant } from 'const';
import { Spinner, DialogSupportCustomer, HtmlRender } from 'components';
import { useStyles } from './styles';
import { getServiceContent } from 'api/service';
import { ImageContainer } from 'components';
import { useGlobalContext } from 'store';

const ServiceDetails = (props) => {
  const id = props.match.params.id;
  const { getDataConfig } = useGlobalContext();
  const origin = window.location.origin;
  const { i18n } = useTranslation();
  const [height, setHeight] = useState(0);
  const ref = useRef(null);
  const classes = useStyles({ textHeight: height });
  const { t: getLabel } = useTranslation();
  const [loaded, setLoaded] = useState(false);
  const [isSeeMore, setIsSeeMore] = useState(false);
  const [isShowPopup, setIsShowPopup] = useState(false);
  const [serviceContent, setServiceContent] = useState({});
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    const run = async () => {
      setLoading(true);
      const res = await getDataConfig(
        `services${Number(id) + 1}`,
        i18n.language
      );
      setServiceContent(res.data[0].value);
      setLoading(false);
    };
    run();

    // const data = getServiceContent(id, i18n.language);
    // setServiceContent(data);

    if (document.documentElement.clientWidth < 812) {
      setIsSeeMore(true);
    } else {
      setIsSeeMore(false);
    }
    setLoaded(false);
  }, [id, i18n.language]);

  useEffect(() => {
    setHeight(ref.current.clientHeight);
  }, [window.innerHeight]);

  const header = (
    <img
      src={serviceContent.imgCover}
      alt="..."
      className={clsx(classes.image)}
    />
  );
  const content = (
    <div className={classes.root}>
      <div
        className="left-banner-1"
        hidden={!serviceContent.is_show_flash_left_top}
      >
        <ImageContainer
          cover={true}
          close={true}
          src={serviceContent.flash_left_top}
        ></ImageContainer>
      </div>
      <div
        className="left-banner-2"
        hidden={!serviceContent.is_show_flash_left_bottom}
      >
        <ImageContainer
          cover={true}
          close={true}
          src={serviceContent.flash_left_bottom}
        ></ImageContainer>
      </div>
      <div
        className="right-banner"
        hidden={!serviceContent.is_show_flash_right}
      >
        <ImageContainer
          cover={true}
          close={true}
          src={serviceContent.flash_right}
        ></ImageContainer>
      </div>
      <div
        className={clsx(
          classes.contentContainer,
          isSeeMore ? classes.paddingFooter : classes.height
        )}
      >
        <div className={classes.container}>
          <div
            className={clsx(classes.containerHtml, 'flex-content')}
            ref={ref}
          >
            <HtmlRender
              className={clsx(
                isSeeMore ? classes.contentFull : classes.contentHtml,
                classes.textHtml
              )}
              defaultStyle={false}
              html={serviceContent.content}
            />
            {!isSeeMore && (
              <div
                onClick={() => setIsSeeMore(true)}
                className={classes.seeMore}
              >
                {'>> ' + getLabel(LangConstant.TXT_SEE_MORE)}
              </div>
            )}
            <div className="center-root">
              <div className={classes.marginRight}>
                <Button
                  className={clsx(classes.button, classes.buttonLeft)}
                  variant="contained"
                  color="primary"
                  size="small"
                  onClick={() =>
                    serviceContent.linkPdf
                      ? window.open(serviceContent.linkPdf)
                      : window.open(
                          '/services/bang-gia-van-chuyen-nguyen-chuyen-va-ghep-xe-ban-ky.pdf'
                        )
                  }
                >
                  {getLabel(LangConstant.TXT_SEE_PRICE_LIST)}
                </Button>
              </div>
              <Button
                className={clsx(classes.button, classes.buttonRight)}
                variant="contained"
                color="primary"
                size="small"
                onClick={() => setIsShowPopup(true)}
              >
                {getLabel(LangConstant.TXT_CREATE_ORDER)}
              </Button>
            </div>
          </div>
        </div>
      </div>
      <DialogSupportCustomer
        isShow={isShowPopup}
        onClose={() => {
          setIsShowPopup(false);
        }}
      />
    </div>
  );
  return loading ? (
    <Spinner />
  ) : (
    <ServiceLayout
      header_backgroud_url={serviceContent.imgBackGroundHead}
      content_background_url={serviceContent.imgBackGround}
      header={header}
      content={content}
    ></ServiceLayout>
  );
};

export default ServiceDetails;

export const ARRAY_SERVICES = [
  {
    name: 'Vn ti ng b',
    href: '/service/0',
  },
  {
    name: 'Vn ti ng st',
    href: '/service/1',
  },
  {
    name: 'Vn ti ng bin',
    href: '/service/2',
  },
  {
    name: 'Vn ti ng hng khng',
    href: '/service/3',
  },
  {
    name: 'Th tc hi quan',
    href: '/service/4',
  },
  {
    name: 'Kho bi v lu tr hng ha',
    href: '/service/5',
  },
  {
    name: 'ng gi v chia chn',
    href: '/service/6',
  },
  {
    name: 'Giao hng im cui',
    href: '/service/7',
  },
];
