import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  root: {},
  headerBox: {
    display: 'flex',
    flexDirection: 'column',
  },

  locationTitle: {
    textAlign: 'center',
    fontSize: '1.2rem',
    fontWeight: 'bold',
    textTransform: 'uppercase',
  },
  locationContainer: {
    height: '60%',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'flex-end',
  },
  boxContainer: {
    width: '100%',
    height: '40%',
    display: 'flex',
    position: 'relative',
    justifyContent: 'center',
    // marginBottom: '20px',
    '& > div:last-child': {
      marginRight: '0px',
    },
  },
  boxBorder: {
    width: '20%',
    height: '80%',
    borderRadius: 20,
    boxSizing: 'border-box',
    padding: '3px',
    backgroundImage:
      'linear-gradient(to bottom, #b1aeaa61, white  20%,#64636347 )',
  },
  boxLocationContainer: {
    display: 'flex',
    width: '100%',
    justifyContent: 'center',
    '& > div:last-child': {
      paddingRight: '0px',
    },
  },
  border: {
    height: '100%',
    boxSizing: 'border-box',
    padding: '0 10px',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    borderRadius: 20,
    alignItems: 'center',

    textTransform: 'uppercase',
    backgroundColor: 'white',

    '& > p': {
      textTransform: 'uppercase',
      color: '#ff152c',
      textAlign: 'center',
      fontWeight: 800,
      lineHeight: '1.5rem',
      fontSize: '0.9rem',
    },
    '& > p:first-child': {
      fontSize: '1.2rem',
      height: '50%',
    },

    // '& > div': {
    //   width: '100%',
    //   height: 2,
    //   backgroundColor: '#ff152c',
    //   margin: '8px 0',
    // },
  },
  numberBor: {
    height: '50%',
    borderBottom: '2px solid #FF152C',
    width: '100%',
    display: 'flex',
    fontWeight: '700',

    color: '#FF152C',
    '& > span': {
      margin: 'auto',
      fontSize: '1.4rem',
    },
  },
  contentBor: {
    height: '50%',
    textAlign: 'center',

    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    flexDirection: 'column',
    color: '#FF152C',
    fontWeight: '600',
    '& > div': {
      fontSize: '0.75rem',
    },
  },
  boxLocation: {
    width: '16.6667%',
    paddingRight: '18px',
    fontSize: '0.9rem',
  },
  location: {
    display: 'flex',
    '& > div:last-child': {
      width: '100%',
    },
    '& > div >p:first-child': {
      fontWeight: '800',
      marginBottom: 10,
    },
    '& > div >p:last-child': {},
  },

  title: {
    textAlign: 'center',
    fontSize: '1.2rem',
    fontWeight: 800,
    textTransform: 'uppercase',
    color: '#1c1c1c',
    letterSpacing: '1px',
    width: '75%',
    position: 'relative',
    bottom: 0,
    paddingBottom: '10px',
  },
  '@media screen and (min-width: 1350px) and (max-width: 1589px) and (max-height:680px) and (min-height:601px)':
    {
      boxContainer: {
        marginBottom: '6px',
        height: '40%',
      },
      boxLocationContainer: {
        paddingTop: '5px',
      },
      boxBorder: {
        height: '90%',
      },
      locationText: {
        fontSize: '0.8rem',
      },
      location: {
        '& > div >p:first-child': {
          fontWeight: '800',
          marginBottom: 5,
        },
      },
    },
  '@media screen and (min-height:621px) and (max-width:1349px)': {
    boxBorder: {
      height: '100%',
    },
    numberBor: {
      '&>span': { fontSize: '1.3rem' },
    },
    boxLocation: {
      paddingRight: '5px',
    },
    location: {
      '&>div>p:first-child': {
        marginBottom: '5px',
      },
    },
    locationTitle: {
      paddingBottom: 5,
    },
  },
  '@media screen and (max-height:600px)': {
    locationTitle: {
      paddingBottom: 10,
    },
    numberBor: {
      '& > span': {
        fontSize: '1.2rem',
      },
    },
    contentBor: {
      '& > div': {
        fontSize: '0.7rem',
      },
    },
    border: {
      padding: 4,
    },
  },
  '@media screen and (max-width: 850px)': {
    title: {
      width: '100%',
    },
    boxContainer: {
      flexWrap: 'wrap',
      justifyContent: 'space-around',
    },
    boxBorder: {
      width: '120px',
      height: '80px',
      marginBottom: '10px',
    },
    numberBor: {
      '&> span': {
        fontSize: '1.1rem',
      },
    },
    boxLocationContainer: {
      flexWrap: 'wrap',
      justifyContent: 'space-between',
    },
    boxLocation: {
      width: '40%',
      margin: '16px 0',
    },
  },
}));
