import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  root: {},
  image: {
    height: '100%',
    minWidth: '100%',
    maxWidth: '100%',
    filter: 'brightness(80%)',
  },
  title: {
    textTransform: 'uppercase',
    height: '40px',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    // backgroundImage:
    //   'linear-gradient(90deg, transparent, #a6a39e80 54%, transparent 102%)',
    background:
      'radial-gradient(circle, rgba(236,236,236,1) 0%, rgba(255,255,255,1) 100%)',
    fontWeight: 800,
    color: '#ff152c',
    fontSize: 16,
  },
  boxInfo: {
    marginBottom: 20,
  },
  boxLogo: { display: 'flex', flexWrap: 'wrap' },
  logo: {
    maxHeight: '100%',
    maxWidth: '100%',
    boxSizing: 'border-box',
  },
  logoContainer: {
    width: '20%',
    minHeight: '50px',
    maxHeight: '10.5vh',
    padding: ' 10px 20px',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'flex-end',
  },
  '@media screen and (min-height:621px) and (max-width:1349px)': {
    boxInfo: {
      marginBottom: 0,
    },
  },
  '@media screen and (min-width: 1360px) and (max-width: 1599px)': {
    boxInfo: {
      marginBottom: 0,
    },
  },
  '@media screen and (min-width: 1920px)': {
    logoContainer: {
      padding: '15px 20px',
    },
  },
  '@media screen and (max-width: 850px)': {
    image: {
      objectFit: 'contain',
    },
    logoContainer: {
      width: '40%',
    },
    boxLogo: {
      justifyContent: 'space-around',
    },
  },
}));
