import React from 'react';
import { makeStyles } from '@material-ui/core';
import { CheckCircleOutlineOutlined } from '@material-ui/icons';
import { useTranslation } from 'react-i18next';
import clsx from 'clsx';
import { LangConstant } from 'const';

const Success = ({ cooperate }) => {
  const classes = useStyles();
  const { t: getLabel } = useTranslation();

  return (
    <div className={clsx('center-root', classes.submitBackground)}>
      <CheckCircleOutlineOutlined style={{ color: '#76ff03', fontSize: 50 }} />
      <div
        style={{
          color: '#76ff03',
          fontWeight: 800,
          fontSize: 16,
          marginTop: 16,
        }}
      >
        {getLabel(LangConstant.TXT_SUCESS_TITLE)}
      </div>
      {cooperate ? (
        <p style={{ fontSize: 13, marginTop: 8, width: '38%' }}>
          {getLabel(LangConstant.TXT_SUCCESS_2)}
        </p>
      ) : (
        <p style={{ fontSize: 13, marginTop: 8, width: '38%' }}>
          {getLabel(LangConstant.TXT_SUCCESS_1)}
        </p>
      )}

      <p style={{ fontSize: 13, marginTop: 8 }}>
        {getLabel(LangConstant.TXT_SINCERELY_THANK)}
      </p>
    </div>
  );
};

export default Success;

export const useStyles = makeStyles((theme) => ({
  submitBackground: {
    width: '100%',
    height: '50vh',
    flexDirection: 'column',
    marginTop: 32,
    background: 'url(/support/success.jpg)',
    backgroundSize: '100% 100%',
  },
}));
