import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    height: '100%',
  },
  image: {
    height: '100%',
    minWidth: '100%',
    maxWidth: '100%',
    // transition: 'opacity .4s ease-in',
    objectFit: 'cover',
  },
  imageFilter: {
    opacity: '0.6',
  },
  buttonLeft: {
    width: '200px',
  },
  buttonRight: {
    width: '200px',
  },
  imageContainer: {
    width: '100%',
    backgroundColor: '#e9e9e9',
    padding: '10px 0',
    height: '45%',
  },

  contentContainer: {
    width: '100%',
  },

  paddingFooter: {
    paddingBottom: 40,
  },

  textHtml: {
    textAlign: 'justify',
    lineHeight: '30px',
  },

  container: {
    display: 'flex',
    height: '100%',
  },

  containerHtml: {
    paddingRight: 15,
    height: '100%',
    fontSize: '1rem',
    '& p': {
      marginBottom: '10px',
    },
    '& h3': {
      marginTop: '15px',
      marginBottom: '5px',
      textTransform: 'uppercase',
      color: 'red',
    },
    '& h4': {
      textTransform: 'uppercase',
    },
  },

  containerMenu: {
    width: '22%',
    paddingTop: 10,
  },

  contentHtml: {
    display: '-webkit-box',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    WebkitLineClamp: 7,
    WebkitBoxOrient: 'vertical',
  },

  contentFull: {
    height: 'auto',
  },

  height: {
    height: '100%',
  },

  seeMore: {
    marginTop: 15,
    fontWeight: 800,
    cursor: 'pointer',
  },

  button: {
    marginTop: 10,
    height: 30,
    borderRadius: '10px 10px 10px 0px',
    fontWeight: 800,
    minWidth: 120,
  },
  marginRight: {
    marginRight: '5px',
  },

  '@media screen and (max-height:600px)': {
    contentHtml: {
      WebkitLineClamp: 3,
    },
  },
  '@media screen and (min-height:601px) and (max-height:700px)': {
    contentHtml: {
      WebkitLineClamp: 4,
    },
  },
  '@media screen and (min-height:701px) and (max-height:849px)': {
    contentHtml: {
      WebkitLineClamp: 5,
    },
  },
  '@media screen and (min-height:850px)': {
    contentHtml: {
      WebkitLineClamp: 7,
    },
  },
  '@media screen and (min-height:1000px)': {
    contentHtml: {
      WebkitLineClamp: 10,
    },
  },
  '@media screen and (max-width: 850px)': {
    containerHtml: {
      paddingRight: 0,
      width: '100%',
    },
    marginRight: {
      width: '50%',
    },
    buttonRight: {
      width: '50%',
    },
    buttonLeft: {
      width: '100%',
    },
    image: {
      objectFit: 'contain',
    },
  },
}));
