import React, { useState, useRef, useEffect } from 'react';
import { Typography } from '@material-ui/core';
import { useTranslation } from 'react-i18next';
import { HomeLayout } from 'layouts';
import { LangConstant } from 'const';
import { useStyles } from './styles';
import { TabButton, Spinner } from 'components';
import { Delivery, Stock, Other } from './components';
import { useGlobalContext } from 'store';

const Cooperate = () => {
  const { getDataConfig, PAGE_KEY } = useGlobalContext();
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState();
  const { t: getLabel, i18n } = useTranslation();
  const OBJ_TAB = getLabel(LangConstant.OBJ_TAB_COOPERATE, {
    returnObjects: true,
  });
  useEffect(() => {
    const run = async () => {
      setLoading(true);
      const res = await getDataConfig(PAGE_KEY.cooperate, i18n.language);
      setData(res.data[0].value);
      setLoading(false);
    };

    run();
  }, [i18n.language]);
  const classes = useStyles({ bgUrl: data?.imgBackGround });
  const arrayComponents = [<Delivery />, <Stock />, <Other />];

  const [tabIndex, setTabIndex] = useState(0);

  return loading ? (
    <Spinner />
  ) : (
    <HomeLayout typeFooter="copyright">
      <div className={classes.root}>
        <div className={classes.container}>
          <div className={classes.header}>
            <Typography className={classes.headerName}>
              {data?.textRed}
              {/* {getLabel(LangConstant.TXT_COOPERATE)} */}
            </Typography>
            <TabButton
              selectedIndex={tabIndex}
              arrayName={Object.values(OBJ_TAB)}
              onChangeTab={(index) => index !== tabIndex && setTabIndex(index)}
              cooperate
            />
          </div>
          <div className={classes.body}>{arrayComponents[tabIndex]}</div>
        </div>
      </div>
    </HomeLayout>
  );
};

export default Cooperate;
