import React, { useEffect, useState } from 'react';
import { IntroLayout } from 'layouts';
import { useStyles } from './styles';
import { OutStandingBox, Spinner } from 'components';
import clsx from 'clsx';
import { useTranslation } from 'react-i18next';
import { getOutstandingContent } from 'api/intro';
import { useGlobalContext } from 'store';

const OutStanding = () => {
  const classes = useStyles();
  const { i18n } = useTranslation();
  const [data, setData] = useState();
  const [loading, setLoading] = useState(false);
  const [data_1, setData_1] = useState();
  const { getDataConfig, PAGE_KEY } = useGlobalContext();
  useEffect(() => {
    const getData = async () => {
      setLoading(true);
      const res = await getDataConfig(PAGE_KEY.outstanding, i18n.language);
      const dataRes = await getOutstandingContent(i18n.language);
      setData(res.data[0].value);
      setData_1(dataRes);
      setLoading(false);
    };
    getData();
  }, [i18n.language]);

  const header = (
    <>
      {data && (
        <div className={classes.headerBox}>
          <div className={clsx('padding-right', classes.headerBoxText)}>
            {data.four_outstanding && (
              <OutStandingBox
                icon={data.four_outstanding[0].icon}
                title={data.four_outstanding[0].title}
                content={data.four_outstanding[0].text}
              />
            )}
          </div>
          {data_1 && (
            <div className={classes.ImageContainer}>
              <img
                className={classes.image}
                src={data_1.textImage}
                alt=""
              ></img>
            </div>
          )}
        </div>
      )}
    </>
  );

  const content = (
    <>
      {data && (
        <div className={clsx(classes.contentBox, 'fullheight')}>
          <div
            className={('fullheight', 'flex-content', classes.boxOutStanding)}
          >
            <div className={classes.outStandingBottom}>
              {data.four_outstanding && (
                <OutStandingBox
                  icon={data.four_outstanding[1].icon}
                  title={data.four_outstanding[1].title}
                  content={data.four_outstanding[1].text}
                  width={'100%'}
                />
              )}
            </div>
            <div className={classes.outStanding3}>
              {data.four_outstanding && (
                <OutStandingBox
                  icon={data.four_outstanding[2].icon}
                  title={data.four_outstanding[2].title}
                  content={data.four_outstanding[2].text}
                  width={'100%'}
                />
              )}
            </div>
          </div>
          <div className={classes.outStandingNotPadding}>
            {data.four_outstanding && (
              <OutStandingBox
                icon={data.four_outstanding[3].icon}
                title={data.four_outstanding[3].title}
                content={data.four_outstanding[3].text}
                width={'100%'}
              />
            )}
          </div>
        </div>
      )}
    </>
  );

  return (
    <>
      {data && (
        <>
          {loading ? (
            <Spinner />
          ) : (
            <IntroLayout
              background_url={data.imgBackGround}
              content={content}
              header={header}
              pathname={'outstanding'}
            ></IntroLayout>
          )}
        </>
      )}
    </>
  );
};

export default OutStanding;
