import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core';
import clsx from 'clsx';
import { PathConstant } from 'const';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { LangConstant } from 'const';

const Menu = (props) => {
  var pathname = window.location.pathname;
  const { titleProps, dataProps, height } = props;
  const { t: getLabel } = useTranslation();
  const classes = useStyles({
    noItem: dataProps ? dataProps.length : 7,
    height,
  });
  const data = dataProps ? dataProps : MOCK_DATA;
  const title = titleProps
    ? titleProps
    : LangConstant.TXT_HEADER_INTRO_2
    ? LangConstant.TXT_HEADER_INTRO_2
    : LangConstant.TXT_HEADER_INTRO;
  return (
    <div className={classes.menu}>
      <div className={classes.listItems}>
        <div className={clsx(classes.headerItem, 'padding-menu-title')}>
          {getLabel(title)
            .split('')
            .map((i, c) => {
              if (i !== ' ') {
                return (
                  <span
                    key={c}
                    className="titleMenu"
                    style={{ animationDelay: `${0.05 * c}s` }}
                  >
                    {i}
                  </span>
                );
              } else {
                return <span key={c}>&nbsp;</span>;
              }
            })}
        </div>
        {data &&
          data.length > 0 &&
          data.map((item, index) => (
            <div
              key={index}
              className={clsx(
                item.href === pathname ? classes.current : '',
                classes.item
              )}
              style={{ animationDelay: `${0.05 * index}s` }}
            >
              <Link
                className={clsx(classes.link, 'padding-left')}
                to={item.href}
              >
                {getLabel(item.name)}
              </Link>
            </div>
          ))}
      </div>
    </div>
  );
};

const MOCK_DATA = [
  {
    name: LangConstant.TXT_HEADER_INTRO_OPENLETTER,
    href: PathConstant.OPENLETTER,
  },
  {
    name: LangConstant.TXT_HEADER_INTRO_HISTORY,
    href: PathConstant.HISTORY,
  },
  {
    name: LangConstant.TXT_HEADER_INTRO_OVERVIEW,
    href: PathConstant.OVERVIEW,
  },
  {
    name: LangConstant.TXT_HEADER_INTRO_OUTSTANDING,
    href: PathConstant.OUTSTANDING,
  },
  {
    name: LangConstant.TXT_HEADER_INTRO_PARTNERS,
    href: PathConstant.PARTNER,
  },
  {
    name: LangConstant.TXT_HEADER_INTRO_ACHIVEMENTS,
    href: PathConstant.ACHIEVEMENTS,
  },
  {
    name: LangConstant.TXT_HEADER_INTRO_PROFILE,
    href: PathConstant.PROFILE,
  },
];

export default Menu;

const useStyles = makeStyles((theme) => ({
  menu: {
    width: '100%',
    maxWidth: '350px',
    minWidth: '200px',
    height: '100%',
    border: '1px solid #e1e1e1',
    boxShadow: '0px 8px 16px 0px rgba(0,0,0,0.2)',
    fontSize: '1rem',
    letterSpacing: '0.04rem',
  },
  listItems: {
    width: '100%',
    height: '100%',
    backgroundColor: '#ffffff',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
  },
  headerItem: {
    // height: ({ height }) => `${height}px`,
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-start',
    fontWeight: 800,
    backgroundColor: '#ff152c',
    color: '#ffffff',
    textTransform: 'uppercase',
    letterSpacing: '1px',
  },
  current: {
    textDecoration: 'underline',
    backgroundColor: '#a6a39e33',
    fontWeight: '800 !important',
  },
  item: {
    // height: ({ height }) => `${height}px`,
    // width: '100%',
    // display: 'flex',
    // alignItems: 'center',
    // justifyContent: 'flex-start',
    // fontWeight: 400,
    // cursor: 'pointer',
    animationName: 'moveMenuItem',
    animationDuration: '.6s',
    animationTimingFunction: 'ease-in-out',
    animationFillMode: 'backwards',
    height: '100%',
    '&:hover': {
      textDecoration: 'underline',
      backgroundColor: '#a6a39e33',
      fontWeight: 800,
    },
  },

  link: {
    width: '100%',
    height: '100%',
    display: 'flex',
    alignItems: 'center',
  },
  // '@media screen and (min-width: 1910px)': {
  //   menu: {
  //     width: '95%',
  //     marginLeft: 'auto',
  //   },
  // },
}));
