import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  root: {
    position: 'absolute',
    bottom: 0,
    width: '100vw',
    backgroundColor: theme.palette.common.white,
    flexDirection: 'column',
  },
  descriptionText: {
    fontSize: '1rem',
  },
  serviceText: {
    marginTop: 8,
    fontSize: '1rem',
    fontWeight: '800',
    color: theme.palette.primary.main,
    textTransform: 'uppercase',
  },
  '@media screen and (max-width: 850px)': {
    descriptionText: {
      fontSize: '1rem',
      textAlign: 'center',
    },
    serviceText: {
      fontSize: '1rem',
      marginTop: '4px',
      textAlign: 'center',
    },
    root: {
      position: 'unset',
      height: '120px',
    },
  },
}));
