import React from 'react';

const newsSideBarSearch = ({ getLabel, LangConstant }) => {
  return (
    <div className="news-sidebar-search">
      <label htmlFor="search-news">
        <img src="/news/search-icon.png" alt="" />
      </label>
      <input
        type="text"
        placeholder={getLabel(LangConstant.TXT_SEARCH_NEWS)}
        id="search-news"
        className="news-sidebar-search-input"
      />
    </div>
  );
};

export default newsSideBarSearch;
