import React from 'react';
import { makeStyles } from '@material-ui/core';
import { HighlightOffOutlined } from '@material-ui/icons';
import { useTranslation } from 'react-i18next';
import clsx from 'clsx';
import { LangConstant } from 'const';

const Failed = ({ cooperate }) => {
  const classes = useStyles();
  const { t: getLabel } = useTranslation();

  return (
    <div className={clsx('center-root', classes.submitBackground)}>
      <HighlightOffOutlined style={{ color: '#FF152C', fontSize: 50 }} />
      <div
        style={{
          color: '#FF152C',
          fontWeight: 600,
          fontSize: 16,
          marginTop: 5,
        }}
      >
        {getLabel(LangConstant.TXT_FAILED_TITLE)}
      </div>

      {cooperate ? (
        <p
          style={{
            fontSize: '16px',
            marginTop: '5px',
            width: '37%',
            textAlign: 'center',
          }}
        >
          {getLabel(LangConstant.TXT_FAILED_2)}
        </p>
      ) : (
        <p
          style={{
            fontSize: '16px',
            marginTop: '5px',
            width: '37%',
            textAlign: 'center',
          }}
        >
          {getLabel(LangConstant.TXT_FAILED_1)}
        </p>
      )}

      <p style={{ fontSize: 16, marginTop: 5 }}>
        {getLabel(LangConstant.TXT_SINCERELY_THANK)}
      </p>
    </div>
  );
};

export default Failed;

export const useStyles = makeStyles((theme) => ({
  submitBackground: {
    width: '100%',
    height: '50vh',
    flexDirection: 'column',
    marginTop: 32,
    background: 'url(/support/failed.jpg)',
    backgroundSize: '100% 100%',
  },
}));
