import { HtmlRender } from 'components';
import React, { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';
import './slider.css';
import { useTranslation } from 'react-i18next';
import { LangConstant } from 'const';
import slugify from 'utils/slugify';

const SliderHome = ({ data }) => {
  const { t: getLabel } = useTranslation();
  const [item, setItem] = useState(0);
  const [action, setAction] = useState(true);
  const couter = useRef(0);
  couter.current = data.length - 1;
  const timerId = useRef(0);
  useEffect(() => {
    if (data.length > 0) {
      const arrayServices = document.querySelectorAll('.section-slider');
      const activeE = document.querySelector('.section-slider.active');
      const nexteE = document.querySelector('.section-slider.next');
      const prevE = document.querySelector('.section-slider.prev');
      if (activeE) {
        activeE.classList.remove('active');
      }
      if (nexteE) {
        nexteE.classList.remove('next');
      }
      if (prevE) {
        prevE.classList.remove('prev');
      }
      if (item === arrayServices.length - 1) {
        arrayServices[item - 1].classList.add('prev');
        arrayServices[item].classList.add('active');
        arrayServices[0].classList.add('next');
      }
      if (item === 0) {
        arrayServices[arrayServices.length - 1].classList.add('prev');
        arrayServices[item].classList.add('active');
        arrayServices[1].classList.add('next');
      }
      if (item > 0 && item < arrayServices.length - 1) {
        arrayServices[item - 1].classList.add('prev');
        arrayServices[item].classList.add('active');
        arrayServices[item + 1].classList.add('next');
      }
    }
  }, [item, data]);
  useEffect(() => {
    timerId.current = setInterval(() => {
      setItem((prev) => {
        if (prev === couter.current) return 0;
        else return prev + 1;
      });
    }, 5000);
    return () => {
      clearInterval(timerId.current);
    };
  }, [action]);
  const handleNext = () => {
    clearInterval(timerId.current);
    setItem((prev) => {
      if (prev === data.length - 1) return 0;
      else return prev + 1;
    });
    setTimeout(() => {
      setAction(!action);
    }, 5000);
  };
  const handlePrev = () => {
    clearInterval(timerId.current);
    setItem((prev) => {
      if (prev === 0) return data.length - 1;
      else return prev - 1;
    });
    setTimeout(() => {
      setAction(!action);
    }, 5000);
  };
  const handleClickIcon = (i) => {
    clearInterval(timerId.current);
    setItem(Number(i));
    setTimeout(() => {
      setAction(!action);
    }, 5000);
  };

  // const handleImageLoad = (e) => {
  //   if (e.target.dataset.src) {
  //     var image = e.target;
  //     image.src = e.target.dataset.src;
  //     image.removeAttribute('data-src');
  //   }
  // };

  return (
    <div className="slider-wrap">
      {data?.map((s, i) => (
        <div key={i} className={`section-slider`}>
          <img
            className="slider-background"
            src={s.src}
            // data-src={s.data_src}
            // onLoad={handleImageLoad}
            loading={'lazy'}
          />
          <div className="slider-content">
            <h2 className="slider-content-title">{s.name}</h2>
            <HtmlRender
              className="slider-content-description"
              html={s.description}
              defaultStyle={false}
            ></HtmlRender>
            <Link className="slider-content-link" to={`${s.link}`}>
              <span className="slider-content-link-text">
                {getLabel(LangConstant.TXT_SEE_MORE)}
              </span>

              <svg
                className="slider-content-link-icon"
                focusable="false"
                viewBox="0 0 24 24"
                aria-hidden="true"
              >
                <path d="M20.08 11.42l-4.04-5.65c-.34-.48-.89-.77-1.48-.77-1.49 0-2.35 1.68-1.49 2.89L16 12l-2.93 4.11c-.87 1.21 0 2.89 1.49 2.89.59 0 1.15-.29 1.49-.77l4.04-5.65c.24-.35.24-.81-.01-1.16z"></path>
                <g fill="#515151" stroke="none">
                  <path d="M13.08 11.42L9.05 5.77C8.7 5.29 8.15 5 7.56 5 6.07 5 5.2 6.68 6.07 7.89L9 12l-2.93 4.11C5.2 17.32 6.07 19 7.56 19c.59 0 1.15-.29 1.49-.77l4.04-5.65c.24-.35.24-.81-.01-1.16z"></path>
                </g>
              </svg>
            </Link>
          </div>
        </div>
      ))}
      <div className="slider-current-list">
        {data.map((a, i) => (
          <div
            key={i}
            onClick={() => handleClickIcon(i)}
            className={`slider-current ${i === item ? 'active' : ''}`}
          >
            <svg
              className="slider-current-icon"
              focusable="false"
              viewBox="0 0 24 24"
              aria-hidden="true"
            >
              <circle cx="12" cy="12" r="8"></circle>
            </svg>
          </div>
        ))}
      </div>
      <button className="slider-btn next" onClick={handleNext}>
        <div className="btn-child">
          <svg
            className="slider-btn-icon"
            focusable="false"
            viewBox="0 0 24 24"
            aria-hidden="true"
          >
            <path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6z"></path>
          </svg>
        </div>
      </button>
      <button className="slider-btn prev" onClick={handlePrev}>
        <div className="btn-child">
          <svg
            className="slider-btn-icon"
            focusable="false"
            viewBox="0 0 24 24"
            aria-hidden="true"
          >
            <path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12z"></path>
          </svg>
        </div>
      </button>
    </div>
  );
};
const SliderNews = ({ data, getTime, categorys }) => {
  const [item, setItem] = useState(0);
  const [action, setAction] = useState(true);
  const timerId = useRef();
  useEffect(() => {
    timerId.current = setInterval(() => {
      setItem((prev) => {
        if (prev === data?.length - 1) {
          return 0;
        } else {
          return prev + 1;
        }
      });
    }, 3000);
    return () => {
      clearInterval(timerId.current);
    };
  }, [action, data]);
  const getNameCategory = (id) => {
    const category = categorys.find((c) => c.id === id);
    if (category?.name) {
      return category.name;
    }
  };
  return (
    <>
      {data?.map((s, i) => (
        <div
          key={i}
          className={`section-slider-news ${i === item ? 'active' : ''}`}
        >
          <img className="slider-news-background" src={s.hightlightImage} />
          <Link
            to={`/news/${slugify(s.title)}.${s.id}`}
            className="slider-news-content"
          >
            <h2 className="slider-news-content-typePost">
              {getNameCategory(s.newsCatogryId)}
            </h2>
            <p className="slider-news-content-title ">{s.title}</p>
            <p className="slider-news-content-timeUpload">
              <img src="/news/clockWhite.png" alt="" />
              {getTime(s.createdAt)}
            </p>
          </Link>
        </div>
      ))}
      <div className="slider-current-list">
        {data?.map((a, i) => (
          <div
            key={i}
            onClick={() => {
              clearInterval(timerId.current);
              setItem(i);
              setTimeout(() => {
                setAction(!action);
              }, 2000);
            }}
            className={`slider-current news ${item === i ? 'active' : ''}`}
          >
            <svg
              className="slider-current-icon news"
              focusable="false"
              viewBox="0 0 24 24"
              aria-hidden="true"
            >
              <circle cx="12" cy="12" r="8"></circle>
            </svg>
          </div>
        ))}
      </div>
    </>
  );
};
export { SliderHome, SliderNews };
