import { makeStyles } from '@material-ui/core';
import { HEADER_HEIGHT, FOOTER_HEIGHT } from 'layouts/home-layout/components';

export const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    background: ({ backgroundUrl }) =>
      backgroundUrl
        ? `url(${backgroundUrl})`
        : "url('support/question-bg-1.jpg')",
    backgroundSize: '100% 100%',
    height: '100%',
  },
  container: {
    width: '65%',
    margin: '0 auto',
    padding: '24px 0px',
    maxWidth: '65%',
    height: '100%',
  },
  header: {
    width: '100%',
    height: 'auto',
    display: 'flex',
    alignItems: 'flex-end',
    justifyContent: 'space-between',
  },
  headerName: {
    width: '30%',
    fontSize: 48,
    textAlign: 'center',
    textTransform: 'uppercase',
    color: theme.palette.common.white,
    fontWeight: 900,
    borderBottom: '5px double ' + theme.palette.primary.main,
    textShadow:
      '#FF152C 0px 0px 3px,   #FF152C 0px 0px 3px,   #FF152C 0px 0px 3px, #FF152C 0px 0px 3px,   #FF152C 0px 0px 3px,   #FF152C 0px 0px 3px',
  },
  '@media screen and (min-width: 1350px) and (max-width: 1589px)': {
    headerName: {
      fontSize: 32,
    },
  },
  '@media screen and (min-width:1200px) and (max-height:600px)': {
    headerName: {
      fontSize: 24,
    },
  },
  '@media screen and (max-width: 850px)': {
    header: {
      display: 'block',
    },
    headerName: {
      width: '100%',
      marginBottom: 16,
    },
    container: {
      width: '80%',
      maxWidth: '80%',
    },
    root: {
      backgroundSize: 'cover',
    },
  },
}));
