import { makeStyles } from '@material-ui/core';
import { HEADER_HEIGHT, FOOTER_HEIGHT } from 'layouts/home-layout/components';

export const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    background: ({ backGroundUrl }) =>
      backGroundUrl
        ? `url(${backGroundUrl})`
        : `url('/support/question-bg-1.jpg')`,
    backgroundSize: '100% 100%',
    height: '100%',
  },
  textEditor: {
    width: '80%',
    height: 120,
  },
  container: {
    width: '65%',
    height: '100%',
    margin: '0 auto',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
  },
  contentContainer: {
    height: '25%',
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
    paddingBottom: '30px',
    justifyContent: 'center',
  },

  title: {
    color: '#FF152C',
    width: '100%',
    fontSize: '1.2rem',
    textAlign: 'center',
    fontWeight: 'bold',
    textTransform: 'uppercase',
    marginBottom: 16,
  },
  content: {
    fontSize: '1rem',
    lineHeight: '28px',
    textAlign: 'justify',
  },
  containerForm: {
    width: '100%',
    height: '60%',
    marginTop: '10px',
    padding: '30px 0',
    borderRadius: '16px',
    flexDirection: 'column',
    backgroundColor: '#cecfd2',
    justifyContent: 'space-around',
  },
  input: {
    height: 40,
    width: '80%',
    backgroundColor: theme.palette.common.white,
  },
  inputArea: {
    height: 120,
  },
  multiline: {
    height: '100% !important',
  },
  button: {
    height: 35,
    padding: '4px 40px',
    borderRadius: 8,
  },
  '@media screen and (min-height:600px) and (max-height:900px)': {
    textEditor: {
      height: 90,
    },
    input: {
      height: 30,
    },
  },
  '@media screen and (max-height:700px) and (min-height:601px)': {
    contentContainer: {
      height: '28%',
      paddingBottom: '0',
    },
  },
  '@media screen and (max-height:600px)': {
    input: {
      marginBottom: 10,
      height: 30,
    },
    textEditor: {
      height: 90,
    },
    contentContainer: {
      height: '28%',
      paddingBottom: '0',
    },
  },

  '@media screen and (min-width: 1910px)': {
    title: {
      fontSize: '1.5rem',
    },
    containerForm: {
      marginTop: 0,
    },
  },
  '@media screen and (max-width: 850px)': {
    container: {
      width: '80%',
    },
    title: {
      margin: '20px 0',
    },
    input: {
      height: 50,
      width: '90%',
      marginBottom: 20,
      borderRadius: '10px',
    },
    textEditor: {
      width: '90%',
      height: 100,
      marginBottom: 20,
    },
    containerForm: {
      marginBottom: '25px',
    },
    root: {
      backgroundSize: 'cover',
    },
  },
}));
