import React, { useState } from 'react';
import { IntroLayout } from 'layouts';
import { useStyles } from './styles';
import { HtmlRender, Spinner } from 'components';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import clsx from 'clsx';
import { useTranslation } from 'react-i18next';
// import { getOverviewContent } from 'api/intro';
import { LangConstant } from 'const';
import { useGlobalContext } from 'store';

const Overview = () => {
  const classes = useStyles();

  const { getDataConfig, PAGE_KEY } = useGlobalContext();

  const { t: getLabel, i18n } = useTranslation();
  const [data, setData] = useState();
  const [loading, setLoading] = useState(false);

  React.useEffect(() => {
    const getData = async () => {
      setLoading(true);
      // const dataRes = await getOverviewContent(i18n.language);
      const res = await getDataConfig(PAGE_KEY.overview, i18n.language);
      setData(res.data[0].value);
      setLoading(false);
    };
    getData();
  }, [i18n.language]);

  const header = (
    <>
      {data && (
        <div className={classes.headerBox}>
          <HtmlRender
            pathname={'overview'}
            html={data.text_head.text}
            className="padding-bottom"
          />
          <div className={classes.title}>{data.text_head.slogan}</div>
        </div>
      )}
    </>
  );

  return (
    <>
      {data && (
        <>
          {loading ? (
            <Spinner />
          ) : (
            <IntroLayout
              background_url={data.imgBackGround}
              headerHeight="45%"
              contentHeight="55%"
              header={header}
              content={
                <div
                  className={clsx(
                    classes.contentBox,
                    'flex-content fullheight'
                  )}
                >
                  <div
                    className={clsx(
                      classes.boxContainer,
                      'overview-box-height'
                    )}
                  >
                    {data.five_box &&
                      data.five_box.map((property, i) => (
                        <div
                          key={i}
                          className={clsx(
                            classes.boxBorder,
                            'flex-cointent',
                            'overview-box-margin'
                          )}
                        >
                          <div className={classes.border}>
                            <div className={classes.numberBor}>
                              <span>{property.number}</span>
                            </div>
                            <div
                              className={classes.contentBor}
                              dangerouslySetInnerHTML={{
                                __html: property.text,
                              }}
                            ></div>
                          </div>
                        </div>
                      ))}
                  </div>
                  <div className={classes.locationContainer}>
                    <div className={classes.locationTitle}>
                      {getLabel(LangConstant.TXT_OVERVIEW_OFFICE)}
                    </div>
                    <div
                      className={clsx(
                        classes.boxLocationContainer,
                        'padding-top'
                      )}
                    >
                      {data.six_place &&
                        data.six_place.map((location, index) => (
                          <div key={index} className={classes.boxLocation}>
                            <div className={classes.location}>
                              <div>
                                <LocationOnIcon
                                  style={{
                                    fontSize: '1.2rem',
                                    paddingRight: '5px',
                                  }}
                                />
                              </div>
                              <div>
                                <p>{location.name}</p>
                                <p className={classes.locationText}>
                                  {location.adress}
                                </p>
                              </div>
                            </div>
                          </div>
                        ))}
                    </div>
                  </div>
                </div>
              }
            />
          )}
        </>
      )}
    </>
  );
};

export default Overview;
