import React, { useState, useEffect } from 'react';
import { Typography } from '@material-ui/core';
import { useTranslation } from 'react-i18next';
import { HomeLayout } from 'layouts';
import { LangConstant } from 'const';
import { TabButton, Spinner } from 'components';
import { useStyles } from './styles';
import Body from './components/Body';
import { useGlobalContext } from 'store';

const Search = () => {
  const { getDataConfig, PAGE_KEY } = useGlobalContext();
  const { t: getLabel, i18n } = useTranslation();
  const OBJ_TAB = getLabel(LangConstant.OBJ_TAB_SEARCH, {
    returnObjects: true,
  });

  const SearchType = {
    1: 'branchs',
    2: 'warehouses',
    3: 'offices',
  };
  const [data, setData] = useState();
  const [loading, setLoading] = useState(false);
  const [tabIndex, setTabIndex] = useState(1);
  useEffect(() => {
    const run = async () => {
      setLoading(true);
      const res = await getDataConfig(PAGE_KEY.search, i18n.language);
      setData(res.data[0].value);
      setLoading(false);
    };
    run();
  }, [i18n.language]);
  const classes = useStyles({ backgroundUrl: data?.imgBackGround });

  return loading ? (
    <Spinner />
  ) : (
    <HomeLayout typeFooter="copyright">
      <div className={classes.root}>
        <div className={classes.container}>
          <div className={classes.header}>
            <Typography className={classes.headerName}>
              {data?.textRed}
              {/* {getLabel(LangConstant.TXT_SEARCH_HEADER)} */}
            </Typography>
            <TabButton
              selectedIndex={tabIndex}
              arrayName={Object.values(OBJ_TAB)}
              onChangeTab={(index) => index !== tabIndex && setTabIndex(index)}
            />
          </div>
          <Body
            dataImg={data && data[ARRAY_IMG[tabIndex]]}
            type={tabIndex !== 0 ? SearchType[tabIndex] : ''}
          />
        </div>
      </div>
    </HomeLayout>
  );
};

const ARRAY_IMG = {
  0: 'imgOrders',
  1: 'ImgBranch',
  2: 'imgWarehosing',
  3: 'imgPostOffice',
};
export default Search;
