import React, { useState, useEffect } from 'react';
import {
  Box,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Paper,
} from '@material-ui/core';
import { AppPagination, Spinner } from 'components';
import { Delete } from '@material-ui/icons';
import { useStyles } from './styles';
import { AdminLayout } from 'layouts';
import { useGlobalContext } from 'store';
import clsx from 'clsx';
import style from '../Customer.module.css';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

const AdminCooperate = () => {
  const classes = useStyles();
  const {
    getTransportCooperate,
    getWarehouseCooperate,
    getCooperateOther,
    exportTransport,
    exportWarehouse,
    exportOther,
  } = useGlobalContext();
  const [arrayCooperates, setArrayCooperates] = useState();
  const [typeCooperate, setTypeCooperate] = useState(0);
  const [loading, setLoading] = useState(false);
  const [pageCurrent, setPageCurrent] = useState(1);
  const [totalPage, setTotalPage] = useState();
  const [form, setForm] = useState({
    from: '',
    to: '',
  });

  const getTime = (e) => {
    const date = new Date(e);
    const day = date.getDate();
    const month = date.getMonth() + 1;
    const year = date.getFullYear();
    return `${day}/${month}/${year}`;
  };
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const setFrom = (e) => {
    setStartDate(e);
    setForm({ ...form, from: getTime(e) });
  };
  const setTo = (e) => {
    setEndDate(e);
    setForm({ ...form, to: getTime(e) });
  };
  const handleExportCooperate = async () => {
    let res;
    if (typeCooperate === 0) {
      res = await exportTransport(form);
    } else if (typeCooperate === 1) {
      res = await exportWarehouse(form);
    } else {
      res = await exportOther(form);
    }
    const url = window.URL.createObjectURL(new Blob([res.data]));
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', 'cooperate.xlsx');
    document.body.appendChild(link);
    link.click();
    link.parentNode.removeChild(link);
  };
  useEffect(() => {
    const run = async () => {
      setLoading(true);
      let res;
      switch (typeCooperate) {
        case 0:
          res = await getTransportCooperate(pageCurrent);
          break;
        case 1:
          res = await getWarehouseCooperate(pageCurrent);
          break;
        case 2:
          res = await getCooperateOther(pageCurrent);
          break;

        default:
          break;
      }
      if (res.success) {
        if (res.total % 6 !== 0) {
          setTotalPage(Math.floor(res.total / 6) + 1);
        } else {
          setTotalPage(res.total / 6);
        }
        setArrayCooperates(res.data);
      } else {
        setArrayCooperates([]);
      }
      setLoading(false);
    };
    run();
  }, [pageCurrent, typeCooperate]);
  return (
    <AdminLayout>
      {loading && <Spinner />}
      <div className={style.boxHeader}>
        <Button
          variant="outlined"
          className={clsx(classes.buttonType, {
            [classes.active]: typeCooperate === 0,
          })}
          onClick={() => setTypeCooperate(0)}
        >
          Vn ti
        </Button>
        <Button
          variant="outlined"
          className={clsx(classes.buttonType, {
            [classes.active]: typeCooperate === 1,
          })}
          onClick={() => setTypeCooperate(1)}
        >
          Kho bi
        </Button>
        <Button
          variant="outlined"
          className={classes.buttonType}
          className={clsx(classes.buttonType, {
            [classes.active]: typeCooperate === 2,
          })}
          onClick={() => setTypeCooperate(2)}
        >
          Khc
        </Button>
        <div className={style.formExport}>
          <label htmlFor="from">From</label>
          <DatePicker selected={startDate} onChange={setFrom} />
        </div>
        <div className={style.formExport}>
          <label htmlFor="to">To</label>
          <DatePicker selected={endDate} onChange={setTo} />
        </div>
        <Button variant="outlined" onClick={handleExportCooperate}>
          Export
        </Button>
      </div>
      <TableContainer className={classes.tableCooperate} component={Paper}>
        <Table aria-label="caption table">
          <TableHead>
            {typeCooperate === 0 && (
              <TableRow>
                <TableCell>H tn/Tn doanh nghip</TableCell>
                <TableCell align="left">a ch</TableCell>
                <TableCell align="left">S in thoi</TableCell>
                <TableCell align="left">Email</TableCell>
                <TableCell align="left">S lng xe</TableCell>
                <TableCell align="left">Loi xe</TableCell>
                <TableCell align="left">Trng ti xe</TableCell>
                <TableCell align="left">Kch thc thng xe</TableCell>
                <TableCell align="left">Ghi ch</TableCell>
                <TableCell align="left">Loi hp ng</TableCell>
              </TableRow>
            )}
            {typeCooperate === 1 && (
              <TableRow>
                <TableCell>H tn/Tn doanh nghip</TableCell>
                <TableCell align="left">a ch</TableCell>
                <TableCell align="left">S in thoi</TableCell>
                <TableCell align="left">Email</TableCell>
                <TableCell align="left">S lng kho</TableCell>
                <TableCell align="left">a im</TableCell>
                <TableCell align="left">Din tch kho</TableCell>
                <TableCell align="left">Ghi ch</TableCell>
                <TableCell align="left">Loi hp ng</TableCell>
              </TableRow>
            )}
            {typeCooperate === 2 && (
              <TableRow>
                <TableCell>H tn/Tn doanh nghip</TableCell>
                <TableCell align="left">a ch</TableCell>
                <TableCell align="left">S in thoi</TableCell>
                <TableCell align="left">Email</TableCell>
                <TableCell align="left">Ghi ch</TableCell>
                <TableCell align="left">Loi hp ng</TableCell>
              </TableRow>
            )}
          </TableHead>
          <TableBody>
            {typeCooperate === 0 &&
              arrayCooperates?.map((item, index) => (
                <TableRow
                  key={index}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                  <TableCell align="left">{item.name}</TableCell>
                  <TableCell align="left">{item.address}</TableCell>
                  <TableCell align="left">{item.phone}</TableCell>
                  <TableCell align="left">{item.email}</TableCell>
                  <TableCell align="left">{item.numberVehicle}</TableCell>
                  <TableCell align="left">{item.rangeVehicle}</TableCell>
                  <TableCell align="left">{item.vehicleWeight}</TableCell>
                  <TableCell align="left">{item.dementionTruck}</TableCell>
                  <TableCell
                    align="left"
                    dangerouslySetInnerHTML={{ __html: item.other }}
                  ></TableCell>
                  <TableCell align="left">{item.type}</TableCell>
                </TableRow>
              ))}
            {typeCooperate === 1 &&
              arrayCooperates?.map((item, index) => (
                <TableRow
                  key={index}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                  <TableCell align="left">{item.name}</TableCell>
                  <TableCell align="left">{item.address}</TableCell>
                  <TableCell align="left">{item.phone}</TableCell>
                  <TableCell align="left">{item.email}</TableCell>
                  <TableCell align="left">{item.numberWarehouse}</TableCell>
                  <TableCell align="left">{item.location}</TableCell>
                  <TableCell align="left">{item.warehouseArea}</TableCell>
                  <TableCell
                    align="left"
                    dangerouslySetInnerHTML={{ __html: item.other }}
                  ></TableCell>
                  <TableCell align="left">{item.type}</TableCell>
                </TableRow>
              ))}
            {typeCooperate === 2 &&
              arrayCooperates?.map((item, index) => (
                <TableRow
                  key={index}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                  <TableCell align="left">{item.name}</TableCell>
                  <TableCell align="left">{item.address}</TableCell>
                  <TableCell align="left">{item.phone}</TableCell>
                  <TableCell align="left">{item.email}</TableCell>
                  <TableCell
                    align="left"
                    dangerouslySetInnerHTML={{ __html: item.other }}
                  ></TableCell>
                  <TableCell align="left">{item.type}</TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </TableContainer>
      <AppPagination
        totalPage={totalPage}
        onChange={(page) => setPageCurrent(page)}
      />
    </AdminLayout>
  );
};

export default AdminCooperate;

export const ARRAY_COOPERATES = [
  {
    name: 'Cty A',
    address: '123 Ton That Tung, Cau giay, Ha Noi',
    phone: '012345678',
    email: 'a@gmail.com',
    number_vehicle: '123',
    type_vehicle: '123',
    weight_vehicle: '123',
    width_vehicle: '123',
    number_warehouse: '123',
    location_warehouse: '123',
    area_warehouse: '123',
    content: '123',
    type_customer: '1',
    type_cooperate: '1  ',
  },
];
