import React from 'react';
import PropTypes from 'prop-types';
import { HomeLayout } from 'layouts';
import { useStyles } from './styles';
import { Menu } from 'components';
import clsx from 'clsx';
import { LangConstant } from 'const';

const ServiceLayout = ({
  content,
  header,
  header_backgroud_url,
  content_background_url,
}) => {
  const classes = useStyles({ header_backgroud_url, content_background_url });

  return (
    <HomeLayout typeFooter="copyright">
      <div className={classes.main}>
        <div className={classes.headerContainer}>
          <div className={clsx(classes.header, 'root-body', 'padding')}>
            {header}
          </div>
        </div>
        <div className={classes.contentContainer}>
          <div className={clsx(classes.content, 'root-body', 'padding')}>
            <div className={classes.contentItem}>{content}</div>
            <div className={clsx(classes.menu, 'hideOnMobile')}>
              <Menu
                dataProps={ARRAY_SERVICES}
                titleProps={LangConstant.TXT_HEADER_SERVICE}
                height={38}
              ></Menu>
            </div>
          </div>
        </div>
      </div>
    </HomeLayout>
  );
};

// ServiceLayout.propTypes = {
//   content: PropTypes.node,
//   header: PropTypes.string,
//   header_backgroud_url: PropTypes.string,
//   content_background_url: PropTypes.string,
// };

export default ServiceLayout;

const ARRAY_SERVICES = [
  {
    name: LangConstant.TXT_MENU_SERVICE_0,
    href: '/service/0',
  },
  {
    name: LangConstant.TXT_MENU_SERVICE_1,
    href: '/service/1',
  },
  {
    name: LangConstant.TXT_MENU_SERVICE_2,
    href: '/service/2',
  },
  {
    name: LangConstant.TXT_MENU_SERVICE_3,
    href: '/service/3',
  },
  {
    name: LangConstant.TXT_MENU_SERVICE_4,
    href: '/service/4',
  },
  {
    name: LangConstant.TXT_MENU_SERVICE_5,
    href: '/service/5',
  },
  {
    name: LangConstant.TXT_MENU_SERVICE_6,
    href: '/service/6',
  },
  {
    name: LangConstant.TXT_MENU_SERVICE_7,
    href: '/service/7',
  },
];
