import React, { useState } from 'react';
import BaseLayout from 'layouts/base-layout';
import style from './Profile.module.css';
import clsx from 'clsx';
import { Menu, Spinner } from 'components';
import { useStyles } from './styles';
import GetAppIcon from '@material-ui/icons/GetApp';
import { useTranslation } from 'react-i18next';
// import { getProfileContent } from 'api/intro';
import { LangConstant } from 'const';
import { useGlobalContext } from 'store';

const Profile = () => {
  const classes = useStyles();
  const { getDataConfig, PAGE_KEY } = useGlobalContext();
  const { t: getLabel, i18n } = useTranslation();
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState({});
  React.useEffect(() => {
    const getData = async () => {
      setLoading(true);
      // const dataRes = await getProfileContent(i18n.language);
      const res = await getDataConfig(PAGE_KEY.profile, i18n.language);

      setData(res.data[0].value);
      setLoading(false);
    };
    getData();
  }, [i18n.language]);

  const onDownloadFile = (url) => {
    fetch(url).then((response) => {
      response.blob().then((blob) => {
        let url = window.URL.createObjectURL(blob);
        let a = document.createElement('a');
        a.href = url;
        const temp = url.split('/');
        a.download = temp[temp.length - 1];
        a.click();
      });
    });
  };

  return (
    <>
      {loading ? (
        <Spinner />
      ) : (
        <BaseLayout background_url={data.imgBackGround} profile>
          <div className={classes.profileContainer}>
            <div className={classes.pdfContainer}>
              <iframe src={data.linkIframe} className="fullwidth fullheight" />
            </div>
            <div className={classes.menu}>
              <div style={{ height: '40vh', width: '100%' }}>
                <Menu />
              </div>
              <button
                className={classes.buttonDownload}
                onClick={() =>
                  data.linkFilePdf
                    ? onDownloadFile(data.linkFilePdf)
                    : onDownloadFile('/intro/profile_VN.pdf')
                }
              >
                <GetAppIcon className={classes.iconDownload} />
                <span className={classes.textDonwload}>
                  {getLabel(LangConstant.TXT_BUTTON_DOWNLOAD)}{' '}
                </span>
              </button>
            </div>
          </div>
          <div className={classes.textViettel}>
            <div className={classes.boxTextViettel}>
              <img
                className={style.imgEffect}
                src={data.imgLogo}
                alt=".."
              ></img>
              <img
                className={style.imgEffect}
                src={data.imgLogo}
                alt=".."
              ></img>
            </div>
          </div>
        </BaseLayout>
      )}
    </>
  );
};

export default Profile;
