import React, { useState } from 'react';
import { makeStyles, Button, Typography, Radio } from '@material-ui/core';
import { useTranslation } from 'react-i18next';
import clsx from 'clsx';
import { LangConstant } from 'const';
import { useGlobalContext } from 'store';
import { Success, Failed, InputSection, TextEditor } from 'components';

const Other = () => {
  const classes = useStyles();
  const { t: getLabel } = useTranslation();
  const { addOtherCooperate } = useGlobalContext();
  const OBJ_HEADER = getLabel(LangConstant.OBJECT_HEADER, {
    returnObjects: true,
  });
  const OBJ_INPUT = getLabel(LangConstant.OBJ_OTHER, {
    returnObjects: true,
  });

  const [radioOption, setRadioOption] = useState(0);
  const [isSuccess, setIsSuccess] = useState(false);
  const [isFailed, setIsFailed] = useState(false);
  const [actionBtn, setActionBtn] = useState(false);
  const isNumeric = (value) => {
    return /^-?\d+$/.test(value);
  };
  const isEmail = (value) => {
    return /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(value);
  };

  const onSubmit = async () => {
    setActionBtn(true);
    if (!isNumeric(form.phone) || !isEmail(form.email)) {
      alert('Vui lng nhp ng s in thoi hoc email');
      setIsFailed(true);
    } else {
      const res = await addOtherCooperate(form);
      if (res.success) {
        setIsSuccess(true);
      } else {
        setIsFailed(true);
      }
    }
  };
  const [form, setForm] = useState({
    name: '',
    address: '',
    phone: '',
    email: '',
    other: '',
    type: 'individual',
  });
  const handleChangEditor = (eee) => {
    setForm({ ...form, other: eee });
  };
  return (
    <>
      {!isSuccess && !isFailed && (
        <div className={clsx('center-root', classes.root)}>
          <div className={classes.headerBox}>
            <div className={classes.headerBoxLeft} />
            <div className={classes.headerBoxRight}>
              <Typography className={classes.header}>
                {getLabel(LangConstant.TXT_DELIVERY_HEADER)}
              </Typography>
              {Object.values(OBJ_HEADER).map((text, index) => (
                <div key={index} style={{ display: 'flex', marginLeft: 24 }}>
                  <Radio
                    checked={index === radioOption}
                    value={index}
                    onChange={() => {
                      if (index === 0) {
                        setForm({ ...form, type: 'individual' });
                      } else {
                        setForm({ ...form, type: 'enterprise' });
                      }
                      setRadioOption(index);
                    }}
                    color="primary"
                    size="small"
                    style={{ padding: 0, marginRight: 12 }}
                  />
                  <Typography
                    className={classes.header}
                    style={{ fontStyle: 'italic', fontWeight: 400 }}
                  >
                    {text}
                  </Typography>
                </div>
              ))}
            </div>
          </div>
          {Object.keys(OBJ_INPUT).map((key, index) => (
            <Input
              key={index}
              name={Object.keys(form)[index]}
              title={OBJ_INPUT[key]}
              last={index === Object.keys(OBJ_INPUT).length - 1}
              form={form}
              setForm={setForm}
              handleChangEditor={handleChangEditor}
            />
          ))}
          <Button
            onClick={onSubmit}
            className={classes.button}
            variant="contained"
            color="primary"
            size="small"
            disabled={actionBtn}
          >
            {getLabel(LangConstant.TXT_BUTTON_COOPERATE)}
          </Button>

          <span className={classes.spanNote}>
            (Vui lng in ng s in thoi v a ch email  hon tt thng
            tin)
          </span>
        </div>
      )}

      {isSuccess && <Success cooperate={true} />}
      {isFailed && <Failed cooperate={true} />}
    </>
  );
};

export default Other;

const Input = ({
  title,
  last,
  name,
  form,
  setForm,
  handleChangEditor,
  ...otherProps
}) => {
  const classes = useStyles({ last });

  return (
    <div className={classes.filedName}>
      <Typography className={classes.label}>{title}</Typography>
      {last ? (
        <div className={classes.boxEditor}>
          <TextEditor cooperate onChange={handleChangEditor} />
        </div>
      ) : (
        <InputSection
          type="text"
          value={form[name]}
          onChange={(e) =>
            setForm({ ...form, [e.target.name]: e.target.value })
          }
          name={name}
        />
      )}
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    height: '100%',
    flexDirection: 'column',
  },
  spanNote: {
    color: 'red',
    display: 'inline-block',
    fontSize: '14px',
    fontStyle: 'italic',
  },
  label: {
    width: '25%',
    minWidth: 200,
    fontSize: 16,
    fontWeight: 800,
  },
  headerBoxRight: {
    display: 'flex',
    width: '75%',
    minHeight: 35,
    alignItems: 'center',
  },
  headerBoxLeft: {
    width: '25%',
    minWidth: 200,
  },
  boxEditor: {
    border: '1px solid #CECFD2',
    borderRadius: '6px',
    width: '75%',
    height: '80%',
  },
  header: {
    fontSize: 18,
    fontWeight: 600,
    color: theme.palette.primary.main,
  },
  filedName: {
    display: 'flex',
    alignItems: 'center',
    width: '100%',
    height: '100%',
    flex: ({ last }) => `${last ? '3' : '1'}`,
  },
  headerBox: {
    display: 'flex',
    width: '100%',
    height: '100%',
    flex: '1',
  },
  button: {
    flex: '1',
    padding: '4px 40px',
    borderRadius: 8,
    maxHeight: 40,
  },
  // '@media screen and (min-width: 1910px)': {
  //   headerBox: {
  //     padding: 30,
  //   },
  // },
  '@media screen and (max-height:600px)': {
    filedName: {
      '& >p': { fontSize: '0.9rem!important' },
    },
  },
  '@media screen and (max-width: 850px)': {
    headerBoxLeft: {
      display: 'none',
    },
    headerBoxRight: {
      display: 'block',
      width: '100%',
      marginBottom: '16px',
    },
    filedName: {
      display: 'block',
      width: '100%',
    },
    label: {
      width: '100%',
      textAlign: 'left',
      marginBottom: '10px',
    },
    boxEditor: {
      width: '100%',
      height: '80px',
      marginBottom: '10px',
    },
  },
}));
