import React, { useState, useEffect } from 'react';
import style from './Login.module.css';
import { useHistory, Redirect } from 'react-router-dom';
import { useGlobalContext } from 'store';
const Login = () => {
  const history = useHistory();
  const { isAuthenticated, login } = useGlobalContext();
  const [valueLogin, setValueLogin] = useState({
    email: '',
    password: '',
  });
  const [message, setMessage] = useState(false);
  const { email, password } = valueLogin;
  const handleChange = (e) => {
    setValueLogin({ ...valueLogin, [e.target.name]: e.target.value });
  };
  const handleLogin = async (e) => {
    e.preventDefault();
    const res = await login(valueLogin);
    if (!res.success) {
      setMessage(true);
    } else {
      history.push('/admin/customer/order');
    }
  };
  useEffect(() => {
    if (isAuthenticated) {
      history.push('/admin/customer/order');
    }
    // document.addEventListener('visibilitychange', () => {
    //   if (document.visibilityState === 'hidden') {
    //     console.log('n ri');
    //   }
    //   if (document.visibilityState === 'visible') {
    //     console.log('hin ri');
    //   }
    // });
  }, [isAuthenticated]);
  return (
    <>
      <div className={style.container}>
        <form className={style.form} onSubmit={handleLogin}>
          <div className={style.imgLogo}>
            <img src="/logo_app.png" alt="" />
          </div>
          <div className={style.formUserName}>
            <input
              placeholder="Email"
              type="text"
              className={style.inputUserName}
              name="email"
              value={email}
              required
              onChange={handleChange}
            />
          </div>
          <div className={style.formPassword}>
            <input
              placeholder="Password"
              type="password"
              className={style.inputPassword}
              name="password"
              value={password}
              onChange={handleChange}
              required
            />
          </div>
          <div className={style.btnLoginBox}>
            <button type="submit" className={style.btnLogin}>
              Login
            </button>
          </div>
          {message && (
            <h1 style={{ color: 'red', fontSize: '1rem' }}>
              Username or password invalid
            </h1>
          )}
        </form>
      </div>
    </>
  );
};

export default Login;
