import React, { useEffect, useState } from 'react';
import {
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Paper,
} from '@material-ui/core';
import { Delete } from '@material-ui/icons';
import { AppPagination, Spinner } from 'components';
import { useStyles } from './styles';
import { AdminLayout } from 'layouts';
import { useGlobalContext } from 'store';
import style from '../Customer.module.css';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

const Order = () => {
  const classes = useStyles();
  const [arrayOrders, setArrayOrders] = useState();
  const [loading, setLoading] = useState(false);
  const [totalPage, setTotalPage] = useState(false);
  const [pageCurrent, setPageCurrent] = useState(1);
  const [form, setForm] = useState({
    from: '',
    to: '',
  });
  const { getOrders, exportOrders } = useGlobalContext();
  const handleChangePage = (page) => {
    setPageCurrent(page);
  };
  const getTime = (e) => {
    const date = new Date(e);
    const day = date.getDate();
    const month = date.getMonth() + 1;
    const year = date.getFullYear();
    return `${day}/${month}/${year}`;
  };
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const setFrom = (e) => {
    setStartDate(e);
    setForm({ ...form, from: getTime(e) });
  };
  const setTo = (e) => {
    setEndDate(e);
    setForm({ ...form, to: getTime(e) });
  };

  useEffect(() => {
    const run = async () => {
      setLoading(true);
      const res = await getOrders(pageCurrent);
      console.log(res.data);
      if (res.success) {
        const arrRevese = res.data;
        if (res.total % 6 !== 0) {
          setTotalPage(Math.floor(res.total / 6) + 1);
        } else {
          setTotalPage(res.total / 6);
        }
        setArrayOrders(arrRevese);
        setLoading(false);
      } else {
        setTotalPage(1);
        setArrayOrders([]);
        setLoading(false);
      }
    };

    run();
  }, [pageCurrent, window.location.pathname]);
  const handleExportOrders = async () => {
    const res = await exportOrders(form);
    const url = window.URL.createObjectURL(new Blob([res.data]));
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', 'orders.xlsx');
    document.body.appendChild(link);
    link.click();
    link.parentNode.removeChild(link);
  };

  return (
    <AdminLayout>
      <div className={style.boxHeader}>
        <div className={style.formExport}>
          <label htmlFor="from">From</label>
          <DatePicker selected={startDate} onChange={setFrom} />
        </div>
        <div className={style.formExport}>
          <label htmlFor="to">To</label>
          <DatePicker selected={endDate} onChange={setTo} />
        </div>
        <Button variant="outlined" onClick={handleExportOrders}>
          Export
        </Button>
      </div>

      {loading && <Spinner />}
      <TableContainer className={classes.tableOrder} component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell width="20%">H tn</TableCell>
              <TableCell width="20%" align="right">
                S in thoi
              </TableCell>
              <TableCell width="20%" align="right">
                Email
              </TableCell>
              <TableCell width="30%" align="right">
                Dch v
              </TableCell>
              <TableCell width="10%" align="right">
                im i
              </TableCell>
              <TableCell width="10%" align="right">
                im n
              </TableCell>
              <TableCell width="5%" align="right">
                Trng lng (Kg)
              </TableCell>
              <TableCell width="5%" align="right">
                Ghi ch
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {arrayOrders?.map((item, index) => (
              <TableRow
                key={index}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              >
                <TableCell component="th" scope="row">
                  {item.name}
                </TableCell>
                <TableCell align="right">{item.phone}</TableCell>
                <TableCell align="right">{item.email}</TableCell>
                <TableCell align="right">
                  {ARRAY_SERVICES[item.service]}
                </TableCell>
                <TableCell align="right">{item.from}</TableCell>
                <TableCell align="right">{item.to}</TableCell>
                <TableCell align="right">{item.weight}</TableCell>
                <TableCell align="right">{item.other}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <AppPagination totalPage={totalPage} onChange={handleChangePage} />
    </AdminLayout>
  );
};
const ARRAY_SERVICES = {
  0: 'Vn ti ng b',
  1: 'Vn ti ng st',
  2: 'Vn ti ng bin',
  3: 'Vn ti ng hng khng',
  4: 'Th tc hi quan',
  5: 'Kho bi v lu tr hng ha',
  6: 'ng gi v chia chn',
  7: 'Giao hng dim cui',
};
export default Order;
