import { makeStyles } from '@material-ui/core';
import { AppConstant } from 'const';

export const useStyles = makeStyles((theme) => ({
  root: {
    top: 0,
    backgroundColor: theme.palette.common.white,
    width: '100vw',
  },
  img: {
    maxWidth: '85%',
    maxHeight: '85%',
  },
  content: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    height: '100%',
  },
  contentLeft: {
    height: '100%',
  },

  textHeader: {
    fontWeight: 400,
    fontSize: '1.1rem',
    height: 40,
    '& .btnHeader': {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      height: '100%',
      padding: '0 10px',
      margin: '0 10px',
      borderRadius: '10px',
    },
    '& a': {
      fontFamily: "'beausanto', sans-serif !important",
      fontSize: '0.9rem',
    },
    '& .headerBar': {
      display: 'block',
      position: 'absolute',
      transform: 'scaleY(0)',
      transformOrigin: '0 0',
      transition: 'transform 0.3s',
    },
    '&:hover': {
      '& .btnHeader': {
        fontWeight: 600,
        // backgroundColor: theme.palette.grey[100],
        cursor: 'pointer',
        '& a': {
          borderBottom: '3px solid ' + theme.palette.primary.main,
        },
      },
      '& .headerBar': {
        transform: 'scaleY(1)',
      },
    },
  },
  btnLogin: {
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  button: {
    height: 40,
    borderRadius: '10px 10px 10px 0px',
    fontSize: '1rem',
    '& a': {
      fontWeight: 800,
      color: '#FFFFFF',
    },
  },
  rightButton: {
    marginRight: 10,
    marginLeft: 20,
  },
  flagIcon: {
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    paddingLeft: '10px',
    width: '40px',
  },
  flagIconBtn: {
    border: 'none',
    background: 'none',
    cursor: 'pointer',
    '& svg': {
      position: 'relative',
      bottom: '-2px',
    },
  },
  '@media screen and (max-width: 850px)': {
    hideOnMobie: {
      display: 'none',
    },
    textHeader: {
      display: 'none',
    },
    content: {
      width: '100%',
    },
    rightButton: {
      margin: '0 10px 0 0',
    },
    menuMobile: {
      height: '45px',
      width: '45px',
      display: 'flex',
      '&>img': {
        width: '60%',
        margin: 'auto',
        opacity: '0.7',
      },
    },
    contentRight: {
      width: '60%',
    },
    contentLeft: {
      width: '25%',
    },
    'flex-center': {
      justifyContent: 'space-around',
    },
    button: {
      width: '45%',
      height: '35px',
      padding: '0',
      '& > span': {
        fontSize: '0.7rem',
      },
    },
    btnLogin: {
      width: '60%',
    },
    flagIcon: {
      width: '20%',
      padding: '0 10px',
    },
  },
  '@media screen and (min-width: 851px)': {
    hideOnPC: {
      display: 'none',
    },
  },
}));
