import React, { useState, useEffect } from 'react';
import {
  Box,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Paper,
  Select,
  FormControl,
  MenuItem,
  InputLabel,
} from '@material-ui/core';
import {
  AppPagination,
  ModalCreateNews,
  Spinner,
  ModalEditNews,
} from 'components';

import { Delete, Edit } from '@material-ui/icons';
import { useStyles } from './styles';
import { AdminLayout } from 'layouts';
import style from './News.module.css';
import { useGlobalContext } from 'store';
const ManageNews = () => {
  const classes = useStyles();
  const { getNews, deleteNews, editCategory, getCategorys } =
    useGlobalContext();
  const [arrayNews, setArrayNews] = useState();
  const [loading, setLoading] = useState(false);
  const [totalPage, setTotalPage] = useState();
  const [pageCurrent, setPageCurrent] = useState(1);
  const [addNews, setAddNews] = useState(false);
  const [start, setStart] = useState(false);
  const [categorys, setCategorys] = useState();
  const [status, setStatus] = useState(null);
  const [editNews, setEditNews] = useState(false);
  const [deleteNews1, setDeleteNews1] = useState(false);
  const [type, setType] = useState();
  const LanguageType = {
    VI: 'vi',
    EN: 'en',
  };
  const [lang, setLang] = useState(LanguageType.VI);

  const handleOpen = () => setAddNews(true);
  useEffect(() => {
    const run = async () => {
      setLoading(true);
      const res = await getNews(type, pageCurrent, 6, lang);
      if (res.success) {
        setArrayNews(res.data);
        setTotalPage(() => {
          if (res.total % 6 !== 0) {
            return Math.floor(res.total / 6) + 1;
          } else {
            return res.total / 6;
          }
        });
        setLoading(false);
      } else {
        setArrayNews([]);
        setTotalPage(1);
        setLoading(false);
      }
    };
    run();
  }, [type, deleteNews1, start, lang]);
  useEffect(() => {
    setLoading(true);
    const run = async () => {
      const res = await getCategorys(lang);

      setCategorys(res.data);
    };
    run();
  }, [lang]);

  const handleChangePage = (page) => {
    setPageCurrent(page);
  };
  const handleChangType = async (e) => {
    if (e.target.value !== '') {
      const categoryCurrent = categorys.find((c) => c.id == e.target.value);

      if (categoryCurrent.is_news === true) {
        setStatus(true);
      } else {
        setStatus(false);
      }
      setType(e.target.value);
    } else {
      setType('');
    }
  };
  const handleDeleteNews = async (id) => {
    setLoading(true);
    const res = await deleteNews(id);
    if (res.success) {
      setDeleteNews1(!deleteNews1);
    }
  };
  const addNewToCategory = async (type) => {
    const res = await editCategory(type, { is_news: true });
    if (res.success) {
      const categoryCurrent = categorys.find((c) => c.id == type);
      categoryCurrent.is_news = true;
    }
  };
  const removeNewCategory = async (type) => {
    const res = await editCategory(type, { is_news: false });
    if (res.success) {
      const categoryCurrent = categorys.find((c) => c.id == type);
      categoryCurrent.is_news = false;
    }
  };

  const getCategory = (id) => {
    const category = categorys.find((c) => c.id === id);
    return category.name;
  };

  return (
    <AdminLayout>
      {loading && <Spinner />}
      {addNews && (
        <ModalCreateNews
          setStart={setStart}
          start={start}
          setAddNews={setAddNews}
          categorys={categorys}
        />
      )}
      {editNews && (
        <ModalEditNews
          setStart={setStart}
          start={start}
          editNews={editNews}
          setEditNews={setEditNews}
          categorys={categorys}
        />
      )}
      <Box className={classes.add}>
        <Button variant="outlined" onClick={handleOpen}>
          Thm mi
        </Button>
        <select
          name=""
          id=""
          className={style.selectType}
          onChange={handleChangType}
        >
          <option value={''}>--Chn loi bi vit--</option>
          {categorys?.map((c, i) => (
            <option key={i} value={c.id}>
              {c.name}
            </option>
          ))}
        </select>
        <div className={style.btnStatus}>
          <Button
            variant="outlined"
            onClick={() => {
              addNewToCategory(type);
              setStatus(true);
            }}
            disabled={status && true}
          >
            NEW
          </Button>
          <Button
            variant="outlined"
            onClick={() => {
              removeNewCategory(type);
              setStatus(false);
            }}
            disabled={!status && true}
          >
            OLD
          </Button>
        </div>
        <FormControl style={{ marginLeft: 'auto' }}>
          <InputLabel id="demo-simple-select-label">Language</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={lang}
            onChange={(event) => setLang(event.target.value)}
          >
            <MenuItem value={LanguageType.VI}>Vietnamese</MenuItem>
            <MenuItem value={LanguageType.EN}>EngLish</MenuItem>
          </Select>
        </FormControl>
      </Box>
      <TableContainer className={classes.tableCooperate} component={Paper}>
        <Table aria-label="caption table">
          <TableHead>
            <TableRow>
              <TableCell align="left" width="35%">
                Tn bi vit
              </TableCell>
              <TableCell align="left" width="35%">
                Loi bi vit
              </TableCell>

              <TableCell align="left" width="30%">
                Thao tc
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {categorys &&
              arrayNews?.map((item, index) => (
                <TableRow
                  key={index}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                  <TableCell
                    width="35%"
                    align="left"
                    component="th"
                    scope="row"
                  >
                    {item.title}
                  </TableCell>
                  <TableCell width="35%" align="left">
                    {getCategory(item.newsCatogryId)}
                  </TableCell>

                  <TableCell width="30%" align="left">
                    <IconButton
                      aria-label="delete"
                      onClick={() => {
                        setEditNews(item);
                      }}
                    >
                      <Edit />
                    </IconButton>
                    <IconButton
                      aria-label="delete"
                      onClick={() => {
                        handleDeleteNews(item.id);
                      }}
                    >
                      <Delete />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </TableContainer>
      <AppPagination totalPage={totalPage} onChange={handleChangePage} />
    </AdminLayout>
  );
};

export default ManageNews;
