import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core';
import CloseOutlinedIcon from '@material-ui/icons/CloseOutlined';
import PropTypes from 'prop-types';

const Image = ({ src, data_src, className, cover, coverImageHistory }) => {
  const classes = useStyles({ coverImageHistory });
  // const [loaded, setLoaded] = React.useState(false);

  // useEffect(() => {
  //   setLoaded(false);
  // }, [src]);

  // const handleImageLoad = (e) => {
  //   if (e.target.dataset.src) {
  //     var image = e.target;
  //     image.src = e.target.dataset.src;
  //     image.removeAttribute('data-src');
  //   } else {
  //     setLoaded(true);
  //   }
  // };
  return (
    <img
      src={src}
      // data-src={data_src}
      alt="..."
      className={clsx(
        classes.image,
        className,
        cover ? classes.imageCover : ''
        // loaded ? '' : classes.imageFilter
      )}
      // onLoad={handleImageLoad}
    />
  );
};

export default Image;

const useStyles = makeStyles((theme) => ({
  image: {
    height: '100%',
    minWidth: '100%',
    maxWidth: '100%',
    transition: 'opacity .4s ease-in',
  },
  imageCover: {
    objectFit: 'cover',
  },
  imageFilter: {
    opacity: '0.6',
  },
  '@media screen and (max-width: 850px)': {
    image: {
      objectFit: ({ coverImageHistory }) =>
        coverImageHistory ? 'cover' : 'contain',
    },
  },
}));
