import React, { useMemo, useState, useEffect } from 'react';
import './news.css';
import { HomeLayout } from 'layouts';
import { SliderNews } from '../../components/slider/Slider';
import { Link, useHistory } from 'react-router-dom';
import slugify from 'utils/slugify';
import { useTranslation } from 'react-i18next';
import { LangConstant } from 'const';
import { Pagination } from '@material-ui/lab';
import { useGlobalContext } from 'store';

import {
  NewsBody,
  NewsSideBarForm,
  NewsSideBarMenu,
  Spinner,
} from 'components';

const News = (props) => {
  const {
    getTypeNews,
    getNews,
    getAllPosts,
    getCategorys,
    getDataConfig,
    PAGE_KEY,
  } = useGlobalContext();
  const { t: getLabel, i18n } = useTranslation();
  const [pageCurrent, setPageCurrent] = useState(1);
  const [newsCurrent, setNewsCurrent] = useState();
  const [totalPage, setTotalPage] = useState();
  const [dataSlider, setDataSlider] = useState();
  const [categorys, setCategorys] = useState();
  const [loading, setLoading] = useState(false);
  const [imgNews, setImgNews] = useState(false);
  const history = useHistory();

  const search = props.location.search;
  const typeNews = useMemo(() => new URLSearchParams(search), [search]);

  const getPostsAll = async (page, lang) => {
    const res = await getTypeNews(page, lang);
    return res;
  };
  const getPostsType = async (type, page, lang) => {
    const res = await getNews(type, page, 2, lang);

    return res;
  };

  const handleChangePage = (event, page) => {
    setPageCurrent(page);
  };

  const handleChangeTypePost = (e) => {
    history.push(`/news/?category=${e.target.value}`);
  };
  const getTime = (time) => {
    const date = new Date(time);
    return `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`;
  };
  const getNameCategory = (id) => {
    const category = categorys.find((c) => c.id === id);
    return category?.name;
  };
  const [banner, setBanner] = useState({
    topLeft: false,
    bottomLeft: false,
    right: false,
  });
  const getIdCategory = (nameSlugify) => {
    const category = categorys.find((c) => slugify(c.name) === nameSlugify);
    if (category) return category.id;
  };
  useEffect(() => {
    const run = async () => {
      setLoading(true);
      const res = await getDataConfig(PAGE_KEY.news, i18n.language);
      if (res.success) {
        setBanner({
          topLeft: res.data[0].value.is_show_flash_left_top,
          bottomLeft: res.data[0].value.is_show_flash_left_bottom,
          right: res.data[0].value.is_show_flash_right,
        });
        setImgNews(res.data[0].value);
      }
      setLoading(false);
    };
    run();
  }, [i18n.language]);
  useEffect(() => {
    const run = async () => {
      const res = await getCategorys(i18n.language);
      if (res.success) {
        setCategorys(res.data);
      } else {
        setCategorys([]);
      }
    };
    run();
  }, [i18n.language]);
  useEffect(() => {
    const getDateSlider = async () => {
      setLoading(true);
      const res = await getAllPosts(i18n.language);
      if (res.success) {
        setDataSlider(
          res.data.filter((p) => {
            if (p.is_highlight) {
              return p.is_highlight;
            }
          })
        );
      } else {
        setDataSlider([]);
      }
      setLoading(false);
    };

    getDateSlider();
  }, [i18n.language]);

  useEffect(() => {
    const run = async () => {
      if (categorys) {
        setLoading(true);
        let res;
        if (typeNews.get('category') !== null) {
          res = await getPostsType(
            getIdCategory(typeNews.get('category')),
            pageCurrent,
            i18n.language
          );
        } else {
          res = await getPostsAll(pageCurrent, i18n.language);
        }
        if (res.total % 2 === 0) {
          let a = res.total / 2;
          if (a === 2) {
            setTotalPage(a + 1);
          } else {
            setTotalPage(a);
          }
        } else {
          let b = Math.floor(res.total / 2) + 1;
          if (b === 2) {
            setTotalPage(b + 1);
          } else {
            setTotalPage(b);
          }
        }
        setNewsCurrent(res.data);
        setLoading(false);
      }
    };

    run();
  }, [search, pageCurrent, categorys, i18n.language]);
  const { topLeft, bottomLeft, right } = banner;
  return (
    <HomeLayout typeFooter="copyright">
      {loading && <Spinner />}
      <NewsBody
        banner={banner}
        topLeft={topLeft}
        bottomLeft={bottomLeft}
        right={right}
        setBanner={setBanner}
        imgNews={imgNews}
      >
        <div className="news-main fullheight">
          <div className="news-main-title">
            <h1>{getLabel(LangConstant.TXT_FEATURED_NEWS)}</h1>
          </div>
          <div className="news-main-slider">
            {categorys && dataSlider && (
              <SliderNews
                categorys={categorys}
                data={dataSlider}
                getTime={getTime}
              />
            )}
          </div>
          <div className="box-type-post showOnMobie">
            <select name="" id="type_post" onChange={handleChangeTypePost}>
              {!loading &&
                categorys?.map((c, i) => (
                  <option key={i} value={slugify(c.name)}>
                    {c.name}
                  </option>
                ))}
            </select>
          </div>
          <div className="news-main-box">
            {newsCurrent?.map((p, i) => (
              <Link
                className="news-main-item-link"
                key={i}
                to={`/news/${slugify(p.title)}.${p.id}`}
              >
                <div className="news-main-item">
                  <img
                    src={p.hightlightImage}
                    alt=""
                    className="news-main-item-img"
                  />
                  <div className="news-main-item-content">
                    <h1 className="news-main-item-type">
                      {getNameCategory(p.newsCatogryId)}
                    </h1>
                    <h2 className="news-main-item-title ellipsis">{p.title}</h2>
                    <p className="news-main-item-description ellipsis">
                      {p.description}
                    </p>
                    <div className="news-main-item-timeUpload">
                      <img src="/news/clock.png" alt="" />
                      <span>{getTime(p.createdAt)}</span>
                    </div>
                  </div>
                </div>
              </Link>
            ))}
          </div>

          <div className="news-main-pagination">
            <span>{getLabel(LangConstant.TXT_PAGES_NEWS)}</span>

            <Pagination
              color="primary"
              variant="outlined"
              count={totalPage}
              defaultPage={pageCurrent}
              shape="rounded"
              onChange={handleChangePage}
              showFirstButton
              showLastButton
            />
          </div>
        </div>
        <div className="news-sidebar fullheight">
          <div className="news-sidebar-space"></div>
          {categorys && <NewsSideBarMenu categorys={categorys} />}
          <NewsSideBarForm getLabel={getLabel} LangConstant={LangConstant} />
        </div>
      </NewsBody>
    </HomeLayout>
  );
};

export default News;
