import React, { useEffect, useState } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
} from '@material-ui/core';
import { Pagination } from '@material-ui/lab';
import { useStyles } from './style';
import { AdminLayout } from 'layouts';
import { useGlobalContext } from 'store';
import { Spinner } from 'components';
import style from '../customer/Customer.module.css';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

const NotifyCation = () => {
  const classes = useStyles();
  const { getUserInfomation, exportInfoUser } = useGlobalContext();
  const [noteArray, setNoteArray] = useState([]);
  const [loading, setLoading] = useState(false);
  const [totalPage, setTotalPage] = useState();
  const [pageCurrent, setPageCurrent] = useState(1);
  const [form, setForm] = useState({
    from: '',
    to: '',
  });
  const getTime = (e) => {
    const date = new Date(e);
    const day = date.getDate();
    const month = date.getMonth() + 1;
    const year = date.getFullYear();
    return `${day}/${month}/${year}`;
  };
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const setFrom = (e) => {
    setStartDate(e);
    setForm({ ...form, from: getTime(e) });
  };
  const setTo = (e) => {
    setEndDate(e);
    setForm({ ...form, to: getTime(e) });
  };
  useEffect(() => {
    const run = async () => {
      setLoading(true);
      const res = await getUserInfomation(pageCurrent, 8);
      if (res.success) {
        if (res.total % 8 !== 0) {
          setTotalPage(Math.floor(res.total / 8) + 1);
        } else {
          setTotalPage(res.total / 8);
        }
        setNoteArray(res.data);
        setLoading(false);
      } else {
        setNoteArray([]);
        setLoading(false);
      }
    };
    run();
  }, [pageCurrent]);
  const handeChangePage = (i, page) => {
    setPageCurrent(page);
  };
  const handleExportInfoUser = async () => {
    const res = await exportInfoUser(form);
    const url = window.URL.createObjectURL(new Blob([res.data]));
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', 'infoUser.xlsx');
    document.body.appendChild(link);
    link.click();
    link.parentNode.removeChild(link);
  };
  return (
    <AdminLayout>
      <div className={style.boxHeader}>
        <div className={style.formExport}>
          <label htmlFor="from">From</label>
          <DatePicker selected={startDate} onChange={setFrom} />
        </div>
        <div className={style.formExport}>
          <label htmlFor="to">To</label>
          <DatePicker selected={endDate} onChange={setTo} />
        </div>
        <Button variant="outlined" onClick={handleExportInfoUser}>
          Export
        </Button>
      </div>
      {loading && <Spinner />}
      <TableContainer className={classes.tableNote} component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell width="30%" align="left">
                H tn
              </TableCell>
              <TableCell width="70%" align="left">
                Email
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {noteArray?.map((item, index) => (
              <TableRow
                key={index}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              >
                <TableCell width="30%" component="th" scope="row">
                  {item.name}
                </TableCell>
                <TableCell width="70%" align="left">
                  {item.email}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>

        <Pagination
          count={totalPage}
          color="primary"
          className={classes.pagination}
          onChange={handeChangePage}
        />
      </TableContainer>
    </AdminLayout>
  );
};

export default NotifyCation;
