import React, { useEffect, useRef, useState } from 'react';
import { IntroLayout } from 'layouts';
import { useStyles } from './styles';
import { HtmlRender, ImageContainer, Spinner } from 'components';
import clsx from 'clsx';
import { useTranslation } from 'react-i18next';
// import { getHistoryContent } from 'api/intro';
import { useGlobalContext } from 'store';

const History = () => {
  const classes = useStyles();
  const { i18n } = useTranslation();
  const contentRef = useRef();
  const [loading, setLoading] = useState(false);
  const { getDataConfig, PAGE_KEY } = useGlobalContext();
  const [data, setData] = useState({});
  useEffect(() => {
    const getData = async () => {
      setLoading(true);
      const res = await getDataConfig(PAGE_KEY.history, i18n.language);
      contentRef.current = `<p>${res.data[0].value.text1}</p><p>${res.data[0].value.text2}</p>`;
      // const dataRes = await getHistoryContent(i18n.language);
      setData(res.data[0].value);
      setLoading(false);
    };
    getData();
  }, [i18n.language]);

  const header = (
    <ImageContainer
      className={clsx(classes.image, 'history-banner-margin')}
      src={data.imgCover}
      alt="..."
    ></ImageContainer>
  );

  const content = (
    <div className="padding-right flex-content fullheight fullwidth">
      <HtmlRender
        pathname={'history'}
        html={contentRef.current}
        className={'history-padding-content'}
      />
      <h3 className="history-content-title color-main align-center">
        {data.footer}
      </h3>
    </div>
  );

  return (
    <>
      {loading ? (
        <Spinner />
      ) : (
        <IntroLayout
          background_url={data.imgBackGround}
          // background_url_thumbnail={data.background_url_thumbnail}
          content={content}
          header={header}
          history
        ></IntroLayout>
      )}
    </>
  );
};

export default History;
