import React from 'react';
import PropTypes from 'prop-types';
import { BaseLayout } from 'layouts';
import { useStyles } from './styles';
import { Menu } from 'components';
import clsx from 'clsx';

const IntroLayout = ({
  header,
  pathname,
  content,
  background_url,
  headerHeight,
  contentHeight,
  contentClassName,
  history,
}) => {
  const classes = useStyles({ background_url, headerHeight, contentHeight });

  return (
    <BaseLayout background_url={background_url} coverImageHistory history>
      <div
        className={clsx(classes.header, 'padding-header', {
          [classes.headerOutStanding]: pathname === 'outstanding',
          [classes.headerHistory]: history,
        })}
      >
        {header}
      </div>
      <div
        className={clsx(
          classes.contentBox,
          contentClassName,
          'max-height-content',
          'padding-content'
        )}
      >
        <div
          className={clsx(classes.content, {
            [classes.contentOutstanding]: pathname === 'outstanding',
          })}
        >
          {content}
        </div>
        <div className={classes.menu}>
          <Menu></Menu>
        </div>
      </div>
    </BaseLayout>
  );
};

IntroLayout.propTypes = {
  header: PropTypes.node,
  content: PropTypes.node,
  background_url: PropTypes.string,
  headerHeight: PropTypes.string,
  contentHeight: PropTypes.string,
  contentClassName: PropTypes.string,
};

export default IntroLayout;
