import React, { useState } from 'react';
import {
  makeStyles,
  Typography,
  Paper,
  InputBase,
  IconButton,
} from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search';
import { useTranslation } from 'react-i18next';
import { useGlobalContext } from 'store';
import { LangConstant } from 'const';
import { Snipper } from 'components';

const Body = ({ type, dataImg }) => {
  const { i18n, t: getLabel } = useTranslation();
  const [loading, setLoading] = useState(true);
  const [searchValue, setSearchValue] = useState('');
  const [data, setData] = useState([]);
  const [searchData, setSearchData] = React.useState([]);
  const classes = useStyles();
  const { getSearchUnit } = useGlobalContext();

  const run = async () => {
    setLoading(true);
    const resData = await getSearchUnit(type, i18n.language);
    setData(resData.data);
    setSearchData(resData.data);
    setLoading(false);
  };

  React.useEffect(() => {
    run();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [type, i18n.language]);

  const handleChangeValue = (event) => {
    setSearchValue(event.target.value);
    const _searchData = data.filter((x) =>
      x.address.toUpperCase().includes(event.target.value.toUpperCase())
    );
    setSearchData(_searchData);
  };

  return (
    <div className={classes.root}>
      <div className={classes.rootBody}>
        {loading && <Snipper className={classes.loading} />}
        <Paper component="form" className="flex-between">
          <InputBase
            fullWidth
            placeholder={getLabel(LangConstant.TXT_SEARCH)}
            inputProps={{ 'aria-label': 'search' }}
            value={searchValue}
            onChange={handleChangeValue}
            style={{ padding: '0 10px' }}
          />
          <IconButton aria-label="search">
            <SearchIcon />
          </IconButton>
        </Paper>
        <div className={classes.boxLocation}>
          <div className={classes.boxLocationText}>
            {searchData.map((item, index) => (
              <div key={index} className={classes.LocationItem}>
                <Typography className={classes.LocationText}>
                  {item.name}
                </Typography>
                <Typography className={classes.LocationDetail}>
                  {item.address}
                </Typography>
              </div>
            ))}
          </div>
          <div className={classes.boxImageSearch}>
            <img src={dataImg} className={classes.imgSearch} alt="..." />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Body;

const useStyles = makeStyles((theme) => ({
  LocationItem: {
    height: 'auto',
    display: 'flex',
    borderBottom: '1px solid silver',
    flexDirection: 'column',
    justifyContent: 'center',
    padding: '10px 0',
  },
  boxImageSearch: {
    width: '65%',
    height: '100%',
    display: 'flex',
    alignItems: 'flex-end',
    paddingBottom: 24,
  },
  LocationText: {
    fontSize: 16,
    fontWeight: 'bold',
    textTransform: 'uppercase',
  },
  LocationDetail: { fontSize: 16 },
  boxLocationText: {
    width: '35%',
    height: 'auto',
    display: 'block',
    overflow: 'auto',
    paddingRight: '5%',
    flexDirection: 'column',
    marginBottom: '24px',
    marginRight: '5px',
    '&::-webkit-scrollbar': {
      width: '10px',
    },
    '&::-webkit-scrollbar-track': {
      boxShadow: 'inset 0 0 5px grey',
      borderRadius: '10px',
    },
    '&::-webkit-scrollbar-thumb': {
      background: 'red',
      borderRadius: '10px',
    },
    '&::-webkit-scrollbar-thumb:hover': {
      background: '#b30000',
    },
  },
  boxLocation: {
    width: '100%',
    display: 'flex',
    paddingTop: '16px',
    height: 'calc(100% - 40px)',
  },

  root: {
    width: '100%',
    padding: '16px 0 0',
    height: '90%',
  },
  rootBody: {
    width: '100%',
    height: '100%',
    padding: '16px',
    border: '1px solid ' + theme.palette.grey[300],
    backgroundColor: theme.palette.common.white,
    position: 'relative',
  },
  loading: {
    margin: '-16px',
  },
  imgSearch: {
    width: '100%',
    height: '100%',
    objectFit: 'cover',
  },
  '@media screen and (max-height:700px)': {
    LocationDetail: {
      fontSize: '0.8rem',
    },
    LocationText: {
      fontSize: '0.8rem',
    },
  },
  '@media screen and (max-width: 850px)': {
    boxLocation: { flexDirection: 'column' },
    boxLocationText: {
      width: '100%',
      paddingRight: '0',
    },
    LocationItem: {
      marginBottom: '16px',
      height: '80px',
      justifyContent: 'space-evenly',
    },
    boxImageSearch: {
      width: '100%',
      maxWidth: '100%',
    },
    searchInput: {
      width: '100%',
    },
  },
}));
