import React, { useRef, useEffect } from 'react';
import style from './TextEditorAdmin.module.css';
import { useGlobalContext } from 'store';

const TextEditorAdmin = ({ input, onChangeInputData }) => {
  const { uploadImg } = useGlobalContext();
  const inputRef = useRef();
  const inputImgDataT = useRef();
  useEffect(() => {
    const handleClickButtonControl = (e) => {
      inputRef.current.focus();
      const cmd = e.dataset.cmd;
      document.execCommand(cmd, false, null);
    };
    const buttonControls = document.querySelectorAll('[data-cmd]');
    buttonControls.forEach((i) => {
      i.addEventListener('click', () => handleClickButtonControl(i));
    });
  }, []);
  const handleInput = (e) => {
    onChangeInputData && onChangeInputData(e.target.innerHTML);
  };
  const handleChangeColor = (e) => {
    inputRef.current.focus();
    document.execCommand('foreColor', false, e.target.value);
  };
  const handleUploadImg = async (e) => {
    const form = new FormData();
    form.append('file', e.target.files[0]);
    form.append('type', 10);
    const res = await uploadImg(form);
    if (res.success) {
      const htmlImg = `<img style="margin: 0 auto;display: block; width:100%;object-fit: contain" alt="..." src="${res.url}">`;
      inputRef.current.focus();
      document.execCommand('insertHTML', false, htmlImg);
    } else {
      alert('upload failed!');
    }
  };
  return (
    <>
      <div className={style.textEditWrap}>
        <div className={style.iconControl}>
          <div data-cmd="bold" className={style.iconControlItem}>
            <img src="/feedback/bold.png"></img>
          </div>
          <div data-cmd="italic" className={style.iconControlItem}>
            <img src="/feedback/italics.png"></img>
          </div>
          <div data-cmd="underline" className={style.iconControlItem}>
            <img src="/feedback/underline.png"></img>
          </div>
          <div data-cmd="justifyLeft" className={style.iconControlItem}>
            <img src="/feedback/align-left.png"></img>
          </div>
          <div data-cmd="justifyCenter" className={style.iconControlItem}>
            <img src="/feedback/align-center.png"></img>
          </div>
          <div data-cmd="justifyRight" className={style.iconControlItem}>
            <img src="/feedback/align-right.png"></img>
          </div>
          <div data-cmd="justifyFull" className={style.iconControlItem}>
            <img src="/feedback/justify.png"></img>
          </div>
          <div className={style.iconControlItem}>
            <input type="color" onChange={handleChangeColor} />
          </div>
          <div
            className={style.iconControlItem}
            onClick={() => inputImgDataT.current.click()}
          >
            <img src="/feedback/photo.png"></img>
            <input
              type="file"
              hidden
              accept="image/*"
              onChange={handleUploadImg}
              ref={inputImgDataT}
            />
          </div>
        </div>
        <div
          className={style.boxInput}
          onInput={handleInput}
          ref={inputRef}
          contentEditable={true}
          dangerouslySetInnerHTML={{ __html: input }}
          suppressContentEditableWarning={true}
        ></div>
      </div>
    </>
  );
};

export default TextEditorAdmin;
