import React, { useState, useEffect, useRef } from 'react';
import { Button } from '@material-ui/core';
import StringFormat from 'string-format';
import clsx from 'clsx';
import { PathConstant } from 'const';
import { HeaderBar, HeaderMenu, MenuMobile } from 'components';
import { ARRAY_SERVICES } from 'pages/service';
import { useStyles } from './styles';
import { Link } from 'react-router-dom';
import { IconFlagVn } from 'icons';
import { IconFlagUK } from 'material-ui-flags';
import { useTranslation } from 'react-i18next';
import { LangConstant } from 'const';
import menuMobile from './menuMobile.png';
import { useGlobalContext } from 'store';
import style from './HeaderAnimation.module.css';

const Header = () => {
  const classes = useStyles({ height: HEADER_HEIGHT });
  const headerRef = useRef();
  // const [lang, setLang] = useState();
  const [menu, setMenu] = useState(false);
  const { lang, setLang } = useGlobalContext();

  const { t: getLabel, i18n } = useTranslation();
  const changeIcon = () => {
    if (lang == 1) {
      i18n.changeLanguage('en');
      localStorage.setItem('lang', JSON.stringify(lang));
      setLang(0);
    } else {
      i18n.changeLanguage('vi');
      localStorage.setItem('lang', JSON.stringify(lang));
      setLang(1);
    }
  };
  useEffect(() => {
    if (
      window.location.pathname === '/intro/open-letter' ||
      window.location.pathname === '/service' ||
      window.location.pathname === '/' ||
      window.location.pathname === '/frequently-question' ||
      (window.location.pathname === '/news' && !window.location.search)
    ) {
      headerRef.current.classList.add(`${style.header}`);
    }
  }, [window.location.pathname]);
  // useEffect(() => {
  //   const currentLang = JSON.parse(localStorage.getItem('lang')) || 0;
  //   if (currentLang == 1) {
  //     setLang(0);
  //     i18n.changeLanguage('en');
  //   } else {
  //     setLang(1);
  //     i18n.changeLanguage('vi');
  //   }
  // }, []);

  return (
    <header ref={headerRef} className={clsx(classes.root, 'header')}>
      <div className={clsx(classes.content, 'root-body')}>
        <Link
          to={PathConstant.ROOT}
          className={clsx('center-root', classes.contentLeft)}
        >
          <img
            className={classes.img}
            src={window.location.origin + '/logo_app.png'}
            alt="..."
          ></img>
        </Link>
        <div className={clsx(classes.contentRight, 'flex-center')}>
          <div className={classes.textHeader}>
            <div className="btnHeader">
              <Link to={PathConstant.OPENLETTER}>
                {getLabel(LangConstant.TXT_HEADER_INTRO)}
              </Link>
            </div>
            <HeaderMenu
              className={'headerBar'}
              listItem={ARRAY_INTRO}
            ></HeaderMenu>
          </div>

          <div className={classes.textHeader}>
            <div className="btnHeader">
              <Link to={PathConstant.SERVICE}>
                {getLabel(LangConstant.TXT_HEADER_SERVICE)}
              </Link>
            </div>

            <HeaderMenu
              className={'headerBar'}
              listItem={ARRAY_SERVICES.map((item, index) => ({
                ...item,
                href: StringFormat(PathConstant.SERVICE_ID, index),
              }))}
              linkServices
            ></HeaderMenu>
          </div>

          <div className={classes.textHeader}>
            <div className="btnHeader">
              <Link to={PathConstant.FREQUENTLY_QUESTION}>
                {getLabel(LangConstant.TXT_HEADER_SUPPORT)}
              </Link>
            </div>
            <HeaderMenu
              className={'headerBar'}
              listItem={ARRAY_SUPPORT_CUSTOMER}
            ></HeaderMenu>
          </div>

          <div className={classes.textHeader}>
            <div className="btnHeader">
              <Link to={PathConstant.NEWS}>
                {getLabel(LangConstant.TXT_HEADER_NEWS)}
              </Link>
            </div>
            <HeaderMenu
              className={'headerBar'}
              listItem={ARRAY_NEWS}
            ></HeaderMenu>
          </div>
          <div className={classes.btnLogin}>
            <Button
              className={clsx(
                classes.button,
                'header-button-width',
                classes.rightButton
              )}
              variant="contained"
              color="primary"
              size="small"
              onClick={() => {
                window.location.href =
                  'https://id.viettelpost.vn/Account/Login?ReturnUrl=%2Fconnect%2Fauthorize%2Fcallback%3Fclient_id%3Dvtp.web%26secret%3Dvtp-web%26scope%3Dopenid%2520profile%2520se-public-api%2520offline_access%26response_type%3Did_token%2520token%26state%3Dabc%26redirect_uri%3Dhttps%253A%252F%252Fviettelpost.vn%252Fstart%252Flogin%26nonce%3D8e3yx8t9kuq9uv8p3bbl2i';
              }}
            >
              {/* <span className="centerBtnHeader">
              </span> */}
              {getLabel(LangConstant.TXT_HEADER_LOGIN)}
            </Button>

            <Button
              className={clsx(classes.button, 'header-button-width')}
              variant="contained"
              color="primary"
              size="small"
              onClick={() => {
                window.location.href =
                  'https://id.viettelpost.vn/Account/Register?returnUrl=%2Fconnect%2Fauthorize%2Fcallback%3Fclient_id%3Dvtp.web%26secret%3Dvtp-web%26scope%3Dopenid%2520profile%2520se-public-api%2520offline_access%26response_type%3Did_token%2520token%26state%3Dabc%26redirect_uri%3Dhttps%253A%252F%252Fviettelpost.vn%252Fstart%252Flogin%26nonce%3D8e3yx8t9kuq9uv8p3bbl2i';
              }}
            >
              {/* <span className="centerBtnHeader">
              </span> */}
              {getLabel(LangConstant.TXT_HEADER_REGISTER)}
            </Button>
          </div>
          <div className={classes.flagIcon}>
            <button className={classes.flagIconBtn} onClick={changeIcon}>
              {icons[lang]?.icon}
              <div>{icons[lang]?.text}</div>
            </button>
          </div>
          <div
            className={clsx(classes.menuMobile, classes.hideOnPC)}
            onClick={() => setMenu(true)}
          >
            <img src={menuMobile} alt="menu" />
          </div>
          {menu && (
            <MenuMobile
              ARRAY_INTRO={ARRAY_INTRO}
              ARRAY_SUPPORT_CUSTOMER={ARRAY_SUPPORT_CUSTOMER}
              ARRAY_NEWS={ARRAY_NEWS}
              setMenu={setMenu}
            />
          )}
        </div>
      </div>
    </header>
  );
};

export default Header;

export const HEADER_HEIGHT = 60;

const icons = [
  {
    icon: <IconFlagVn></IconFlagVn>,
    text: 'VN',
  },
  {
    icon: <IconFlagUK></IconFlagUK>,
    text: 'EN',
  },
];

const ARRAY_INTRO = [
  {
    name: LangConstant.TXT_HEADER_INTRO_OPENLETTER,
    href: PathConstant.OPENLETTER,
  },
  {
    name: LangConstant.TXT_HEADER_INTRO_HISTORY,
    href: PathConstant.HISTORY,
  },
  {
    name: LangConstant.TXT_HEADER_INTRO_OVERVIEW,
    href: PathConstant.OVERVIEW,
  },
  {
    name: LangConstant.TXT_HEADER_INTRO_OUTSTANDING,
    href: PathConstant.OUTSTANDING,
  },
  {
    name: LangConstant.TXT_HEADER_INTRO_PARTNERS,
    href: PathConstant.PARTNER,
  },
  {
    name: LangConstant.TXT_HEADER_INTRO_ACHIVEMENTS,
    href: PathConstant.ACHIEVEMENTS,
  },
  {
    name: LangConstant.TXT_HEADER_INTRO_PROFILE,
    href: PathConstant.PROFILE,
  },
];

const ARRAY_SUPPORT_CUSTOMER = [
  {
    name: LangConstant.TXT_HEADER_SUPPORT_QUESTION,
    href: PathConstant.FREQUENTLY_QUESTION,
  },
  {
    name: LangConstant.TXT_HEADER_SUPPORT_COMMENT,
    href: PathConstant.FEEDBACK,
  },
  {
    name: LangConstant.TXT_HEADER_SUPPORT_SEARCH,
    href: PathConstant.SEARCH,
  },
  {
    name: LangConstant.TXT_HEADER_SUPPORT_COOPERATE,
    href: PathConstant.COOPERATE,
  },
];

const ARRAY_NEWS = [
  {
    name: LangConstant.TXT_HEADER_NEWS_BUSINESS,
    href: '/news?category=thong-tin-doanh-nghiep',
  },
  {
    name: LangConstant.TXT_HEADER_NEWS_SPECIALIZED,
    href: '/news?category=thong-tin-chuyen-nganh',
  },
  {
    name: LangConstant.TXT_HEADER_NEWS_NOTIFY,
    href: '/news?category=thong-bao',
  },
  {
    name: LangConstant.TXT_HEADER_NEWS_BIDDING,
    href: '/news?category=tin-dau-thau',
  },
  {
    name: LangConstant.TXT_HEADER_NEWS_RECRUITMENT,
    href: '/news?category=tuyen-dung',
  },
  {
    name: LangConstant.TXT_HEADER_NEWS_PROMOTIONS,
    href: '/news?category=khuyen-mai',
  },
];
