import React from 'react';
import PropTypes from 'prop-types';
import { HomeLayout } from 'layouts';
import { useStyles } from './styles';
import { Menu } from 'components';
import clsx from 'clsx';

const TwoBackLayout = ({
  content,
  header,
  header_backgroud_url,
  content_background_url,
  partner,
}) => {
  const classes = useStyles({ header_backgroud_url, content_background_url });

  return (
    <HomeLayout typeFooter="copyright">
      <div className={classes.main}>
        <div
          className={clsx(classes.headerContainer, 'max-height-content', {
            [classes.headerPartnerContainer]: partner,
          })}
        >
          <div className={clsx(classes.header, 'root-body', 'padding')}>
            {header}
          </div>
        </div>
        <div className={classes.contentContainer}>
          <div
            className={clsx(classes.content, 'root-body', 'padding-content')}
          >
            <div className={clsx(classes.contentItem, 'padding-right')}>
              {content}
            </div>
            <div className={classes.menu}>
              <Menu />
            </div>
          </div>
        </div>
      </div>
    </HomeLayout>
  );
};

export default TwoBackLayout;
