import { makeStyles } from '@material-ui/core';
import { AppConstant } from 'const';

export const useStyles = makeStyles((theme) => ({
  header: {
    width: '100%',
    height: ({ headerHeight }) =>
      headerHeight ? headerHeight : AppConstant.introHeaderHeight,
    display: 'flex',
  },
  headerOutStanding: {
    width: '75%',
    paddingRight: 20,
  },
  contentBox: {
    width: '100%',
    height: ({ contentHeight }) =>
      contentHeight ? contentHeight : AppConstant.introContentHeight,
    display: 'flex',
  },
  content: {
    width: '75%',
    height: '100%',
    paddingRight: '20px',
    zIndex: '100',
  },
  menu: {
    width: '25%',
    zIndex: '100',
    height: '100%',
  },
  '@media screen and (min-width: 1350px) and (max-width: 1589px) and (max-height:680px) and (min-height:601px)':
    {
      contentOutstanding: {
        marginTop: '-36px',
      },
    },
  '@media screen and (min-width:1200px) and (max-height:600px)': {
    contentOutstanding: {
      marginTop: '-36px',
    },
  },
  '@media screen and (min-height:621px) and (max-width:1349px)': {
    contentOutstanding: {
      marginTop: '-36px',
    },
  },
  // '@media screen and (min-width: 1910px)': {
  //   contentOutstanding: {
  //     marginTop: '-70px',
  //   },
  // },
  '@media screen and (max-width: 850px)': {
    menu: {
      display: 'none',
    },
    content: {
      width: '100%',
      zIndex: '20',
      paddingRight: 0,
    },
    headerHistory: {
      height: '160px!important',
    },
    headerOutStanding: {
      width: '100%',
      paddingRight: 0,
    },
  },
}));
