import React, { useState, useEffect, useRef } from 'react';
import { Typography, Button, Input } from '@material-ui/core';
import { Pagination } from '@material-ui/lab';
import { useTranslation } from 'react-i18next';
import { BaseLayout } from 'layouts';
import { LangConstant } from 'const';
import { ImageContainer, Spinner } from 'components';
import { useStyles } from './styles';
import { useGlobalContext } from 'store';

const FrequentlyQuestion = () => {
  const { t: getLabel, i18n } = useTranslation();
  const { getQuestion, searchQuestions, getDataConfig, PAGE_KEY } =
    useGlobalContext();
  const [pageCurrent, setPageCurrent] = useState(1);
  const [loading, setLoading] = useState(false);
  const [dataSearch, setDataSearch] = useState('');
  const [showPagination, setShowPagination] = useState(true);
  const [imageQuestion, setImageQuestion] = useState(true);
  const [dataValue, setDataValue] = useState();
  const [anserCurrent, setAnserCurrent] = useState(false);
  const [arrayQuestion, setArrayQuestion] = useState();
  const [totalPage, setTotalPage] = useState(1);
  const popup = useRef();
  const classes = useStyles();

  const handleClickQuestion = (item) => {
    setAnserCurrent(item.answer);
  };
  const handleChangePage = (e, page) => {
    setPageCurrent(page);
  };
  useEffect(() => {
    const run = async () => {
      setLoading(true);
      const res = await getQuestion(pageCurrent, i18n.language, 8);
      if (res.success) {
        if (res.total % 8 !== 0) {
          setTotalPage(Math.floor(res.total / 8 + 1));
        } else {
          setTotalPage(res.total / 8);
        }
        setArrayQuestion(res.data);
        setLoading(false);
      } else {
        setTotalPage(1);
        setArrayQuestion([]);
        setLoading(false);
      }
    };
    run();
  }, [i18n.language, pageCurrent]);
  useEffect(() => {
    const handleClickDocument = (event) => {
      if (anserCurrent && !popup.current?.contains(event.target)) {
        setAnserCurrent(false);
      }
    };
    document.addEventListener('click', handleClickDocument);
    return () => {
      document.removeEventListener('click', handleClickDocument);
    };
  }, [anserCurrent]);
  useEffect(() => {
    const run = async () => {
      if (dataSearch === '') {
        setDataValue(arrayQuestion);
        setShowPagination(true);
      } else {
        setShowPagination(false);
        const value = await searchQuestions(dataSearch, i18n.language);
        if (value.success) {
          setDataValue(value.data);
        }
      }
    };

    run();
  }, [dataSearch, arrayQuestion, i18n.language]);
  const handleChangeSearch = (e) => {
    setDataSearch(e.target.value);
  };
  useEffect(() => {
    const run = async () => {
      setLoading(true);
      const res = await getDataConfig(PAGE_KEY.question, i18n.language);
      setImageQuestion(res.data[0].value);
      setLoading(false);
    };
    run();
  }, [i18n.language]);
  return (
    <BaseLayout background_url={imageQuestion.imgBackGround}>
      {loading && <Spinner />}
      <div className={classes.headerContainer}>
        <Typography className={classes.header}>
          {getLabel(LangConstant.TXT_FREQUENTLY_QUESTION)}
        </Typography>
      </div>

      <div style={{ display: 'flex', width: '100%', paddingLeft: '20px' }}>
        <Input
          classes={{
            root: classes.outlinedSearch,
            input: classes.input,
          }}
          onChange={handleChangeSearch}
          value={dataSearch}
          placeholder={getLabel(LangConstant.TXT_SEARCH)}
        />
        <Button className={classes.buttonSearch} variant="outlined">
          {getLabel(LangConstant.TXT_SEARCH)}
        </Button>
      </div>
      <div className={classes.contentQuestionContainer}>
        <div className={classes.contentQuestionImage}>
          <ImageContainer
            src={imageQuestion.imgQuestion}
            alt="..."
            style={{
              maxWidth: '70%',
            }}
            contain={true}
          ></ImageContainer>
        </div>
        <div className={classes.contentQuestionText}>
          <div className={classes.boxQuestion}>
            {dataValue?.map((item, index) => (
              <div
                key={index}
                onClick={() => handleClickQuestion(item)}
                className={classes.linkQuestion}
              >
                {item.question}
              </div>
            ))}
          </div>
          {showPagination && (
            <div className={classes.paginationBox}>
              <span>Trang</span>
              <Pagination
                className={classes.rootPage}
                color="primary"
                variant="outlined"
                count={totalPage}
                defaultPage={pageCurrent}
                // boundaryCount={2}
                shape="rounded"
                onChange={handleChangePage}
                showFirstButton
                showLastButton
              />
            </div>
          )}
        </div>
      </div>
      {anserCurrent && (
        <div ref={popup} className={classes.popup_question}>
          {/* <h2 className="popup-question-title">{anserCurrent.title}</h2> */}
          <p
            className="popup_question-description"
            dangerouslySetInnerHTML={{ __html: anserCurrent }}
          ></p>
          {/* <p
            className={classes.popup_question_content}
            dangerouslySetInnerHTML={{ __html: anserCurrent.content }}
          ></p>
          <p
            className={classes.popup_question_note}
            dangerouslySetInnerHTML={{ __html: anserCurrent.note }}
          ></p> */}
          <span
            onClick={() => setAnserCurrent(false)}
            className={classes.close_popup}
          >
            &#10095;&#10095;
          </span>
        </div>
      )}
    </BaseLayout>
  );
};

export default FrequentlyQuestion;
