import React, { useState } from 'react';

const newsBody = ({
  children,
  banner,
  topLeft,
  bottomLeft,
  right,
  setBanner,
  imgNews,
}) => {
  return (
    <div
      className="news-body fullheight"
      style={{ backgroundImage: `url(${imgNews.imgBackGround})` }}
    >
      {topLeft && (
        <div className="news-left-top-baner">
          <div style={{ backgroundImage: `url(${imgNews.flash_left_top})` }}>
            <svg
              className="MuiSvgIcon-root MuiSvgIcon-fontSizeSmall"
              focusable="false"
              viewBox="0 0 24 24"
              aria-hidden="true"
              style={{ border: '1px solid rgb(255, 255, 255)' }}
              className="news-button-close-banner"
              onClick={() => setBanner({ ...banner, topLeft: false })}
            >
              <path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12 19 6.41z" />
            </svg>
          </div>
        </div>
      )}

      {bottomLeft && (
        <div className="news-left-bottom-baner">
          <div style={{ backgroundImage: `url(${imgNews.flash_left_bottom})` }}>
            <svg
              className="MuiSvgIcon-root MuiSvgIcon-fontSizeSmall"
              focusable="false"
              viewBox="0 0 24 24"
              aria-hidden="true"
              style={{ border: '1px solid rgb(255, 255, 255)' }}
              className="news-button-close-banner"
              onClick={() => setBanner({ ...banner, bottomLeft: false })}
            >
              <path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12 19 6.41z" />
            </svg>
          </div>
        </div>
      )}

      {right && (
        <div className="news-right-baner">
          <div style={{ backgroundImage: `url(${imgNews.flash_right})` }}>
            <svg
              className="MuiSvgIcon-root MuiSvgIcon-fontSizeSmall"
              focusable="false"
              viewBox="0 0 24 24"
              aria-hidden="true"
              style={{ border: '1px solid rgb(255, 255, 255)' }}
              className="news-button-close-banner"
              onClick={() => setBanner({ ...banner, right: false })}
            >
              <path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12 19 6.41z" />
            </svg>
          </div>
        </div>
      )}

      <div className="root-body fullheight">
        <div className="row fullheight" style={{ margin: 0 }}>
          <div className="col fullheight l12 margin-rl-auto news-container d-flex">
            {children}
          </div>
        </div>
      </div>
    </div>
  );
};

export default newsBody;
