import React from 'react';
import { makeStyles } from '@material-ui/core';
import clsx from 'clsx';

const HtmlRender = ({ html, className, pathname, defaultStyle = true }) => {
  const classes = useStyles();
  return (
    <div
      className={defaultStyle ? clsx(classes.textHtml, className, "flex-content" ,{
        'text-height': pathname === 'openLetter',
        'text-height-overview': pathname === 'overview',
        'text-height-outstanding': pathname === 'outstanding',
        'text-height-history': pathname === 'history',
        'text-height-achivements': pathname === 'achievements'
      }) : className}
      dangerouslySetInnerHTML={{ __html: html }}
    />
  );
};

export default HtmlRender;

const useStyles = makeStyles((theme) => ({
  textHtml: {
    width: '100%',
    height: '100%',
    fontSize: '1rem',
    textAlign: 'justify',
    letterSpacing: '0.3px',
    overflow: 'auto',
    '& p:last-child': {
      paddingBottom: '0px',
    },
    '& p': {
      paddingBottom: '10px',
    },
    '& h2': {
      marginTop: '20px',
      marginBottom: '5px',
      textTransform: 'uppercase',
      color: 'red',
      fontWeight: 800,
    },
    '& h3': {
      marginTop: '15px',
      marginBottom: '5px',
      textTransform: 'uppercase',
      color: 'red',
    },
    '& h4': {
      textTransform: 'uppercase',
    },
  }
}));
