import React, { useEffect, useRef, useState } from 'react';
// import { getOpenLetterContent } from 'api/intro';
import { HtmlRender, Image, Spinner } from 'components';
import TwoBackLayout from 'layouts/twoback-layout';
import { useTranslation } from 'react-i18next';
import { useGlobalContext } from 'store';

const OpenLetter = () => {
  const { i18n } = useTranslation();
  const { getDataConfig, PAGE_KEY } = useGlobalContext();
  const [data, setData] = useState({});
  const [loading, setLoading] = useState(false);
  const dataRef = useRef();
  useEffect(() => {
    const getData = async () => {
      setLoading(true);
      // const dataRes = await getOpenLetterContent(i18n.language);
      const res = await getDataConfig(PAGE_KEY.letter, i18n.language);
      dataRef.current = `<p><b>${res.data[0].value.title}</b></p>
      <p>${res.data[0].value.text1}</p>
      <p>${res.data[0].value.text2}</p>
      <p>${res.data[0].value.text3}</p>
      <p><b>${res.data[0].value.footer}</b></p>`;
      setData(res.data[0].value);
      setLoading(false);
    };
    getData();
  }, [i18n.language]);

  const header = <Image src={data.imgCover} />;
  const content = (
    <HtmlRender html={dataRef.current} pathname={'openLetter'}></HtmlRender>
  );
  return (
    <>
      {data && (
        <>
          {loading ? (
            <Spinner />
          ) : (
            <TwoBackLayout
              header={header}
              header_backgroud_url={data.imgBackGroundHead}
              content_background_url={data.imgBackGround}
              content={content}
            />
          )}
        </>
      )}
    </>
  );
};

export default OpenLetter;
