import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  root: {
    padding: '24px 0',
  },
  container: {
    width: '100%',
    height: '100%',
    background: "url('background.jpg')",
    backgroundSize: '100% 100%',
  },
  rootStepper: {
    width: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  content: {
    width: '100%',
    height: '100%',
  },
  header: {},
  title: {
    background: 'linear-gradient(90deg, white, transparent)',
    lineHeight: '20px',
    padding: '13.5px 16px',
    display: 'flex',
    width: 'fit-content',
    color: '#ff152c',
    marginBottom: '10px',
    alignItems: 'center',
    '& > div:last-child': {
      marginLeft: 30,
      textTransform: 'uppercase',
      // color: "#ff152c",
      fontWeight: 800,
      fontSize: 16,
    },
  },
  icon: {
    fontSize: 35,
  },
  text: {
    marginTop: 16,
    lineHeight: '22px',
  },
  stepper: {
    marginTop: '16px',
    height: '50%',
  },
  info: {
    display: 'flex',
    width: '100%',
    height: '100%',
  },
  imagePrize: {
    maxHeight: '80%',
    maxWidth: '30%',
    animation: 'spin 4s infinite',
  },
  image: {
    maxWidth: '100%',
    height: '100%',
  },
  details: {
    flexGrow: 1,
    margin: '0px 32px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    width: '70%',
  },
  menu: {
    width: 250,
  },
  listItems: {
    width: '100%',
    backgroundColor: '#ffffff',
  },
  headerItem: {
    padding: '8px 24px',
    fontSize: 16,
    fontWeight: 800,
    backgroundColor: '#ff152c',
    color: '#ffffff',
  },
  item: {
    width: '100%',
    boxSizing: 'border-box',
    padding: '11px 24px',
    fontWeight: 800,
    cursor: 'pointer',
    '&:hover': {
      textDecoration: 'underline',
      backgroundColor: '#a6a39e33',
    },
  },
  button: {
    backgroundColor: 'transparent',
    border: 'none',
    cursor: 'pointer',
    fontWeight: 800,
    fontSize: 13,
    color: '#ff152c',

    '& > span': {
      fontSize: 20,
      marginLeft: 2,
    },
    '& > span:hover': {
      textDecoration: 'underline',
    },
  },
  circle: {
    width: 5,
    height: 5,
    backgroundColor: '#ff152c',
    borderRadius: '50%',
    marginRight: 16,
  },
  awards: {
    fontWeight: 800,
    display: 'flex',
    alignItems: 'center',
    textTransform: 'uppercase',
    marginBottom: 10,
  },
  buttonPrevNext: {
    color: '#ff152c',
    border: 'none',
    cursor: 'pointer',
    backgroundColor: 'transparent',
    width: '32px',
    height: '28px',
    '& > img': { width: '32px', height: '28px' },
  },

  leftButton: {
    left: 90,
  },
  iconArrow: {
    width: 30,
    height: 40,
  },
  left: {
    position: 'relative',
    left: 8,
  },
  right: {
    position: 'relative',
    right: 8,
  },
  colorRedOpacity: {
    color: '#ff9191',
  },
  '@media screen and (max-width: 850px)': {
    image: {
      objectFit: 'contain',
    },
    buttonPrevNext: {
      '&>img': {
        width: '20px',
        height: '20px',
        verticalAlign: 'middle',
      },
    },
    awards: {
      fontSize: '0.8rem',
    },
  },
}));
