import React from 'react';
import { Typography } from '@material-ui/core';
import { useTranslation } from 'react-i18next';
import clsx from 'clsx';
import { LangConstant } from 'const';
import { useStyles } from './styles';

const HomeFooter = () => {
  const classes = useStyles({ height: HOME_FOOTER_HEIGHT });
  const { t: getLabel } = useTranslation();

  return (
    <footer className={clsx('center-root', 'home-footer', classes.root)}>
      <Typography className={classes.descriptionText}>
        {getLabel(LangConstant.TXT_DESCRIPTION_FOOTER)}
      </Typography>
      <Typography className={classes.serviceText}>
        {getLabel(LangConstant.TXT_SERVICE_FOOTER)}
      </Typography>
      <span
        className="messengerText"
        style={{ color: 'black', bottom: '10px' }}
      >
        Hotline: 18008095 Email: cskh@viettellogistics.com.vn
      </span>
    </footer>
  );
};

export default HomeFooter;

export const HOME_FOOTER_HEIGHT = 80;
