import React, { useState, useEffect } from 'react';
import './newsDetails.css';
import { getPosts } from 'api/news';
import { NewsBody, NewsSideBarForm, Spinner } from 'components';
import { HomeLayout } from 'layouts';
import { Link } from 'react-router-dom';
import slugify from 'utils/slugify';
import { useTranslation } from 'react-i18next';
import { LangConstant } from 'const';
import { useGlobalContext } from 'store';

const NewsDetails = ({ ...props }) => {
  const { t: getLabel, i18n } = useTranslation();
  const { getNewsDetail, getNews, getCategorys, getDataConfig, PAGE_KEY } =
    useGlobalContext();
  const [postArray, setPostArray] = React.useState([]);
  const [postCurrent, setPostCurrent] = useState();
  const [categorys, setCategorys] = useState();
  const [loading, setLoading] = useState(false);
  const [imgNews, setImgNews] = useState(false);

  useEffect(() => {
    const getPostCurrent = async () => {
      setLoading(true);
      const res = await getNewsDetail(props.match.params.id);
      if (res.success) {
        setPostCurrent(res.data);
        setLoading(false);
      } else {
        setPostCurrent([]);
        setLoading(false);
      }
    };
    getPostCurrent();
  }, [props.match.params.id]);
  useEffect(() => {
    const run = async () => {
      const res = await getCategorys(i18n.language);
      if (res.success) {
        setCategorys(res.data);
      } else {
        setCategorys([]);
      }
    };
    run();
  }, [i18n.language]);
  useEffect(() => {
    const run = async () => {
      if (postCurrent) {
        setLoading(true);
        const res = await getNews(
          postCurrent.newsCatogryId,
          1,
          4,
          i18n.language
        );
        if (res.success) {
          setPostArray(res.data);
          setLoading(false);
        } else {
          setPostArray([]);
          setLoading(false);
        }
      }
    };
    run();
  }, [postCurrent, i18n.language]);
  const [banner, setBanner] = useState({
    topLeft: false,
    bottomLeft: false,
    right: false,
  });
  useEffect(() => {
    const run = async () => {
      setLoading(true);
      const res = await getDataConfig(PAGE_KEY.newsdetail, i18n.language);
      if (res.success) {
        setBanner({
          topLeft: res.data[0].value.is_show_flash_left_top,
          bottomLeft: res.data[0].value.is_show_flash_left_bottom,
          right: res.data[0].value.is_show_flash_right,
        });
        setImgNews(res.data[0].value);
      }
      setLoading(false);
    };
    run();
  }, [i18n.language]);
  const { topLeft, bottomLeft, right } = banner;
  const getTime = (time) => {
    const date = new Date(time);
    return `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear() + 1}`;
  };
  const getNameCategory = (id) => {
    const category = categorys.find((c) => c.id === id);
    return category?.name;
  };
  const postsRelated = () => {
    return postArray?.slice(0, 4).map((p, i) => {
      return (
        <li key={i} className="news-ralated-posts-item">
          <Link
            to={`/news/${slugify(p.title)}.${p.id}`}
            className="news-ralated-posts-item-link ellipsis_2"
          >
            <img
              className="news-ralated-posts-item-link-icon"
              src="icon-item-post-related.png"
              alt=""
            />
            <p className="news-ralated-posts-item-link-text ellipsis_2">
              {' '}
              {p.title}
            </p>
          </Link>
        </li>
      );
    });
  };
  return (
    <HomeLayout typeFooter="copyright">
      <NewsBody
        banner={banner}
        topLeft={topLeft}
        bottomLeft={bottomLeft}
        right={right}
        setBanner={setBanner}
        imgNews={imgNews}
      >
        {loading && <Spinner />}
        <div className="news-main post">
          {categorys && postCurrent ? (
            <>
              <div className="news-main-header-type-post">
                <p>{getNameCategory(postCurrent.newsCatogryId)}</p>
              </div>
              <div className="news-main-header-post">
                <h2 className="news-main-header-title-post">
                  {postCurrent.title}
                </h2>
                <div className="news-main-header-action">
                  <img src="/news/clock.png" alt="" />
                  <span>{getTime(postCurrent.createdAt)}</span>
                </div>
              </div>
              <div className="news-main-img-post">
                <img
                  className="news-main-img-post-item"
                  src={postCurrent.hightlightImage}
                  alt=""
                />
              </div>
              <div className="news-main-content-post">
                <p
                  dangerouslySetInnerHTML={{ __html: postCurrent.content }}
                ></p>
              </div>
            </>
          ) : (
            <h1 style={{ color: 'red', textAlign: 'center' }}>
              Khng tn ti bi vit ny
            </h1>
          )}
        </div>
        {categorys && (
          <div className="news-sidebar fullheight">
            <div className="news-sidebar-space"></div>
            <div className="news-ralated-posts">
              <ul className="news-ralated-posts-list">
                <li className="news-ralated-posts-item">
                  <h2 className="news-ralated-posts-item-title">
                    {getLabel(LangConstant.TXT_RELATED_POST_NEWS)}
                  </h2>
                </li>
                {postsRelated()}
                <li className="news-ralated-posts-item">
                  <Link
                    to={`/news?category=${
                      postCurrent &&
                      slugify(
                        getNameCategory(postCurrent.newsCatogryId)
                          ? getNameCategory(postCurrent.newsCatogryId)
                          : 'null'
                      )
                    }`}
                    className="news-ralated-posts-item-see_more"
                  >
                    {getLabel(LangConstant.TXT_SEE_MORE_NEWS)}
                  </Link>
                </li>
              </ul>
            </div>
            <NewsSideBarForm getLabel={getLabel} LangConstant={LangConstant} />
          </div>
        )}
      </NewsBody>
    </HomeLayout>
  );
};

export default NewsDetails;
