import React, { useState, useEffect } from 'react';
import { TwoBackLayout } from 'layouts';
import { useStyles } from './styles';
import { ImageContainer, Spinner } from 'components';
import { useTranslation } from 'react-i18next';
// import { getPartnerContent } from 'api/intro';
import { LangConstant } from 'const';
import { useGlobalContext } from 'store';

const Partner = () => {
  const classes = useStyles();
  const { getDataConfig, PAGE_KEY } = useGlobalContext();
  const { t: getLabel, i18n } = useTranslation();
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState();
  useEffect(() => {
    const getData = async () => {
      setLoading(true);
      // const dataRes = await getPartnerContent();
      const res = await getDataConfig(PAGE_KEY.partner, i18n.language);
      setData(res.data[0].value);
      setLoading(false);
    };
    getData();
  }, [i18n.language]);

  return (
    <>
      {data && (
        <>
          {loading ? (
            <Spinner />
          ) : (
            <TwoBackLayout
              content_background_url={data.imgBackGround}
              header_backgroud_url={data.imgBackGroundHead}
              partner
              header={
                <img src={data.imgCover} alt="..." className={classes.image} />
              }
              content={
                <div className="fullheight flex-content">
                  <div className={classes.boxInfo}>
                    <div className={classes.title}>
                      {getLabel(LangConstant.TXT_PARTNER)}
                    </div>
                    <div className={classes.boxLogo}>
                      {data &&
                        data.ArrImgPartner.map((image, index) => (
                          <div key={index} className={classes.logoContainer}>
                            <img
                              src={image}
                              alt="..."
                              className={classes.logo}
                            />
                          </div>
                        ))}
                    </div>
                  </div>
                  <div className={classes.boxInfo}>
                    <div className={classes.title}>
                      {getLabel(LangConstant.TXT_CUSTOMER)}
                    </div>
                    <div className={classes.boxLogo}>
                      {data &&
                        data.ArrImgCustomer.map((image, index) => (
                          <div key={index} className={classes.logoContainer}>
                            <img
                              src={image}
                              alt="..."
                              className={classes.logo}
                            />
                          </div>
                        ))}
                    </div>
                  </div>
                </div>
              }
            />
          )}
        </>
      )}
    </>
  );
};

export default Partner;
