import React, { useRef, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import Messenger from './m1.png';
import boxchat from './boxchat.png';

import {
  Header,
  Footer,
  FOOTER_HEIGHT,
  HomeFooter,
  HOME_FOOTER_HEIGHT,
} from './components';

const HomeLayout = ({ children, typeFooter }) => {
  const linkRef = useRef();
  const [showBox, setShowBox] = useState(false);
  useEffect(() => {
    if (window.location.pathname === '/') {
      linkRef.current.classList.add('home');
    }
  }, [window.location.pathname]);
  return (
    <>
      <div>
        <Header />
        <main
          className={clsx(
            typeFooter === 'home' ? 'home-body' : 'body',
            'fullwidth'
          )}
        >
          {children}
        </main>
        <a
          href="https://m.me/275542932584182"
          className="messenger"
          target="_blank"
          rel="nofollow noopener noreferrer"
          ref={linkRef}
        >
          <img
            src={Messenger}
            width="48"
            height="48"
            title="T vn trn Facebook Messenger"
            alt="T vn trn Facebook Messenger"
          />
          <div className="boxchat" hidden={!showBox}>
            Xin cho! Chng ti c th gip g cho bn?
            {/* <svg
              width={20}
              height={20}
              viewBox="0 0 32 32"
              fill="white"
              xmlns="http://www.w3.org/2000/svg"
              className="boxChatIcon"
            
            >
              <path
                d="M0 16C0 7.16344 7.16344 0 16 0C24.8366 0 32 7.16344 32 16C32 24.8366 24.8366 32 16 32C7.16344 32 0 24.8366 0 16Z"
                fill="black"
                fillOpacity="0.04"
              />
              <path
                d="M22.0278 15.0278C22.5647 15.0278 23 15.4631 23 16.0001C23 16.537 22.5647 16.9723 22.0278 16.9723L9.97222 16.9723C9.43528 16.9723 9 16.537 9 16.0001C9 15.4631 9.43528 15.0278 9.97222 15.0278L22.0278 15.0278Z"
                fill="black"
              />
            </svg> */}
          </div>
        </a>
        {typeFooter ? FOOTER_OPTION[typeFooter].components : <Footer />}
      </div>
    </>
  );
};

HomeLayout.propTypes = {
  children: PropTypes.node,
  typeFooter: PropTypes.string,
};

export default HomeLayout;

const FOOTER_OPTION = {
  home: {
    height: HOME_FOOTER_HEIGHT,
    components: <HomeFooter />,
  },
  copyright: {
    height: FOOTER_HEIGHT,
    components: <Footer />,
  },
};
