import React, { useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { LangConstant } from 'const';
import slugify from 'utils/slugify';
import { useGlobalContext } from 'store';
const NewsSideBarMenu = ({ categorys }) => {
  const { t: getLabel, i18n } = useTranslation();
  const { search } = useLocation();
  // const { getCategorys } = useGlobalContext();
  const [category, setCategory] = useState();

  useEffect(() => {
    const query = new URLSearchParams(search);
    setCategory(query.get('category'));
  }, [search]);
  useEffect(() => {
    const run = async () => {
      categorys &&
        Array.from(categorys).forEach((c) => {
          if (c.is_news) {
            const item = document.querySelector(`[data-new="${c.id}"]`);
            item.classList.add('activeNew');
          }
        });
    };
    run();
  }, [categorys]);
  useEffect(() => {
    const test = document.querySelectorAll('.news-sidebar-menu-item.activeNew');
    if (test) {
      const timeId = setInterval(() => {
        test.forEach((i) => i.classList.toggle('flash'));
      }, 100);
    }
  }, []);
  return (
    <div className="news-sidebar-menu">
      <h2 className="news-sidebar-menu-title">
        {getLabel(LangConstant.TXT_TITLE_NEWS)
          .split('')
          .map((i, c) => {
            if (i !== ' ') {
              return (
                <span key={c} style={{ animationDelay: `${0.05 * c}s` }}>
                  {i}
                </span>
              );
            } else {
              return <span key={c}>&nbsp;</span>;
            }
          })}
      </h2>
      <ul className="news-sidebar-menu-list">
        {categorys?.map((c, i) => (
          <li
            key={i}
            className="news-sidebar-menu-item"
            style={{ animationDelay: `${0.05 * i}s` }}
            data-new={c.id}
          >
            <Link
              className={`news-sidebar-menu-item-link ${
                category === slugify(c.name) ? 'active' : ''
              }`}
              to={`/news?category=${slugify(c.name)}`}
            >
              {c.name}
              <div className="news-sidebar-menu-item-icon">
                <img src="/news/newbrand.png" alt="" />
              </div>
            </Link>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default NewsSideBarMenu;
