import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    background: "url('background.jpg')",
    backgroundSize: '100% 100%',
  },
  noteContainer: {
    flexBasis: '20%',
    textAlign: 'right',
  },
  container: {
    height: 'inherit',
    width: '70%',
    margin: '0 auto',
    padding: '100px 0',
    display: 'grid',
    gridTemplateColumns: 'repeat(4, minmax(0, 1fr));',
    columnGap: 30,
    rowGap: 70,
  },
  boxService: {
    width: '100%',
    height: '100%',
    display: 'flex',
    borderRadius: '10px',
    flexDirection: 'column',
    overflow: 'hidden',
    backgroundColor: theme.palette.primary.main,

    '&:hover > div': {
      backgroundSize: '125%',
    },
  },
  boxContent: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-start',
    height: '30%',
    width: '100%',
  },
  note: {
    fontSize: '1.8rem',
    color: theme.palette.common.white,
    textAlign: 'center',
  },
  textContent: {
    fontSize: '15px',
    fontWeight: 800,
    color: theme.palette.common.white,
    textTransform: 'uppercase',
    verticalAlign: 'middle',
  },

  imageService: {
    minHeight: '70%',
    borderTopLeftRadius: 4,
    borderTopRightRadius: 4,
    filter: 'brightness(80%)',
  },
  content: {
    flex: '80%',
    height: '100%',
    display: 'flex',
    justifyContent: 'flex-start',
    alignItems: 'center',
  },
  '@media screen and (max-height:600px)': {
    textContent: {
      fontSize: 10,
    },
  },
  '@media screen and (max-height:700px) and (min-height:601px)': {
    textContent: {
      fontSize: 12,
    },
  },
  '@media screen and (max-width: 850px)': {
    container: {
      width: '80%',
      gridTemplateColumns: 'repeat(1, minmax(0, 1fr))',
      padding: '20px 0',
    },
    boxService: {
      height: '200px',
    },
    textContent: {
      fontSize: '0.8rem',
    },
  },
}));
