import { makeStyles } from '@material-ui/core';
import { AppConstant } from 'const';

export const useStyles = makeStyles((theme) => ({
  main: {
    width: '100%',
    height: '100%',
  },
  headerContainer: {
    width: '100%',
    height: AppConstant.introHeaderHeight,
    background: ({ header_backgroud_url }) =>
      `${
        header_backgroud_url
          ? `url(${header_backgroud_url}) no-repeat center / cover`
          : '#e6e6e6'
      }`,
  },
  header: {
    height: '100%',
  },
  contentContainer: {
    width: '100%',
    height: AppConstant.introContentHeight,
    backgroundSize: 'cover',
    backgroundImage: ({ content_background_url }) =>
      `url(${
        content_background_url
          ? content_background_url
          : 'content_background.png'
      })`,
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center',

    overflow: 'auto',
  },
  content: {
    height: '100%',
    display: 'flex',
  },
  contentItem: {
    width: '75%',
    height: '100%',
    overflowY: 'auto',
  },
  menu: {
    width: '25%',
    height: '100%',
  },
  '@media screen and (max-width: 850px)': {
    menu: {
      display: 'none',
    },
    contentItem: {
      width: '100%',
      paddingRight: 0,
    },
    headerContainer: {
      padding: '16px 0',
      height: '160px',
    },
    headerPartnerContainer: {
      height: '160px',
    },
    header: {
      width: '96%',
      height: '100%',
    },
  },
}));
