import React, { useState, useEffect } from 'react';

import {
  Box,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Paper,
  Select,
  FormControl,
  MenuItem,
  InputLabel,
} from '@material-ui/core';
import clsx from 'clsx';
import { Delete, Edit } from '@material-ui/icons';
import { useStyles } from './styles';
import { AdminLayout } from 'layouts';
import { useGlobalContext } from 'store';
import { Spinner, ModalCreateBranch, ModalEditBranch } from 'components';

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

const SearchType = {
  BRANCH: 'branchs',
  WAREHOUSE: 'warehouses',
  OFFICE: 'offices',
};

const LanguageType = {
  VI: 'vi',
  EN: 'en',
};

const AdminSearch = () => {
  const classes = useStyles();
  const { getSearchUnit, uploadSearchUnit, deleteUnitsSearch, exportSearch } =
    useGlobalContext();
  const [open, setOpen] = useState(false);
  const [data, setData] = useState([]);
  const [editItem, setEditItem] = useState(null);
  const [lang, setLang] = useState(LanguageType.VI);
  const [type, setType] = useState(SearchType.BRANCH);
  const [loading, setLoading] = useState(false);
  const [start, setStart] = useState(false);

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const run = async () => {
    setLoading(true);
    const resData = await getSearchUnit(type, lang);
    setData(resData.data);
    setLoading(false);
  };
  const handleDeleteUnitSearch = async (id) => {
    await deleteUnitsSearch(id);
    run();
  };
  useEffect(() => {
    run();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [type, lang, start]);

  const handleUpload = async () => {
    var formData = new FormData();
    var excelFile = document.querySelector('#contained-button-file').files[0];
    formData.append('file', excelFile);
    formData.append('type', type);
    formData.append('lang', lang);
    setLoading(true);
    const resData = await uploadSearchUnit(formData);
    if (resData) {
      alert('Success');
    } else {
      alert('Message');
    }
    setLoading(false);
    run();
  };
  const handleExportSearch = async () => {
    const res = await exportSearch({
      type,
      lang,
    });
    const url = window.URL.createObjectURL(new Blob([res.data]));
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', `${type}.xlsx`);
    document.body.appendChild(link);
    link.click();
    link.parentNode.removeChild(link);
  };
  return (
    <AdminLayout>
      {loading && <Spinner />}
      {open && (
        <ModalCreateBranch
          setStart={setStart}
          start={start}
          handleClose={handleClose}
        />
      )}
      {editItem && (
        <ModalEditBranch
          editItem={editItem}
          setStart={setStart}
          start={start}
          setEditItem={setEditItem}
        />
      )}
      <Box className="flex-between">
        <div>
          <Button
            variant="outlined"
            onClick={() => setType(SearchType.BRANCH)}
            className={clsx(
              classes.buttonType,
              type === SearchType.BRANCH ? 'button-select' : ''
            )}
          >
            Chi nhnh
          </Button>
          <Button
            variant="outlined"
            onClick={() => setType(SearchType.WAREHOUSE)}
            className={clsx(
              classes.buttonType,
              type === SearchType.WAREHOUSE ? 'button-select' : ''
            )}
          >
            Kho bi
          </Button>
          <Button
            variant="outlined"
            onClick={() => setType(SearchType.OFFICE)}
            className={clsx(
              classes.buttonType,
              type === SearchType.OFFICE ? 'button-select' : ''
            )}
          >
            Bu cc
          </Button>
        </div>
        <FormControl className={classes.formControl}>
          <InputLabel id="demo-simple-select-label">Language</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={lang}
            onChange={(event) => setLang(event.target.value)}
          >
            <MenuItem value={LanguageType.VI}>Vietnamese</MenuItem>
            <MenuItem value={LanguageType.EN}>EngLish</MenuItem>
          </Select>
        </FormControl>
      </Box>

      <Box className={classes.add}>
        <div>
          <input
            accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
            className={classes.input}
            id="contained-button-file"
            type="file"
            onChange={handleUpload}
          />
          <label htmlFor="contained-button-file">
            <Button variant="outlined" component="span">
              Import
            </Button>
            <Button
              style={{ marginLeft: 10 }}
              variant="outlined"
              onClick={handleExportSearch}
            >
              Export
            </Button>
          </label>
        </div>
        <Button variant="outlined" onClick={handleOpen}>
          Thm mi
        </Button>
      </Box>

      <TableContainer className={classes.tableCooperate} component={Paper}>
        <Table aria-label="caption table">
          <TableHead>
            <TableRow>
              <TableCell width="20%">Tn</TableCell>
              <TableCell width="20%" align="right">
                a ch
              </TableCell>
              <TableCell width="10%" align="right">
                Thao tc
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data.map((item, index) => (
              <TableRow
                key={item.id}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              >
                <TableCell component="th" scope="row">
                  {item.name}
                </TableCell>
                <TableCell align="right">{item.address}</TableCell>
                <TableCell width="10%" align="right">
                  <IconButton
                    aria-label="delete"
                    onClick={() => setEditItem(item)}
                  >
                    <Edit />
                  </IconButton>
                  <IconButton
                    aria-label="delete"
                    onClick={() => handleDeleteUnitSearch(item.id)}
                  >
                    <Delete />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </AdminLayout>
  );
};

export default AdminSearch;
