import React, { useEffect, useState } from 'react';
import style from './ManageQuestion.module.css';
import {
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Paper,
} from '@material-ui/core';
import { Delete, Edit } from '@material-ui/icons';
import {
  AppPagination,
  Spinner,
  ModalAddQuestion,
  ModalEditQuestion,
} from 'components';
import { AdminLayout } from 'layouts';
import { useGlobalContext } from 'store';

const ManageQuestion = () => {
  const { getQuestion, deleteQuestion } = useGlobalContext();
  const [arrayOrders, setArrayOrders] = useState();
  const [start, setStart] = useState(true);
  const [language, setLanguage] = useState('vi');
  const [addQuestion, setAddQuestion] = useState(false);
  const [editQuestion, setEditQuestion] = useState({
    status: false,
    item: null,
  });
  const [pageCurrent, setPageCurrent] = useState(1);
  const [totalPage, setTotalPage] = useState();
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    async function run() {
      const res = await getQuestion();
      if (res.success) {
        if (res.total % 6 !== 0) {
          setTotalPage(Math.floor(res.total / 6) + 1);
        } else {
          setTotalPage(res.total / 6);
        }
      }
    }
    run();
  }, []);
  useEffect(() => {
    const getData = async () => {
      setLoading(true);
      const res = await getQuestion(pageCurrent, language);
      if (res.success) {
        setLoading(false);
        setArrayOrders(res.data);
      } else {
        setLoading(false);
        setArrayOrders([]);
      }
    };
    getData();
  }, [pageCurrent, language, start]);
  const handleDeleteQuestion = async (id) => {
    const res = await deleteQuestion(id);
    if (res.success) {
      setArrayOrders(arrayOrders.filter((item) => item.id !== id));
    }
  };
  const handleEditQuestion = (item) => {
    setEditQuestion({ item, status: true });
  };
  return (
    <AdminLayout>
      <Button
        variant="outlined"
        className={style.buttonExport}
        onClick={() => setAddQuestion(true)}
      >
        Thm cu hi
      </Button>
      <Button
        variant="outlined"
        className={style.buttonExport}
        onClick={() => setLanguage('en')}
      >
        English
      </Button>
      <Button
        variant="outlined"
        className={style.buttonExport}
        onClick={() => setLanguage('vi')}
      >
        Vienamese
      </Button>
      {addQuestion && (
        <ModalAddQuestion
          start={start}
          setStart={setStart}
          setAddQuestion={setAddQuestion}
        />
      )}
      {editQuestion.status && (
        <ModalEditQuestion
          setEditQuestion={setEditQuestion}
          editQuestion={editQuestion}
          start={start}
          setStart={setStart}
        />
      )}
      {loading && <Spinner />}
      <TableContainer className={style.tableOrder} component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell className={style.bold} width="30%" align="left">
                Question
              </TableCell>
              <TableCell className={style.bold} width="30%" align="left">
                Answer
              </TableCell>
              <TableCell className={style.bold} width="30%" align="left">
                Language
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {arrayOrders?.map((item, index) => (
              <TableRow
                key={index}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              >
                <TableCell className={style.bold} align="left">
                  {item.question}
                </TableCell>
                <TableCell
                  align="left"
                  dangerouslySetInnerHTML={{ __html: item.answer }}
                ></TableCell>
                <TableCell align="left">{item.language}</TableCell>

                <TableCell width="10%" align="left" style={{ display: 'flex' }}>
                  <IconButton
                    aria-label="edit"
                    onClick={() => handleEditQuestion(item)}
                  >
                    <Edit />
                  </IconButton>
                  <IconButton
                    aria-label="delete"
                    onClick={() => handleDeleteQuestion(item.id)}
                  >
                    <Delete />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <AppPagination
        totalPage={totalPage}
        onChange={(page) => {
          setPageCurrent(page);
        }}
      />
    </AdminLayout>
  );
};

export default ManageQuestion;
