import React, { useEffect, useState } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Paper,
  Button,
} from '@material-ui/core';
import { Delete } from '@material-ui/icons';
import { AppPagination } from 'components';
import { useStyles } from './styles';
import { AdminLayout } from 'layouts';
import { useGlobalContext } from 'store';
import { Spinner } from 'components';
import style from '../Customer.module.css';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

const Note = () => {
  const classes = useStyles();
  const { getFeedbacks, exportFeed } = useGlobalContext();
  const [noteArray, setNoteArray] = useState([]);
  const [totalPage, setTotalPage] = useState();
  const [pageCurrent, setPageCurrent] = useState(1);
  const [feedbackLoading, setFeedbackLoading] = useState(true);
  const [form, setForm] = useState({
    from: '',
    to: '',
  });
  const getTime = (e) => {
    const date = new Date(e);
    const day = date.getDate();
    const month = date.getMonth() + 1;
    const year = date.getFullYear();
    return `${day}/${month}/${year}`;
  };
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const setFrom = (e) => {
    setStartDate(e);
    setForm({ ...form, from: getTime(e) });
  };
  const setTo = (e) => {
    setEndDate(e);
    setForm({ ...form, to: getTime(e) });
  };
  useEffect(() => {
    async function run() {
      const res = await getFeedbacks();
      if (res.success) {
        if (res.total % 6 !== 0) {
          setTotalPage(Math.floor(res.total / 6) + 1);
        } else {
          setTotalPage(res.total / 6);
        }
      }
    }
    run();
  }, []);
  useEffect(() => {
    const getData = async () => {
      setFeedbackLoading(true);
      const res = await getFeedbacks(pageCurrent);
      if (res.success) {
        setNoteArray(res.data);
        setFeedbackLoading(false);
      } else {
        setNoteArray([]);
        setFeedbackLoading(false);
      }
    };
    getData();
  }, [pageCurrent]);
  const handleExportFeedback = async () => {
    const res = await exportFeed(form);
    const url = window.URL.createObjectURL(new Blob([res.data]));
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', 'feedback.xlsx');
    document.body.appendChild(link);
    link.click();
    link.parentNode.removeChild(link);
  };

  return (
    <AdminLayout>
      <div className={style.boxHeader}>
        <div className={style.formExport}>
          <label htmlFor="from">From</label>
          <DatePicker selected={startDate} onChange={setFrom} />
        </div>
        <div className={style.formExport}>
          <label htmlFor="to">To</label>
          <DatePicker selected={endDate} onChange={setTo} />
        </div>
        <Button variant="outlined" onClick={handleExportFeedback}>
          Export
        </Button>
      </div>
      <TableContainer className={classes.tableNote} component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell width="20%">H tn</TableCell>
              <TableCell width="20%" align="right">
                S in thoi
              </TableCell>
              <TableCell width="20%" align="right">
                Email
              </TableCell>
              <TableCell width="30%" align="right">
                Ghi ch
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {noteArray?.map((item, index) => (
              <TableRow
                key={index}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              >
                <TableCell width="20%" component="th" scope="row">
                  {item.name}
                </TableCell>
                <TableCell width="20%" align="right">
                  {item.phone}
                </TableCell>
                <TableCell width="20%" align="right">
                  {item.email}
                </TableCell>
                <TableCell
                  width="30%"
                  align="right"
                  dangerouslySetInnerHTML={{ __html: item.content }}
                ></TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
        {feedbackLoading && <Spinner />}
      </TableContainer>
      <AppPagination
        totalPage={totalPage}
        onChange={(page) => {
          setPageCurrent(page);
        }}
      />
    </AdminLayout>
  );
};

export default Note;

export const NOTES = [
  {
    id: '1',
    name: 'a vuong',
    phone: '12345678',
    email: 'a@gmail.com',
    content: 'xinchao',
  },
];
