import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  image: {
    maxWidth: '100%',
    display: 'block',
    maxHeight: '100%',
    position: 'relative',
  },
  boxOutStanding: {
    width: '50%',
  },
  headerBox: {
    display: 'flex',
    alignItems: 'center',
    width: '100%',
  },
  outStanding: {
    padding: '0 30px 30px 0',
  },
  outStandingBottom: {
    padding: '0 30px 30px 0',
  },
  outStanding3: {
    padding: '0 30px 0 0',
  },
  outStandingNotPadding: {
    padding: '0 30px 0 0',
    width: '50%',
  },
  contentBox: {
    display: 'flex',
  },
  ImageContainer: {
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    maxWidth: '45%',
    paddingBottom: '50px',
    marginLeft: 'auto',
  },
  headerBoxText: { width: '50%', paddingRight: 30 },
  // '@media screen and (min-width: 1910px)': {
  //   headerBoxText: {
  //     alignSelf: 'flex-start!important',
  //     paddingTop: '24px!important',
  //   },
  // },
  '@media screen and (min-width: 1910px)': {
    outStandingBottom: {
      marginTop: '-40px',
      padding: '0 30px 70px 0',
    },
  },
  '@media screen and (max-width: 850px)': {
    headerBox: {
      flexWrap: 'wrap',
    },
    headerBoxText: { order: '2', width: '100%', marginBottom: '20px' },
    ImageContainer: {
      width: '100%',
      alignItems: 'unset',
      margin: '0 auto',
      height: 'auto',
      paddingBottom: '0',
    },
    image: {
      width: '160px',
      height: '180px',
      objectFit: 'contain',
      transform: 'scale(1.5)',
    },
    contentBox: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    boxOutStanding: { width: '100%', marginBottom: '30px' },
    outStandingNotPadding: { width: '100%' },
  },
}));
