import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  profileContainer: {
    display: 'flex',
    height: '70%',
    paddingTop: '20px',
  },
  boxTextViettel: {
    width: '100%  ',
    height: '100%',
    position: 'relative',
  },
  pdfContainer: {
    width: '75%',
    paddingRight: '20px',
    border: 'solid 1px #e3e3e3',
    '& .rpv-core__inner-page': {
      background:
        "url('tongquan_back.jpg')no-repeat left bottom fixed !important",
      backgroundSize: '100% 100%',
    },
  },
  menu: {
    width: '25%',
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
    alignItems: 'flex-start',
  },
  buttonDownload: {
    width: '100%',
    height: '40px',
    border: '1px solid #e3e3e3',
    backgroundColor: '#FFFFFF',
    display: 'flex',
    justifyContent: 'flex-start',
    alignItems: 'center',
    cursor: 'pointer',
    fontSize: '1rem',
  },

  iconDownload: {
    height: '100%',
    width: '40px',
    padding: '5px',
    background:
      'linear-gradient(45deg, rgb(243 16 102) 0%, rgb(253 23 23) 40%, rgb(255 0 0) 100%)',
    color: '#ffffff',
  },

  textDonwload: {
    display: 'block',
    paddingLeft: '20px',
    textTransform: 'uppercase',
    color: '#ff152c',
    fontWeight: 800,
  },

  textViettel: {
    height: '30%',
    padding: '10px',
    display: 'flex',
    justifyContent: 'center',
  },
  '@media screen and (max-width: 850px)': {
    menu: {
      display: 'none',
    },
    pdfContainer: {
      width: '100%',
    },
    textViettel: {
      height: '120px',
    },
  },
}));
