import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core';
import CloseOutlinedIcon from '@material-ui/icons/CloseOutlined';
import PropTypes from 'prop-types';

const ImageContainer = ({
  src,
  className,
  close,
  children,
  cover,
  contain,
}) => {
  const classes = useStyles({ src, cover, contain });
  const [show, setShow] = React.useState(true);
  return (
    <div className={clsx(classes.image, className, show ? '' : 'hidden')}>
      {close && (
        <div className={classes.closeIcon} onClick={() => setShow(false)}>
          <CloseOutlinedIcon
            fontSize="small"
            style={{ border: '1px solid #fff' }}
          />
        </div>
      )}
      {children}
    </div>
  );
};

ImageContainer.propTypes = {
  children: PropTypes.node,
  src: PropTypes.string,
  className: PropTypes.string,
  close: PropTypes.bool,
  cover: PropTypes.bool,
};

export default ImageContainer;

const useStyles = makeStyles((theme) => ({
  image: {
    background: ({ src, cover, contain }) =>
      `url(${src}) no-repeat center/${
        cover ? 'cover' : contain ? 'contain' : '100% 100%'
      }`,

    width: '100%',
    height: '100%',
  },
  closeIcon: {
    position: 'absolute',
    right: '5px',
    top: '7px',
    color: '#ffffff',
    cursor: 'pointer',
  },
  '@media screen and (max-width: 850px)': {
    image: {
      backgroundSize: 'cover!important',
    },
  },
}));
