import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  image: {
    width: '100%',
    display: 'block',
    position: 'relative',
    backgroundSize: '100% 100%',
    height: '175%',
  },
  contentBox: {},
  contentContainerBox: {
    position: 'relative',
    top: '-100px',
  },
  '@media screen and (max-width: 850px)': {
    image: {
      backgroundSize: 'contain',
    },
  },
}));
