import React from 'react';
import { Typography } from '@material-ui/core';
import clsx from 'clsx';
import { useStyles } from './styles';
import style from './FooterAnimation.module.css';

const Footer = () => {
  const classes = useStyles({ height: FOOTER_HEIGHT });

  return (
    <footer
      className={clsx('center-root', 'footer', classes.root, style.footer)}
    >
      <Typography className={classes.text}>
        Copyright 2016 - 2020 VTLOG | All Rights Reserved | Powered by Viettel
        Logistics
      </Typography>
      <span className="messengerText">
        Hotline: 18008095 Email: cskh@viettellogistics.com.vn
      </span>
    </footer>
  );
};

export default Footer;

export const FOOTER_HEIGHT = 30;
