import { makeStyles } from '@material-ui/core';
import { AppConstant } from 'const';
import { HEADER_HEIGHT, FOOTER_HEIGHT } from 'layouts/home-layout/components';

export const useStyles = makeStyles((theme) => ({
  main: {
    width: '100%',
    height: '100%',
    background: ({ content_background_url }) =>
      `url('${
        content_background_url
          ? content_background_url
          : 'content_background.png'
      }') no-repeat center 110px fixed `,
    backgroundSize: () => '100% 100%',
    overflow: 'auto',
  },
  headerContainer: {
    width: '100%',
    height: AppConstant.introHeaderHeight,
    background: ({ header_backgroud_url }) =>
      `${
        header_backgroud_url
          ? `url(${header_backgroud_url}) no-repeat center / cover  `
          : '#e6e6e6'
      }`,
  },
  header: {
    height: '100%',
    padding: '20px 0',
  },
  contentContainer: {
    width: '100%',
    height: AppConstant.introContentHeight,

    padding: '10px 0',
  },
  content: {
    height: '100%',
    display: 'flex',
  },
  contentItem: {
    width: '75%',
    height: '100%',
    paddingRight: '20px',
  },
  menu: {
    width: '25%',
  },
  '@media screen and (max-width: 850px)': {
    contentItem: {
      width: '100%',
      paddingRight: '0',
    },
    headerContainer: {
      height: '150px',
    },
  },
}));
