import React, { memo, useState } from 'react';
import ProtoType from 'prop-types';
import {
  Button,
  IconButton,
  Dialog,
  Typography,
  makeStyles,
  OutlinedInput,
} from '@material-ui/core';
import { Close } from '@material-ui/icons';
import { useTranslation } from 'react-i18next';
import { SelectBar } from 'components';
import { LangConstant } from 'const';
import { ARRAY_SERVICES } from 'pages/service';
import { Success, Failed } from 'components';
import clsx from 'clsx';
import style from './DialogSupportCustomer.module.css';
import { useGlobalContext } from 'store';

const DialogSupportCustomer = ({ isShow, onClose }) => {
  const classes = useStyles();
  const { t: getLabel } = useTranslation();
  const { addOrder } = useGlobalContext();
  const [formInput, setFormInput] = useState({
    fullname: '',
    phone: '',
    email: '',
    service: '',
    start: '',
    end: '',
    weight: '',
    note: '',
  });
  const [isDisplayWarning, setIsDisplayWarning] = useState(false);
  const [isSuccess, setIsSuccess] = useState(false);
  const [isFailed, setIsFailed] = useState(false);

  const isDisable =
    !formInput.fullname ||
    !formInput.phone ||
    formInput.service === '' ||
    !formInput.start ||
    !formInput.end ||
    !formInput.weight;
  const onChangeData = (event) => {
    if (isDisplayWarning) {
      setIsDisplayWarning(false);
    }

    setFormInput({
      ...formInput,
      [event.target.name]: event.target.value,
    });
  };

  const onSubmit = () => {
    if (!isNumeric(formInput.phone) || !isEmail(formInput.email)) {
      alert('Email hoc s in thoi khng ng. Vui lng nhp li');
      setIsFailed(true);
    } else {
      setIsSuccess(true);
      addOrder(formInput);
    }
  };

  const isNumeric = (value) => {
    return /^-?\d+$/.test(value);
  };
  const isEmail = (value) => {
    return /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(value);
  };

  return (
    <Dialog
      open={isShow}
      classes={{ paper: clsx('center-root', classes.dialogPaper) }}
    >
      <IconButton
        onClick={() => {
          setIsFailed(false);
          setIsSuccess(false);
          onClose();
        }}
        className={classes.buttonClose}
      >
        <Close />
      </IconButton>
      {!isSuccess && !isFailed ? (
        <div className={clsx('center-root', classes.boxWhite)}>
          <div className={classes.wrapTitle}>
            <Typography
              className={classes.title}
              dangerouslySetInnerHTML={{
                __html: getLabel(LangConstant.TXT_TITLE_POPUP_INFO),
              }}
            >
              {/* {getLabel(LangConstant.TXT_TITLE_POPUP_INFO)} */}
            </Typography>
            <Typography className={classes.note}>
              {getLabel(LangConstant.TXT_NOTE_POPUP_INFO)}
            </Typography>
          </div>

          <div className={classes.twoInput}>
            <div className={classes.boxOneInput}>
              <Input
                name="fullname"
                placeholder="VD: Nguyn Vn A"
                onChange={onChangeData}
                title={getLabel(LangConstant.TXT_FULLNAME)}
              />
            </div>
            <div className={classes.boxOneInput}>
              <Input
                name="phone"
                placeholder="VD: 0123456789"
                onChange={onChangeData}
                title={getLabel(LangConstant.TXT_PHONE)}
              />
            </div>
          </div>
          <div className={classes.twoInput}>
            <div className={classes.boxOneInput}>
              <Input
                name="email"
                placeholder="VD: example@gmail.com"
                onChange={onChangeData}
                title={getLabel(LangConstant.TXT_EMAIL)}
              />
            </div>
            <div className={classes.boxOneInput}>
              <Typography className={style.inputText}>
                {`${getLabel(LangConstant.TXT_SERVICE)}*`}
              </Typography>
              <SelectBar
                onChange={(index) => {
                  return setFormInput({ ...formInput, service: index });
                }}
                arrayItem={ARRAY_SERVICES}
              />
            </div>
          </div>
          <div className={classes.twoInput}>
            <div className={classes.boxOneInput}>
              <Input
                name="start"
                placeholder="VD: QL32, P.Minh Khai, H Ni"
                onChange={onChangeData}
                title={getLabel(LangConstant.TXT_START)}
              />
            </div>
            <div className={classes.boxOneInput}>
              <Input
                name="end"
                placeholder="VD: H Ni,  Nng, Cn Th"
                onChange={onChangeData}
                title={getLabel(LangConstant.TXT_END)}
              />
            </div>
          </div>
          <Input
            name="weight"
            placeholder="VD: 1000 tn"
            onChange={onChangeData}
            title={getLabel(LangConstant.TXT_WEIGHT)}
            boxBottom
          />
          <Input
            name="note"
            placeholder="VD: Hng d v, thi gian giao hng gp,..."
            onChange={onChangeData}
            title={getLabel(LangConstant.TXT_NOTE)}
            boxBottom
          />
          <Button
            onClick={onSubmit}
            className={classes.button}
            variant="contained"
            color="primary"
            size="small"
            disabled={isDisable}
          >
            {getLabel(LangConstant.TXT_BUTTON_SEND_INFO)}
          </Button>
          {isDisable && (
            <Typography className={classes.warning}>
              {getLabel(LangConstant.TXT_WARNING_FORM)}
            </Typography>
          )}
        </div>
      ) : (
        <div style={{ width: '75%' }}>
          {isSuccess && <Success />}
          {isFailed && <Failed />}
        </div>
      )}
    </Dialog>
  );
};

DialogSupportCustomer.protoType = {
  isShow: ProtoType.bool.isRequired,
  onClose: ProtoType.func.isRequired,
  onSubmit: ProtoType.func.isRequired,
};

DialogSupportCustomer.defaultProps = {};

export default memo(DialogSupportCustomer);

const Input = ({ title, boxBottom, ...otherProps }) => {
  return (
    <div className={clsx(style.inputBox, { [style.inputBox1]: boxBottom })}>
      <Typography className={style.inputText}>{title}</Typography>
      <OutlinedInput
        autoComplete="off"
        className={style.inputValue}
        {...otherProps}
      />
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  dialogPaper: {
    height: '80vh',
    minWidth: '80vw',
    borderRadius: 0,
    margin: 0,
    backgroundImage: 'url(/support/question-bg-1.jpg)',
  },
  wrapTitle: { width: '100%', textAlign: 'center' },
  boxWhite: {
    width: '75%',
    height: '85%',
    padding: '16px 80px',
    borderRadius: '8px',
    flexDirection: 'column',
    backgroundColor: '#fff',
    justifyContent: 'space-between',
  },
  boxOneInput: {
    width: '47%',
  },
  twoInput: { width: '100%', display: 'flex', justifyContent: 'space-between' },
  buttonClose: {
    position: 'absolute',
    top: 16,
    right: 16,
    padding: 4,
    color: theme.palette.common.white,
    backgroundColor: theme.palette.grey[400],
  },
  title: {
    fontSize: '1rem',
    color: theme.palette.primary.main,
    textAlign: 'center',
    fontWeight: '800',
    width: '80%',
    margin: '0 auto 16px',
  },
  note: {
    fontWeight: '800',
    fontStyle: 'italic',
  },
  warning: {
    fontStyle: 'italic',
    color: theme.palette.primary.main,
  },
  button: {
    height: 35,
    borderRadius: 8,
  },
  '@media screen and (min-height:701px)': {
    boxWhite: {
      padding: '50px 80px',
    },
  },
  '@media screen and (max-width: 850px)': {
    boxWhite: {
      width: '100%',
      padding: '0',
      overflow: 'auto',
    },
    twoInput: {
      display: 'block',
    },
    title: {
      width: '90%',
    },
    boxOneInput: {
      width: '90%',
      margin: '0 auto 10px',
    },
    warning: {
      textAlign: 'center',
      margin: '10px 0',
    },
  },
}));
